import React from 'react';
import { 
  Bell, 
  X, 
  CheckSquare, 
  AlertCircle, 
  Calendar, 
  Activity, 
  MessageSquare,
  Image,
  ArrowLeft
} from 'lucide-react';
import { useApp } from '../../context/AppContext';

const NotificationsView = () => {
  const { 
    appState, 
    updateAppState, 
    notifications, 
    markNotificationAsRead, 
    markNotificationsAsRead 
  } = useApp();
  
  // Get notifications for current user
  const userNotifications = notifications
    .filter(notification => 
      !notification.userId || notification.userId === appState.currentUser?.id
    )
    .sort((a, b) => new Date(b.date) - new Date(a.date)); // Sort by date (newest first)
  
  // Get unread count
  const unreadCount = userNotifications.filter(notification => !notification.read).length;
  
  // Format date
  const formatDate = (dateString) => {
    const date = new Date(dateString);
    const now = new Date();
    const diffInDays = Math.floor((now - date) / (1000 * 60 * 60 * 24));
    const diffInHours = Math.floor((now - date) / (1000 * 60 * 60));
    const diffInMinutes = Math.floor((now - date) / (1000 * 60));
    
    if (diffInDays > 7) {
      return date.toLocaleDateString('lt-LT');
    } else if (diffInDays > 0) {
      return `prieš ${diffInDays} ${diffInDays === 1 ? 'dieną' : 'dienas'}`;
    } else if (diffInHours > 0) {
      return `prieš ${diffInHours} ${diffInHours === 1 ? 'valandą' : 'valandas'}`;
    } else if (diffInMinutes > 0) {
      return `prieš ${diffInMinutes} ${diffInMinutes === 1 ? 'minutę' : 'minutes'}`;
    } else {
      return 'ką tik';
    }
  };
  
  // Get notification icon
  const getNotificationIcon = (type) => {
    switch (type) {
      case 'task':
        return <CheckSquare size={20} className="text-blue-500" />;
      case 'alert':
        return <AlertCircle size={20} className="text-red-500" />;
      case 'reminder':
        return <Calendar size={20} className="text-amber-500" />;
      case 'activity':
        return <Activity size={20} className="text-green-500" />;
      case 'message':
        return <MessageSquare size={20} className="text-purple-500" />;
      case 'photo':
        return <Image size={20} className="text-cyan-500" />;
      default:
        return <Bell size={20} className="text-gray-500" />;
    }
  };
  
  // Handle click on notification
  const handleNotificationClick = (notification) => {
    // Mark as read
    if (!notification.read) {
      markNotificationAsRead(notification.id);
    }
    
    // Navigate based on notification type and related ID
    if (notification.type === 'task' && notification.relatedId) {
      updateAppState({ 
        currentView: 'taskDetails', 
        selectedTask: notification.relatedId 
      });
    } else if (notification.type === 'hive' && notification.relatedId) {
      updateAppState({
        currentView: 'hiveDetails',
        selectedHive: notification.relatedId
      });
    }
    // Other types can be handled as needed
  };
  
  return (
    <div className="p-6">
      <div className="flex items-center justify-between mb-6">
        <div className="flex items-center">
          <button
            onClick={() => updateAppState({ currentView: 'hives' })}
            className="mr-2 text-gray-600 hover:text-gray-800"
            type="button"
          >
            <ArrowLeft size={20} />
          </button>
          <h1 className="text-2xl font-bold">Pranešimai</h1>
        </div>
        
        {unreadCount > 0 && (
          <button
            onClick={markNotificationsAsRead}
            className="text-amber-600 hover:text-amber-700 text-sm font-medium"
            type="button"
          >
            Pažymėti visus kaip perskaitytus
          </button>
        )}
      </div>
      
      <div className="bg-white rounded-lg shadow-sm overflow-hidden">
        {userNotifications.length > 0 ? (
          <div className="divide-y">
            {userNotifications.map(notification => (
              <div 
                key={notification.id} 
                className={`
                  flex items-start p-4 cursor-pointer hover:bg-gray-50
                  ${!notification.read ? 'bg-amber-50' : ''}
                `}
                onClick={() => handleNotificationClick(notification)}
              >
                <div className="flex-shrink-0 mr-3">
                  {getNotificationIcon(notification.type)}
                </div>
                
                <div className="flex-grow">
                  <div className="flex justify-between">
                    <h3 className={`font-medium ${!notification.read ? 'text-amber-800' : ''}`}>
                      {notification.title}
                    </h3>
                    <span className="text-xs text-gray-500">{formatDate(notification.date)}</span>
                  </div>
                  <p className={`text-sm mt-1 ${!notification.read ? 'text-amber-700' : 'text-gray-600'}`}>
                    {notification.message}
                  </p>
                </div>
                
                {!notification.read && (
                  <div 
                    className="flex-shrink-0 ml-2"
                    onClick={(e) => {
                      e.stopPropagation();
                      markNotificationAsRead(notification.id);
                    }}
                  >
                    <button
                      className="text-gray-400 hover:text-gray-600 p-1"
                      type="button"
                      aria-label="Pažymėti kaip perskaitytą"
                    >
                      <X size={18} />
                    </button>
                  </div>
                )}
              </div>
            ))}
          </div>
        ) : (
          <div className="py-12 text-center">
            <Bell size={48} className="mx-auto text-gray-300 mb-3" />
            <p className="text-gray-500 mb-1">Nėra jokių pranešimų</p>
            <p className="text-gray-400 text-sm">Čia atsiras pranešimai apie svarbius įvykius susijusius su jūsų aviliais.</p>
          </div>
        )}
      </div>
    </div>
  );
};

export default NotificationsView; 