import React, { useState, useEffect } from 'react';
import { Plus, Search, Calendar, CheckCircle, Clock, AlertCircle, Filter, BarChart2, X } from 'lucide-react';
import { useApp } from '../../context/AppContext';

const TasksView = () => {
  const { 
    appState, 
    updateAppState, 
    tasks, 
    hives,
    forms,
    updateForm,
    addTask,
    userTaskSteps
  } = useApp();
  
  const [showAddForm, setShowAddForm] = useState(false);
  const [filteredTasks, setFilteredTasks] = useState([]);
  const [filters, setFilters] = useState({
    status: 'all',
    priority: 'all',
    hiveId: 'all',
    search: '',
    showCompleted: true,
    showFuture: true
  });
  const [showFilters, setShowFilters] = useState(false);
  const [searchIsOpen, setSearchIsOpen] = useState(false);
  const [searchTerm, setSearchTerm] = useState('');
  
  useEffect(() => {
    console.log("Visi tasks:", tasks);
    console.log("Dabartinis vartotojas:", appState.currentUser);
    
    if (!tasks || tasks.length === 0) {
      console.log("Nėra užduočių duomenų");
    }
    
    // Išspausdinkime visas užduotis, skirtas dabartiniam vartotojui
    if (appState.currentUser) {
      const userTasks = tasks.filter(task => 
        task.assignedUserId === appState.currentUser.id || 
        task.groupId === appState.currentUser.groupId
      );
      console.log("Vartotojo užduotys:", userTasks);
    }
  }, [tasks, appState.currentUser]);
  
  // Apply filters to tasks
  useEffect(() => {
    // Pirma filtruojame užduotis, kurios priklauso dabartinio vartotojo grupei arba yra priskirtos konkrečiam vartotojui,
    // ir kurių pradžios data jau yra praėjusi (arba jei showFuture yra true)
    const today = new Date();
    const currentUser = appState.currentUser;
    
    // Jei nėra prisijungusio vartotojo arba nėra užduočių, grąžiname tuščią masyvą
    if (!currentUser || !tasks || tasks.length === 0) {
      console.log("Nėra vartotojo arba užduočių duomenų");
      setFilteredTasks([]);
      return;
    }
    
    console.log("Filtruojamos užduotys vartotojui:", currentUser.id, "grupė:", currentUser.groupId);
    
    let result = [...tasks].filter(task => {
      // Rodyti užduotis, kurios:
      // 1. Yra priskirtos vartotojo grupei ARBA
      // 2. Yra priskirtos konkrečiam vartotojui
      const isAssignedToUser = task.assignedUserId === currentUser.id || task.assignedTo === currentUser.id;
      const isAssignedToGroup = task.groupId === currentUser.groupId;
      
      // Debug info
      console.log(`Užduotis ${task.id}: grupė ${task.groupId}, priskirta vartotojui ${task.assignedUserId || task.assignedTo}, skirta šiam vartotojui: ${isAssignedToUser || isAssignedToGroup}`);
      
      if (!(isAssignedToUser || isAssignedToGroup)) {
        return false;
      }
      
      // Tikriname, ar užduoties pradžios data jau praėjo
      if (!filters.showFuture && task.startDate) {
        const startDate = new Date(task.startDate);
        if (startDate > today) {
          return false;
        }
      }
      
      return true;
    });
    
    console.log("Filtruotos pagal vartotoją/grupę užduotys:", result.length);
    
    // Filter by status
    if (filters.status !== 'all') {
      result = result.filter(task => task.status === filters.status);
    } else if (!filters.showCompleted) {
      result = result.filter(task => task.status !== 'completed');
    }
    
    // Filter by priority
    if (filters.priority !== 'all') {
      result = result.filter(task => task.priority === filters.priority);
    }
    
    // Filter by hive
    if (filters.hiveId !== 'all') {
      result = result.filter(task => task.hiveId === parseInt(filters.hiveId));
    }
    
    // Filter by search term
    if (filters.search) {
      const searchLower = filters.search.toLowerCase();
      result = result.filter(task => 
        task.title.toLowerCase().includes(searchLower) || 
        (task.description && task.description.toLowerCase().includes(searchLower))
      );
    }
    
    // Sort by due date (closest first)
    result.sort((a, b) => new Date(a.dueDate) - new Date(b.dueDate));
    
    console.log("Galutinės filtruotos užduotys:", result);
    
    setFilteredTasks(result);
  }, [tasks, filters, appState.currentUser]);
  
  // Reset add task form
  const resetAddForm = () => {
    updateForm('task', 'title', '');
    updateForm('task', 'description', '');
    updateForm('task', 'dueDate', '');
    updateForm('task', 'status', 'pending');
    updateForm('task', 'priority', 'medium');
    updateForm('task', 'hiveId', '');
  };
  
  // Handle add task form submission
  const handleAddTask = () => {
    addTask();
    setShowAddForm(false);
  };
  
  // Check if task is overdue
  const isOverdue = (dueDate) => {
    if (!dueDate) return false;
    const today = new Date();
    const due = new Date(dueDate);
    return due < today;
  };
  
  // Get status icon
  const getStatusIcon = (status, dueDate) => {
    if (status === 'completed') {
      return <CheckCircle size={18} className="text-green-500" />;
    } else if (isOverdue(dueDate)) {
      return <AlertCircle size={18} className="text-red-500" />;
    } else {
      return <Clock size={18} className="text-amber-500" />;
    }
  };
  
  // Get priority badge
  const getPriorityBadge = (priority) => {
    switch (priority) {
      case 'high':
        return <span className="px-2 py-1 text-xs font-medium rounded-full bg-red-100 text-red-800">Aukštas</span>;
      case 'medium':
        return <span className="px-2 py-1 text-xs font-medium rounded-full bg-amber-100 text-amber-800">Vidutinis</span>;
      case 'low':
        return <span className="px-2 py-1 text-xs font-medium rounded-full bg-green-100 text-green-800">Žemas</span>;
      default:
        return <span className="px-2 py-1 text-xs font-medium rounded-full bg-gray-100 text-gray-800">{priority}</span>;
    }
  };
  
  // Calculate task progress
  const getTaskProgress = (taskId) => {
    const taskSteps = userTaskSteps.filter(step => step.taskId === taskId);
    if (taskSteps.length === 0) return 0;
    
    const completedSteps = taskSteps.filter(step => step.status === 'completed').length;
    return Math.round((completedSteps / taskSteps.length) * 100);
  };
  
  // Format date
  const formatDate = (dateString) => {
    if (!dateString) return '-';
    const date = new Date(dateString);
    return date.toLocaleDateString('lt-LT');
  };
  
  // Get task status and icon
  const getTaskStatusInfo = (task) => {
    const taskOverdue = isOverdue(task.dueDate) && task.status !== 'completed';
    
    if (task.status === 'completed') {
      return {
        label: 'Užbaigta',
        color: 'bg-green-100 text-green-800',
        icon: <CheckCircle size={16} className="text-green-600 mr-1" />
      };
    } else if (taskOverdue) {
      return {
        label: 'Vėluojama',
        color: 'bg-red-100 text-red-800',
        icon: <AlertCircle size={16} className="text-red-600 mr-1" />
      };
    } else if (task.status === 'in-progress') {
      return {
        label: 'Vykdoma',
        color: 'bg-blue-100 text-blue-800',
        icon: <Clock size={16} className="text-blue-600 mr-1" />
      };
    } else {
      return {
        label: 'Suplanuota',
        color: 'bg-amber-100 text-amber-800',
        icon: <Clock size={16} className="text-amber-600 mr-1" />
      };
    }
  };
  
  // View task details
  const viewTaskDetails = (taskId) => {
    updateAppState({ 
      currentView: 'taskDetails', 
      selectedTask: taskId 
    });
  };
  
  // Get hive name
  const getHiveName = (hiveId) => {
    const hive = hives.find(h => h.id === hiveId);
    return hive ? hive.name : 'Nepriskirta';
  };
  
  // Get status filter count
  const getStatusCount = (status) => {
    if (status === 'all') {
      return tasks.filter(task => task.assignedTo === appState.currentUser?.id).length;
    }
    return tasks.filter(task => 
      task.assignedTo === appState.currentUser?.id && task.status === status
    ).length;
  };
  
  return (
    <div className="p-6 pb-20 md:pb-6">
      <div className="flex flex-col md:flex-row md:items-center justify-between mb-6">
        <h2 className="text-2xl font-bold mb-4 md:mb-0">Užduotys</h2>
        
        <div className="flex flex-col md:flex-row space-y-2 md:space-y-0 md:space-x-2">
          <div className="relative">
            <div className="absolute inset-y-0 left-0 flex items-center pl-3 pointer-events-none">
              <Search size={18} className="text-gray-500" />
            </div>
            <input 
              type="text" 
              placeholder="Ieškoti užduočių..." 
              className="pl-10 p-2 border border-gray-300 rounded-md focus:outline-none w-full md:w-auto"
              value={filters.search}
              onChange={(e) => setFilters({...filters, search: e.target.value})}
            />
          </div>
          
          <div className="flex space-x-2">
            <button 
              onClick={() => {
                setFilters({...filters, search: ''});
              }}
              className="flex items-center justify-center bg-gray-200 text-gray-600 px-3 py-2 rounded-md hover:bg-gray-300 focus:outline-none"
            >
              {filters.search ? <X size={18} /> : <Search size={18} />}
            </button>
            
            <button 
              onClick={() => setShowFilters(!showFilters)}
              className="flex items-center justify-center bg-gray-200 text-gray-700 px-4 py-2 rounded-md hover:bg-gray-300 focus:outline-none"
            >
              <Filter size={18} className="mr-1" />
              Filtrai
            </button>
            
            {appState.currentUser?.role === 'admin' && (
              <button 
                onClick={() => {
                  resetAddForm();
                  setShowAddForm(true);
                }}
                className="flex items-center justify-center bg-amber-600 text-white px-4 py-2 rounded-md hover:bg-amber-700 focus:outline-none"
              >
                <Plus size={18} className="mr-1" />
                Nauja užduotis
              </button>
            )}
          </div>
        </div>
      </div>
      
      {/* Filters panel */}
      {showFilters && (
        <div className="bg-gray-50 p-4 rounded-md mb-6">
          <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
            <div>
              <label className="block mb-1 text-sm font-medium text-gray-700">
                Statusas
              </label>
              <select 
                value={filters.status}
                onChange={(e) => setFilters({...filters, status: e.target.value})}
                className="w-full p-2 border border-gray-300 rounded-md focus:outline-none"
              >
                <option value="all">Visi statusai</option>
                <option value="pending">Suplanuota</option>
                <option value="in-progress">Vykdoma</option>
                <option value="completed">Baigta</option>
                <option value="delayed">Vėluojama</option>
              </select>
            </div>
            
            <div>
              <label className="block mb-1 text-sm font-medium text-gray-700">
                Prioritetas
              </label>
              <select 
                value={filters.priority}
                onChange={(e) => setFilters({...filters, priority: e.target.value})}
                className="w-full p-2 border border-gray-300 rounded-md focus:outline-none"
              >
                <option value="all">Visi prioritetai</option>
                <option value="high">Aukštas</option>
                <option value="medium">Vidutinis</option>
                <option value="low">Žemas</option>
              </select>
            </div>
            
            <div>
              <label className="block mb-1 text-sm font-medium text-gray-700">
                Avilys
              </label>
              <select 
                value={filters.hiveId}
                onChange={(e) => setFilters({...filters, hiveId: e.target.value})}
                className="w-full p-2 border border-gray-300 rounded-md focus:outline-none"
              >
                <option value="all">Visi aviliai</option>
                <option value="">Nepriskirta</option>
                {hives.map(hive => (
                  <option key={hive.id} value={hive.id}>{hive.name}</option>
                ))}
              </select>
            </div>
            
            <div className="flex items-end">
              <label className="flex items-center">
                <input 
                  type="checkbox" 
                  checked={filters.showCompleted}
                  onChange={(e) => setFilters({...filters, showCompleted: e.target.checked})}
                  className="w-4 h-4 text-amber-600 rounded focus:ring-amber-500"
                />
                <span className="ml-2 text-sm text-gray-700">Rodyti baigtas užduotis</span>
              </label>
            </div>
            
            <div className="flex items-end">
              <label className="flex items-center">
                <input 
                  type="checkbox" 
                  checked={filters.showFuture}
                  onChange={(e) => setFilters({...filters, showFuture: e.target.checked})}
                  className="w-4 h-4 text-amber-600 rounded focus:ring-amber-500"
                />
                <span className="ml-2 text-sm text-gray-700">Rodyti užduotis, kurios prasideda ateityje</span>
              </label>
            </div>
          </div>
          
          <div className="flex justify-end mt-4">
            <button 
              onClick={() => setFilters({
                status: 'all',
                priority: 'all',
                hiveId: 'all',
                search: '',
                showCompleted: true,
                showFuture: true
              })}
              className="text-amber-600 hover:text-amber-800 focus:outline-none"
            >
              Išvalyti filtrus
            </button>
          </div>
        </div>
      )}
      
      {/* Add task form */}
      {showAddForm && (
        <div className="bg-white p-6 rounded-lg shadow-md mb-6">
          <h3 className="text-lg font-semibold mb-4">Pridėti naują užduotį</h3>
          
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-4">
            <div>
              <label className="block mb-1 text-sm font-medium text-gray-700">
                Pavadinimas <span className="text-red-500">*</span>
              </label>
              <input 
                type="text" 
                value={forms.task.title}
                onChange={(e) => updateForm('task', 'title', e.target.value)}
                className="w-full p-2 border border-gray-300 rounded-md focus:outline-none"
                placeholder="Užduoties pavadinimas"
                required
              />
            </div>
            
            <div>
              <label className="block mb-1 text-sm font-medium text-gray-700">
                Terminas <span className="text-red-500">*</span>
              </label>
              <input 
                type="date" 
                value={forms.task.dueDate}
                onChange={(e) => updateForm('task', 'dueDate', e.target.value)}
                className="w-full p-2 border border-gray-300 rounded-md focus:outline-none"
                required
              />
            </div>
            
            <div>
              <label className="block mb-1 text-sm font-medium text-gray-700">
                Statusas <span className="text-red-500">*</span>
              </label>
              <select 
                value={forms.task.status}
                onChange={(e) => updateForm('task', 'status', e.target.value)}
                className="w-full p-2 border border-gray-300 rounded-md focus:outline-none"
                required
              >
                <option value="pending">Suplanuota</option>
                <option value="in-progress">Vykdoma</option>
                <option value="completed">Baigta</option>
                <option value="delayed">Vėluojama</option>
              </select>
            </div>
            
            <div>
              <label className="block mb-1 text-sm font-medium text-gray-700">
                Prioritetas <span className="text-red-500">*</span>
              </label>
              <select 
                value={forms.task.priority}
                onChange={(e) => updateForm('task', 'priority', e.target.value)}
                className="w-full p-2 border border-gray-300 rounded-md focus:outline-none"
                required
              >
                <option value="high">Aukštas</option>
                <option value="medium">Vidutinis</option>
                <option value="low">Žemas</option>
              </select>
            </div>
            
            <div>
              <label className="block mb-1 text-sm font-medium text-gray-700">
                Avilys
              </label>
              <select 
                value={forms.task.hiveId}
                onChange={(e) => updateForm('task', 'hiveId', e.target.value)}
                className="w-full p-2 border border-gray-300 rounded-md focus:outline-none"
              >
                <option value="">-- Nepriskirta --</option>
                {hives.map(hive => (
                  <option key={hive.id} value={hive.id}>{hive.name}</option>
                ))}
              </select>
            </div>
          </div>
          
          <div className="mb-4">
            <label className="block mb-1 text-sm font-medium text-gray-700">
              Aprašymas
            </label>
            <textarea 
              rows="3"
              value={forms.task.description}
              onChange={(e) => updateForm('task', 'description', e.target.value)}
              className="w-full p-2 border border-gray-300 rounded-md focus:outline-none"
              placeholder="Išsamus užduoties aprašymas..."
            ></textarea>
          </div>
          
          <div className="flex justify-end space-x-2">
            <button 
              onClick={() => setShowAddForm(false)}
              className="px-4 py-2 border border-gray-300 rounded-md hover:bg-gray-100 focus:outline-none"
              type="button"
            >
              Atšaukti
            </button>
            <button 
              onClick={handleAddTask}
              className="px-4 py-2 bg-amber-600 text-white rounded-md hover:bg-amber-700 focus:outline-none"
              type="button"
              disabled={!forms.task.title || !forms.task.dueDate}
            >
              Pridėti užduotį
            </button>
          </div>
        </div>
      )}
      
      {/* Tasks list */}
      {filteredTasks.length > 0 ? (
        <div className="space-y-4">
          {filteredTasks.map(task => {
            const statusInfo = getTaskStatusInfo(task);
            const progress = getTaskProgress(task.id);
            const isOverdue = new Date(task.dueDate) < new Date() && task.status !== 'completed';
            
            return (
              <div 
                key={task.id} 
                className={`p-4 rounded-lg border ${statusInfo.color} cursor-pointer hover:shadow-md transition-shadow`}
                onClick={() => viewTaskDetails(task.id)}
              >
                <div className="flex items-start justify-between mb-3">
                  <div className="flex items-start">
                    {statusInfo.icon}
                    <div className="ml-2">
                      <div className="font-medium">{task.title}</div>
                      <div className="text-sm text-gray-600 line-clamp-1">{task.description}</div>
                    </div>
                  </div>
                  <div className={`text-sm font-medium ${statusInfo.color}`}>
                    {statusInfo.label}
                  </div>
                </div>
                
                <div className="flex flex-col sm:flex-row sm:justify-between sm:items-center text-sm mb-3 space-y-2 sm:space-y-0">
                  <div className="flex flex-col sm:flex-row sm:space-x-4 space-y-1 sm:space-y-0">
                    <div>
                      <span className="text-gray-500">Terminas:</span> {formatDate(task.dueDate)}
                    </div>
                    <div>
                      <span className="text-gray-500">Avilys:</span> {getHiveName(task.hiveId)}
                    </div>
                  </div>
                  <div className="font-medium">
                    {progress}% užbaigta
                  </div>
                </div>
                
                <div className="w-full bg-gray-200 rounded-full h-2.5">
                  <div 
                    className="bg-amber-600 h-2.5 rounded-full" 
                    style={{ width: `${progress}%` }}
                  ></div>
                </div>
              </div>
            );
          })}
        </div>
      ) : (
        <div className="text-center py-16">
          <BarChart2 size={48} className="mx-auto text-gray-300 mb-4" />
          <h2 className="text-xl font-semibold text-gray-600 mb-2">Nėra užduočių</h2>
          <p className="text-gray-500 mb-4">
            {filters.search || filters.status !== 'all'
              ? 'Nerasta užduočių pagal paieškos kriterijus'
              : 'Jums dar nepriskirta jokių užduočių'}
          </p>
        </div>
      )}
    </div>
  );
};

export default TasksView; 