import React, { useState } from 'react';
import { Plus, Edit, Trash, Search, X, Save, User, Eye, Hexagon, CheckSquare, Image, ArrowLeft } from 'lucide-react';
import { useApp } from '../../context/AppContext';

const AdminUsers = () => {
  const { 
    appState, 
    updateAppState, 
    users, 
    userGroups,
    hives,
    tasks,
    userTaskSteps,
    hivePhotos,
    forms,
    updateForm,
    addUser,
    updateUser,
    deleteUser
  } = useApp();
  
  const [showForm, setShowForm] = useState(false);
  const [editingUserId, setEditingUserId] = useState(null);
  const [searchTerm, setSearchTerm] = useState('');
  const [confirmDelete, setConfirmDelete] = useState(null);
  const [selectedUser, setSelectedUser] = useState(null);
  const [userDetailsView, setUserDetailsView] = useState(null); // 'hives', 'tasks', 'gallery', null
  
  // Form handlers
  const resetForm = () => {
    updateForm('userAdd', 'name', '');
    updateForm('userAdd', 'email', '');
    updateForm('userAdd', 'password', '');
    updateForm('userAdd', 'phone', '');
    updateForm('userAdd', 'address', '');
    updateForm('userAdd', 'role', 'user');
    updateForm('userAdd', 'groupId', '');
  };
  
  const handleSubmit = (e) => {
    e.preventDefault();
    
    if (editingUserId) {
      // Redagavimas
      const updatedUser = {
        id: editingUserId,
        name: forms.userAdd.name,
        email: forms.userAdd.email,
        password: forms.userAdd.password || users.find(u => u.id === editingUserId).password,
        phone: forms.userAdd.phone,
        address: forms.userAdd.address,
        role: forms.userAdd.role,
        groupId: forms.userAdd.groupId
      };
      
      updateUser(updatedUser);
      setEditingUserId(null);
    } else {
      // Naujo vartotojo pridėjimas
      addUser();
    }
    
    setShowForm(false);
  };
  
  const handleEdit = (user) => {
    setEditingUserId(user.id);
    updateForm('userAdd', 'name', user.name);
    updateForm('userAdd', 'email', user.email);
    updateForm('userAdd', 'password', ''); // Saugumo sumetimais neužpildome slaptažodžio
    updateForm('userAdd', 'phone', user.phone || '');
    updateForm('userAdd', 'address', user.address || '');
    updateForm('userAdd', 'role', user.role);
    updateForm('userAdd', 'groupId', user.groupId || '');
    setShowForm(true);
  };
  
  const handleDelete = (userId) => {
    if (confirmDelete === userId) {
      // Vartotojo šalinimas
      deleteUser(userId);
      setConfirmDelete(null);
    } else {
      setConfirmDelete(userId);
    }
  };
  
  const handleViewUser = (user) => {
    setSelectedUser(user);
    setUserDetailsView('overview');
  };
  
  const handleBackFromUserDetails = () => {
    setSelectedUser(null);
    setUserDetailsView(null);
  };
  
  // Filtruojame pagal paiešką
  const filteredUsers = users.filter(user => 
    user.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
    user.email.toLowerCase().includes(searchTerm.toLowerCase()) ||
    (user.phone && user.phone.includes(searchTerm))
  );
  
  // Gauname grupės pavadinimą pagal ID
  const getGroupName = (groupId) => {
    if (!groupId) return 'Nepriskirta';
    const group = userGroups.find(g => g.id === groupId);
    return group ? group.name : 'Nepriskirta';
  };
  
  // Get user's hives
  const getUserHives = (userId) => {
    return hives.filter(hive => hive.userId === userId);
  };
  
  // Get user's tasks
  const getUserTasks = (userId) => {
    return tasks.filter(task => task.assignedTo === userId);
  };
  
  // Get task progress
  const getTaskProgress = (taskId) => {
    const steps = userTaskSteps.filter(step => step.taskId === taskId);
    if (!steps.length) return 0;
    
    const completedSteps = steps.filter(step => step.status === 'completed').length;
    return Math.round((completedSteps / steps.length) * 100);
  };
  
  // Format date
  const formatDate = (dateString) => {
    if (!dateString) return '';
    return new Date(dateString).toLocaleDateString('lt-LT');
  };
  
  // If user details view is active
  if (selectedUser) {
    const userHives = getUserHives(selectedUser.id);
    const userTasks = getUserTasks(selectedUser.id);
    const totalTasks = userTasks.length;
    const completedTasks = userTasks.filter(task => task.status === 'completed').length;
    const pendingTasks = userTasks.filter(task => task.status !== 'completed').length;
    const overallProgress = totalTasks > 0 ? Math.round((completedTasks / totalTasks) * 100) : 0;
    
    return (
      <div className="p-6">
        <div className="flex items-center mb-6">
          <button
            onClick={handleBackFromUserDetails}
            className="mr-2 text-gray-600 hover:text-gray-800"
            type="button"
          >
            <ArrowLeft size={20} />
          </button>
          <h2 className="text-2xl font-bold">Vartotojo detalės</h2>
        </div>
        
        <div className="bg-white rounded-lg shadow-md p-6 mb-6">
          <div className="flex flex-col md:flex-row md:items-center md:justify-between">
            <div className="flex items-center mb-4 md:mb-0">
              <div className="h-16 w-16 bg-amber-100 rounded-full flex items-center justify-center mr-4">
                <User size={24} className="text-amber-600" />
              </div>
              <div>
                <h3 className="text-xl font-bold">{selectedUser.name}</h3>
                <p className="text-gray-600">{selectedUser.email}</p>
                <p className="text-gray-500">{selectedUser.phone}</p>
              </div>
            </div>
            
            <div className="flex flex-col md:items-end">
              <div className="mb-2">
                <span className={`px-3 py-1 rounded-full text-sm font-medium ${
                  selectedUser.role === 'admin' ? 'bg-purple-100 text-purple-800' : 'bg-green-100 text-green-800'
                }`}>
                  {selectedUser.role === 'admin' ? 'Administratorius' : 'Vartotojas'}
                </span>
              </div>
              <p className="text-sm text-gray-600">Grupė: {getGroupName(selectedUser.groupId)}</p>
              <p className="text-sm text-gray-600">Sukurtas: {formatDate(selectedUser.dateCreated)}</p>
            </div>
          </div>
        </div>
        
        <div className="mb-6">
          <div className="tabs flex space-x-1 border-b">
            <button 
              onClick={() => setUserDetailsView('overview')}
              className={`px-4 py-2 font-medium ${userDetailsView === 'overview' ? 'text-amber-600 border-b-2 border-amber-600' : 'text-gray-500 hover:text-gray-700'}`}
              type="button"
            >
              Apžvalga
            </button>
            <button 
              onClick={() => setUserDetailsView('hives')}
              className={`px-4 py-2 font-medium ${userDetailsView === 'hives' ? 'text-amber-600 border-b-2 border-amber-600' : 'text-gray-500 hover:text-gray-700'}`}
              type="button"
            >
              Aviliai ({userHives.length})
            </button>
            <button 
              onClick={() => setUserDetailsView('tasks')}
              className={`px-4 py-2 font-medium ${userDetailsView === 'tasks' ? 'text-amber-600 border-b-2 border-amber-600' : 'text-gray-500 hover:text-gray-700'}`}
              type="button"
            >
              Užduotys ({userTasks.length})
            </button>
          </div>
        </div>
        
        {userDetailsView === 'overview' && (
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            <div className="bg-white rounded-lg shadow-md p-6">
              <h3 className="text-lg font-semibold mb-4">Progreso santrauka</h3>
              
              <div className="grid grid-cols-3 gap-4 mb-6">
                <div className="bg-amber-50 p-3 rounded-lg text-center">
                  <p className="text-sm text-amber-800">Visos užduotys</p>
                  <p className="text-xl font-bold text-amber-600">{totalTasks}</p>
                </div>
                <div className="bg-green-50 p-3 rounded-lg text-center">
                  <p className="text-sm text-green-800">Baigtos</p>
                  <p className="text-xl font-bold text-green-600">{completedTasks}</p>
                </div>
                <div className="bg-blue-50 p-3 rounded-lg text-center">
                  <p className="text-sm text-blue-800">Vykdomos</p>
                  <p className="text-xl font-bold text-blue-600">{pendingTasks}</p>
                </div>
              </div>
              
              <div>
                <div className="flex justify-between mb-1">
                  <span className="text-sm font-medium">Bendras progresas</span>
                  <span className="text-sm font-medium">{overallProgress}%</span>
                </div>
                <div className="w-full bg-gray-200 rounded-full h-2.5">
                  <div className="bg-amber-600 h-2.5 rounded-full" style={{ width: `${overallProgress}%` }}></div>
                </div>
              </div>
            </div>
            
            <div className="bg-white rounded-lg shadow-md p-6">
              <h3 className="text-lg font-semibold mb-4">Vartotojo informacija</h3>
              
              <div className="space-y-3">
                <div>
                  <p className="text-sm text-gray-500">Adresas</p>
                  <p>{selectedUser.address || 'Nenurodytas'}</p>
                </div>
                <div>
                  <p className="text-sm text-gray-500">Priskirti aviliai</p>
                  <p>{userHives.length}</p>
                </div>
                <div>
                  <p className="text-sm text-gray-500">Priskirtos užduotys</p>
                  <p>{userTasks.length}</p>
                </div>
              </div>
            </div>
          </div>
        )}
        
        {userDetailsView === 'hives' && (
          <div className="bg-white rounded-lg shadow-md overflow-hidden">
            {userHives.length > 0 ? (
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4 p-6">
                {userHives.map(hive => {
                  const hiveTasksCount = tasks.filter(task => task.hiveId === hive.id).length;
                  const completedHiveTasksCount = tasks.filter(task => task.hiveId === hive.id && task.status === 'completed').length;
                  const hiveProgress = hiveTasksCount > 0 ? Math.round((completedHiveTasksCount / hiveTasksCount) * 100) : 0;
                  
                  return (
                    <div key={hive.id} className="border rounded-lg overflow-hidden shadow-sm">
                      <div className="h-40 bg-gray-200 relative">
                        {hive.image ? (
                          <img src={hive.image} alt={hive.name} className="w-full h-full object-cover" />
                        ) : (
                          <div className="h-full flex items-center justify-center">
                            <Hexagon size={64} className="text-gray-400" />
                          </div>
                        )}
                      </div>
                      <div className="p-4">
                        <h4 className="font-bold text-lg mb-1">{hive.name}</h4>
                        <p className="text-gray-600 text-sm mb-2">{hive.location}</p>
                        
                        <div className="flex justify-between text-sm mb-1">
                          <span>Užduočių progresas</span>
                          <span>{hiveProgress}%</span>
                        </div>
                        <div className="w-full bg-gray-200 rounded-full h-2 mb-2">
                          <div 
                            className="bg-amber-600 h-2 rounded-full" 
                            style={{ width: `${hiveProgress}%` }}
                          ></div>
                        </div>
                        
                        <div className="flex justify-between text-sm text-gray-600">
                          <span>Sukurtas: {formatDate(hive.dateCreated)}</span>
                        </div>
                      </div>
                    </div>
                  );
                })}
              </div>
            ) : (
              <div className="text-center py-12">
                <Hexagon size={48} className="mx-auto text-gray-300 mb-3" />
                <p className="text-gray-500">Vartotojas neturi priskirtų avilių.</p>
              </div>
            )}
          </div>
        )}
        
        {userDetailsView === 'tasks' && (
          <div className="bg-white rounded-lg shadow-md overflow-hidden">
            {userTasks.length > 0 ? (
              <div className="divide-y">
                {userTasks.map(task => {
                  const progress = getTaskProgress(task.id);
                  const isOverdue = new Date(task.dueDate) < new Date() && task.status !== 'completed';
                  
                  let statusClass = 'bg-amber-100 text-amber-800';
                  if (task.status === 'completed') {
                    statusClass = 'bg-green-100 text-green-800';
                  } else if (isOverdue) {
                    statusClass = 'bg-red-100 text-red-800';
                  }
                  
                  return (
                    <div key={task.id} className="p-4 hover:bg-gray-50">
                      <div className="flex justify-between">
                        <div>
                          <h4 className="font-medium">{task.title}</h4>
                          <p className="text-sm text-gray-600 mb-2">{task.description}</p>
                          
                          <div className="flex space-x-3 text-sm">
                            <span className={`px-2 py-0.5 rounded ${statusClass}`}>
                              {task.status === 'completed' ? 'Atlikta' : isOverdue ? 'Vėluojama' : 'Vykdoma'}
                            </span>
                            <span className="text-gray-600">
                              Terminas: {formatDate(task.dueDate)}
                            </span>
                          </div>
                        </div>
                        
                        <div className="text-right">
                          <div className="text-sm font-medium mb-1">{progress}% atlikta</div>
                          <div className="w-32 bg-gray-200 rounded-full h-2">
                            <div 
                              className={`h-2 rounded-full ${task.status === 'completed' ? 'bg-green-500' : 'bg-amber-600'}`}
                              style={{ width: `${progress}%` }}
                            ></div>
                          </div>
                        </div>
                      </div>
                    </div>
                  );
                })}
              </div>
            ) : (
              <div className="text-center py-12">
                <CheckSquare size={48} className="mx-auto text-gray-300 mb-3" />
                <p className="text-gray-500">Vartotojui nėra priskirtų užduočių.</p>
              </div>
            )}
          </div>
        )}
        
      </div>
    );
  }
  
  return (
    <div className="p-6">
      <div className="flex justify-between items-center mb-6">
        <h2 className="text-2xl font-bold">Vartotojų administravimas</h2>
        <button 
          onClick={() => {
            resetForm();
            setEditingUserId(null);
            setShowForm(true);
          }}
          className="bg-amber-600 text-white px-4 py-2 rounded-md hover:bg-amber-700 flex items-center"
        >
          <Plus size={16} className="mr-2" />
          Pridėti vartotoją
        </button>
      </div>
      
      {/* Paieškos laukas */}
      <div className="mb-6 relative">
        <input 
          type="text"
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
          placeholder="Ieškoti pagal vardą, el. paštą arba telefono numerį..."
          className="w-full p-3 pl-10 pr-10 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-amber-500"
        />
        <Search size={20} className="absolute left-3 top-3.5 text-gray-400" />
        {searchTerm && (
          <button 
            onClick={() => setSearchTerm('')}
            className="absolute right-3 top-3.5 text-gray-400 hover:text-gray-600"
          >
            <X size={20} />
          </button>
        )}
      </div>
      
      {/* Vartotojų forma */}
      {showForm && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
          <div className="bg-white rounded-lg shadow-xl w-full max-w-2xl max-h-[90vh] overflow-y-auto">
            <div className="p-6">
              <div className="flex justify-between items-center mb-6">
                <h3 className="text-xl font-bold">{editingUserId ? 'Redaguoti vartotoją' : 'Pridėti naują vartotoją'}</h3>
                <button
                  onClick={() => setShowForm(false)}
                  className="text-gray-500 hover:text-gray-700"
                  type="button"
                >
                  <X size={20} />
                </button>
              </div>
              
              <form onSubmit={handleSubmit}>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-6">
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">Vardas ir pavardė *</label>
                    <input 
                      type="text"
                      value={forms.userAdd?.name || ''}
                      onChange={(e) => updateForm('userAdd', 'name', e.target.value)}
                      className="w-full p-2 border border-gray-300 rounded-md"
                      required
                    />
                  </div>
                  
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">El. paštas *</label>
                    <input 
                      type="email"
                      value={forms.userAdd?.email || ''}
                      onChange={(e) => updateForm('userAdd', 'email', e.target.value)}
                      className="w-full p-2 border border-gray-300 rounded-md"
                      required
                    />
                  </div>
                  
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">
                      {editingUserId ? 'Slaptažodis (palikite tuščią, jei nekeičiate)' : 'Slaptažodis *'}
                    </label>
                    <input 
                      type="password"
                      value={forms.userAdd?.password || ''}
                      onChange={(e) => updateForm('userAdd', 'password', e.target.value)}
                      className="w-full p-2 border border-gray-300 rounded-md"
                      required={!editingUserId}
                    />
                  </div>
                  
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">Telefono numeris</label>
                    <input 
                      type="tel"
                      value={forms.userAdd?.phone || ''}
                      onChange={(e) => updateForm('userAdd', 'phone', e.target.value)}
                      className="w-full p-2 border border-gray-300 rounded-md"
                    />
                  </div>
                  
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">Adresas</label>
                    <input 
                      type="text"
                      value={forms.userAdd?.address || ''}
                      onChange={(e) => updateForm('userAdd', 'address', e.target.value)}
                      className="w-full p-2 border border-gray-300 rounded-md"
                    />
                  </div>
                  
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">Rolė *</label>
                    <select 
                      value={forms.userAdd?.role || 'user'}
                      onChange={(e) => updateForm('userAdd', 'role', e.target.value)}
                      className="w-full p-2 border border-gray-300 rounded-md"
                      required
                    >
                      <option value="user">Vartotojas</option>
                      <option value="admin">Administratorius</option>
                    </select>
                  </div>
                  
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">Grupė</label>
                    <select 
                      value={forms.userAdd?.groupId || ''}
                      onChange={(e) => updateForm('userAdd', 'groupId', e.target.value)}
                      className="w-full p-2 border border-gray-300 rounded-md"
                    >
                      <option value="">-- Nepriskirta --</option>
                      {userGroups.map(group => (
                        <option key={group.id} value={group.id}>{group.name}</option>
                      ))}
                    </select>
                  </div>
                </div>
                
                <div className="flex justify-end space-x-2">
                  <button
                    type="button"
                    onClick={() => setShowForm(false)}
                    className="px-4 py-2 border border-gray-300 rounded-md hover:bg-gray-50"
                  >
                    Atšaukti
                  </button>
                  <button
                    type="submit"
                    className="px-4 py-2 bg-amber-600 text-white rounded-md hover:bg-amber-700"
                  >
                    {editingUserId ? 'Atnaujinti' : 'Pridėti'}
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
      )}
      
      <div className="bg-white rounded-lg shadow-md overflow-hidden">
        <table className="min-w-full divide-y divide-gray-200">
          <thead className="bg-gray-50">
            <tr>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Vartotojas</th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Kontaktai</th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Rolė</th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Grupė</th>
              <th className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider">Veiksmai</th>
            </tr>
          </thead>
          <tbody className="bg-white divide-y divide-gray-200">
            {filteredUsers.length > 0 ? (
              filteredUsers.map(user => (
                <tr key={user.id} className="hover:bg-gray-50">
                  <td className="px-6 py-4 whitespace-nowrap">
                    <div className="flex items-center">
                      <div className="flex-shrink-0 h-10 w-10 bg-gray-200 rounded-full flex items-center justify-center">
                        <User size={20} className="text-gray-500" />
                      </div>
                      <div className="ml-4">
                        <div className="text-sm font-medium text-gray-900">{user.name}</div>
                        <div className="text-sm text-gray-500">ID: {user.id}</div>
                      </div>
                    </div>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    <div className="text-sm text-gray-900">{user.email}</div>
                    <div className="text-sm text-gray-500">{user.phone}</div>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    <span className={`px-2 inline-flex text-xs leading-5 font-semibold rounded-full ${
                      user.role === 'admin' ? 'bg-purple-100 text-purple-800' : 'bg-green-100 text-green-800'
                    }`}>
                      {user.role === 'admin' ? 'Administratorius' : 'Vartotojas'}
                    </span>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                    {getGroupName(user.groupId)}
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap text-right text-sm font-medium">
                    <button 
                      onClick={() => handleViewUser(user)}
                      className="text-blue-600 hover:text-blue-900 mx-2"
                      title="Peržiūrėti detalią informaciją"
                    >
                      <Eye size={18} />
                    </button>
                    <button 
                      onClick={() => handleEdit(user)}
                      className="text-amber-600 hover:text-amber-900 mx-2"
                      title="Redaguoti vartotoją"
                    >
                      <Edit size={18} />
                    </button>
                    <button 
                      onClick={() => handleDelete(user.id)}
                      className={`text-red-600 hover:text-red-900 mx-2 ${
                        confirmDelete === user.id ? 'animate-pulse' : ''
                      }`}
                      title={confirmDelete === user.id ? 'Patvirtinti šalinimą' : 'Šalinti vartotoją'}
                    >
                      <Trash size={18} />
                    </button>
                  </td>
                </tr>
              ))
            ) : (
              <tr>
                <td colSpan={5} className="px-6 py-4 text-center text-gray-500">
                  {searchTerm ? 'Nerasta vartotojų pagal paiešką' : 'Nėra vartotojų'}
                </td>
              </tr>
            )}
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default AdminUsers; 