import React from 'react';
import { AppProvider, useApp } from './context/AppContext';
import Login from './components/auth/Login';
import Sidebar from './components/layout/Sidebar';
import Header from './components/layout/Header';
import Footer from './components/layout/Footer';
import HivesView from './components/hives/HivesView';
import HiveDetails from './components/hives/HiveDetails';
import TasksView from './components/tasks/TasksView';
import TaskDetails from './components/tasks/TaskDetails';
import NotificationsView from './components/notifications/NotificationsView';
import MessagesView from './components/messages/MessagesView';
import ProfileView from './components/profile/ProfileView';
import DashboardView from './components/dashboard/DashboardView';

// Admin components
import AdminDashboard from './components/admin/AdminDashboard';
import AdminUsers from './components/admin/AdminUsers';
import AdminGroups from './components/admin/AdminGroups';
import AdminHives from './components/admin/AdminHives';
import AdminSteps from './components/admin/AdminSteps';
import AdminTasks from './components/admin/AdminTasks';
import AdminTemplates from './components/admin/AdminTemplates';

function App() {
  const { appState, updateAppState } = useApp();
  
  console.log('App.js: Current app state:', appState);
  
  // If currentView is login or there's no current user, show login
  if (appState.currentView === 'login' || !appState.currentUser) {
    console.log('App.js: Showing login screen');
    return <Login />;
  }
  
  // Get current view and details
  const currentView = appState.currentView;
  const viewParams = appState.viewParams || {};
  
  console.log('App.js: Rendering view:', currentView);
  
  // Render appropriate view component
  const renderView = () => {
    switch (currentView) {
      case 'dashboard':
        return <DashboardView />;
      case 'hives':
        return <HivesView />;
      case 'hiveDetails':
        return <HiveDetails hiveId={appState.selectedHive} />;
      case 'tasks':
        return <TasksView />;
      case 'taskDetails':
        return <TaskDetails taskId={appState.selectedTask} />;
      case 'notifications':
        return <NotificationsView />;
      case 'messages':
        return <MessagesView />;
      case 'profile':
        return <ProfileView />;
      // Admin views
      case 'admin-dashboard':
        return <AdminDashboard />;
      case 'admin-users':
        return <AdminUsers />;
      case 'admin-groups':
        return <AdminGroups />;
      case 'admin-hives':
        return <AdminHives />;
      case 'admin-steps':
        return <AdminSteps />;
      case 'admin-tasks':
        return <AdminTasks />;
      case 'admin-templates':
        return <AdminTemplates />;
      default:
        return <DashboardView />;
    }
  };
  
  return (
    <div className="flex h-screen bg-gray-100">
      <Sidebar />
      
      <div className="flex flex-col flex-1 overflow-hidden">
        <Header />
        
        <main className="flex-1 overflow-y-auto p-4">
          <div className="container mx-auto">
            {renderView()}
          </div>
        </main>
        
        <Footer />
      </div>
    </div>
  );
}

// Wrap the App component with the AppProvider
function AppWithProvider() {
  return (
    <AppProvider>
      <App />
    </AppProvider>
  );
}

export default AppWithProvider; 