import React from 'react';
import { Bell, User } from 'lucide-react';
import { useApp } from '../../context/AppContext';

const Header = () => {
  const { appState, updateAppState } = useApp();
  
  // Get current user
  const user = appState.currentUser;
  
  // Get unread notifications count
  const unreadNotifications = appState.unreadNotificationsCount || 0;
  
  // Navigate to notifications
  const goToNotifications = () => {
    updateAppState({ currentView: 'notifications' });
  };
  
  // Navigate to profile
  const goToProfile = () => {
    updateAppState({ currentView: 'profile' });
  };
  
  return (
    <header className="bg-white shadow-sm">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex justify-between h-16">
          <div className="flex items-center">
            <h1 className="text-xl font-bold text-amber-600">Bitininkų sistema</h1>
          </div>
          
          <div className="flex items-center space-x-4">
            {/* Notifications button */}
            <button
              onClick={goToNotifications}
              className="relative p-2 rounded-full text-gray-600 hover:text-amber-600 focus:outline-none"
              type="button"
              aria-label="Pranešimai"
            >
              <Bell size={20} />
              {unreadNotifications > 0 && (
                <span className="absolute top-0 right-0 block h-4 w-4 rounded-full bg-red-500 text-white text-xs font-bold flex items-center justify-center">
                  {unreadNotifications}
                </span>
              )}
            </button>
            
            {/* User profile button */}
            <button
              onClick={goToProfile}
              className="flex items-center space-x-2 text-gray-600 hover:text-amber-600 focus:outline-none"
              type="button"
              aria-label="Profilis"
            >
              <div className="bg-amber-100 p-2 rounded-full">
                <User size={20} className="text-amber-600" />
              </div>
              <span className="hidden md:block font-medium">{user?.name || 'Vartotojas'}</span>
            </button>
          </div>
        </div>
      </div>
    </header>
  );
};

export default Header; 