import React from 'react';
import { Grid, CheckSquare, AlertCircle, Clock, CheckCircle, ArrowRight, Camera, Hexagon } from 'lucide-react';
import { useApp } from '../../context/AppContext';

const DashboardView = () => {
  const { 
    appState, 
    updateAppState,
    hives = [], 
    tasks = [], 
    userTaskSteps = [],
    changeHiveImage 
  } = useApp();
  
  if (!appState.currentUser) {
    return <div className="p-6">Neprisijungęs vartotojas</div>;
  }
  
  // Filter hives for the current user
  const userHives = hives.filter(hive => 
    hive.userId === appState.currentUser.id || appState.currentUser.role === 'admin'
  );
  
  // Filter tasks for the current user
  const userTasks = tasks.filter(task => 
    task.assignedTo === appState.currentUser.id || appState.currentUser.role === 'admin'
  );
  
  // Get upcoming tasks (not completed, sorted by due date)
  const upcomingTasks = [...userTasks]
    .filter(task => task.status !== 'completed')
    .sort((a, b) => new Date(a.dueDate) - new Date(b.dueDate))
    .slice(0, 5);
  
  // Count tasks by status
  const taskCounts = {
    total: userTasks.length,
    completed: userTasks.filter(task => task.status === 'completed').length,
    inProgress: userTasks.filter(task => task.status === 'in_progress').length,
    pending: userTasks.filter(task => task.status === 'pending').length,
    overdue: userTasks.filter(task => {
      const dueDate = new Date(task.dueDate);
      const today = new Date();
      return task.status !== 'completed' && dueDate < today;
    }).length
  };
  
  // Check if task is overdue
  const isOverdue = (dueDate) => {
    const today = new Date();
    const due = new Date(dueDate);
    return due < today;
  };
  
  // Get status icon
  const getStatusIcon = (status, dueDate) => {
    if (status === 'completed') {
      return <CheckCircle size={16} className="text-green-600" />;
    } else if (isOverdue(dueDate)) {
      return <AlertCircle size={16} className="text-red-600" />;
    } else {
      return <Clock size={16} className="text-amber-600" />;
    }
  };
  
  // Calculate task progress
  const getTaskProgress = (taskId) => {
    const steps = userTaskSteps.filter(step => step.taskId === taskId);
    if (!steps.length) return 0;
    
    const completedSteps = steps.filter(step => step.status === 'completed').length;
    return Math.round((completedSteps / steps.length) * 100);
  };
  
  // Format date
  const formatDate = (dateString) => {
    if (!dateString) return '';
    const date = new Date(dateString);
    return date.toLocaleDateString('lt-LT');
  };
  
  // Navigate to task details
  const goToTaskDetails = (taskId) => {
    updateAppState({ 
      currentView: 'taskDetails', 
      selectedTask: taskId 
    });
  };
  
  // Navigate to all tasks
  const goToAllTasks = () => {
    updateAppState({ currentView: 'tasks' });
  };
  
  // Navigate to hive details
  const goToHiveDetails = (hiveId) => {
    updateAppState({ 
      currentView: 'hiveDetails', 
      selectedHive: hiveId 
    });
  };
  
  return (
    <div className="p-6 pb-20 md:pb-6">
      <h2 className="text-2xl font-bold mb-6">Sveiki, {appState.currentUser.name}!</h2>
      
      {/* Statistikos kortelės */}
      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-4 mb-8">
        <div className="bg-white rounded-lg shadow-sm p-4 flex items-center border-l-4 border-amber-500">
          <div className="rounded-full bg-amber-100 p-3 mr-4">
            <Hexagon size={24} className="text-amber-600" />
          </div>
          <div>
            <div className="text-sm text-gray-500">Viso avilių</div>
            <div className="text-2xl font-bold">{userHives.length}</div>
          </div>
        </div>
        
        <div className="bg-white rounded-lg shadow-sm p-4 flex items-center border-l-4 border-blue-500">
          <div className="rounded-full bg-blue-100 p-3 mr-4">
            <CheckSquare size={24} className="text-blue-600" />
          </div>
          <div>
            <div className="text-sm text-gray-500">Viso užduočių</div>
            <div className="text-2xl font-bold">{userTasks.length}</div>
          </div>
        </div>
        
        <div className="bg-white rounded-lg shadow-sm p-4 flex items-center border-l-4 border-green-500">
          <div className="rounded-full bg-green-100 p-3 mr-4">
            <CheckCircle size={24} className="text-green-600" />
          </div>
          <div>
            <div className="text-sm text-gray-500">Atliktos užduotys</div>
            <div className="text-2xl font-bold">{taskCounts.completed}</div>
          </div>
        </div>
        
        <div className="bg-white rounded-lg shadow-sm p-4 flex items-center border-l-4 border-red-500">
          <div className="rounded-full bg-red-100 p-3 mr-4">
            <AlertCircle size={24} className="text-red-600" />
          </div>
          <div>
            <div className="text-sm text-gray-500">Vėluojančios</div>
            <div className="text-2xl font-bold">{taskCounts.overdue}</div>
          </div>
        </div>
      </div>
      
      {/* Aviliai su nuotraukomis */}
      <div className="mb-8">
        <div className="flex justify-between items-center mb-4">
          <h3 className="text-xl font-semibold">Mano aviliai</h3>
          <button 
            onClick={() => updateAppState({ currentView: 'hives' })}
            className="text-amber-600 hover:text-amber-700 text-sm font-medium flex items-center"
            type="button"
          >
            Visi aviliai 
            <ArrowRight size={16} className="ml-1" />
          </button>
        </div>
        
        {userHives.length > 0 ? (
          <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4">
            {userHives.slice(0, 4).map(hive => (
              <div 
                key={hive.id} 
                className="bg-white rounded-lg shadow-md overflow-hidden hover:shadow-lg transition-shadow cursor-pointer"
                onClick={() => goToHiveDetails(hive.id)}
              >
                <div className="relative h-40 bg-gray-100">
                  <img 
                    src={hive.image || '/default-hive.jpg'} 
                    alt={hive.name} 
                    className="w-full h-full object-cover"
                  />
                  <button 
                    onClick={(e) => {
                      e.stopPropagation();
                      changeHiveImage(hive.id);
                    }}
                    className="absolute top-2 right-2 bg-white bg-opacity-75 p-1 rounded-full hover:bg-opacity-100 focus:outline-none"
                    aria-label="Keisti nuotrauką"
                    type="button"
                  >
                    <Camera size={18} />
                  </button>
                </div>
                <div className="p-4">
                  <h4 className="font-semibold text-lg mb-1">{hive.name}</h4>
                  <p className="text-sm text-gray-600 mb-3 truncate">{hive.location || 'Nenurodyta lokacija'}</p>
                  <div className="flex justify-between text-xs text-gray-500">
                    <span>Sukurtas: {formatDate(hive.dateCreated)}</span>
                  </div>
                </div>
              </div>
            ))}
          </div>
        ) : (
          <div className="bg-amber-50 border-l-4 border-amber-500 p-4 mb-8">
            <div className="flex">
              <div className="ml-3">
                <h3 className="text-lg font-medium text-amber-800">Neturite priskirtų avilių</h3>
                <div className="mt-2 text-amber-700">
                  <p>
                    Jums dar nepriskirta jokių avilių. Kreipkitės į administratorių, kad priskirtų avilius jūsų paskyrai.
                  </p>
                </div>
              </div>
            </div>
          </div>
        )}
      </div>
      
      {/* Tasks card */}
      <div className="bg-white rounded-lg shadow-md overflow-hidden">
        <div className="bg-amber-600 text-white px-4 py-3 flex justify-between items-center">
          <h3 className="text-lg font-semibold">Artėjančios užduotys</h3>
          <button 
            onClick={goToAllTasks}
            className="text-white hover:text-amber-200 focus:outline-none"
            type="button"
          >
            Visos užduotys <ArrowRight size={16} className="inline ml-1" />
          </button>
        </div>
        
        <div className="p-4">
          {upcomingTasks.length > 0 ? (
            <div className="divide-y divide-gray-200">
              {upcomingTasks.map(task => (
                <div 
                  key={task.id} 
                  className="py-3 cursor-pointer hover:bg-gray-50"
                  onClick={() => goToTaskDetails(task.id)}
                >
                  <div className="flex items-start justify-between">
                    <div className="flex items-start space-x-2">
                      {getStatusIcon(task.status, task.dueDate)}
                      <div>
                        <h4 className="font-medium">{task.title}</h4>
                        <p className={`text-xs ${isOverdue(task.dueDate) && task.status !== 'completed' ? 'text-red-600' : 'text-gray-500'}`}>
                          Terminas: {formatDate(task.dueDate)}
                        </p>
                        
                        {/* Task progress */}
                        <div className="mt-2 w-full max-w-xs">
                          <div className="flex justify-between text-xs mb-1">
                            <span>Progresas</span>
                            <span>{getTaskProgress(task.id)}%</span>
                          </div>
                          <div className="w-full bg-gray-200 rounded-full h-1.5">
                            <div 
                              className="bg-amber-600 h-1.5 rounded-full" 
                              style={{ width: `${getTaskProgress(task.id)}%` }}
                            ></div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          ) : (
            <div className="text-center py-6">
              <CheckCircle size={36} className="mx-auto text-green-400 mb-2" />
              <p className="text-gray-600 font-medium">Puiku! Nėra artėjančių užduočių.</p>
              <p className="text-gray-500 text-sm mt-1">Visos užduotys yra atliktos arba dar nepriskirtos.</p>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default DashboardView; 