import React, { useState, useEffect } from 'react';
import { LogIn, User, Lock, AlertCircle, Eye, EyeOff } from 'lucide-react';
import { useApp } from '../../context/AppContext';

const Login = () => {
  const { login, updateForm, forms } = useApp();
  
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const [loading, setLoading] = useState(false);
  const [showPassword, setShowPassword] = useState(false);
  const [readyToLogin, setReadyToLogin] = useState(false);
  
  // Šis useEffect bus naudojamas stebėti, kada forms.login pasikeis ir tada kviesti login funkciją
  useEffect(() => {
    if (readyToLogin) {
      console.log('Bandoma prisijungti su:', forms.login);
      try {
        const success = login();
        
        if (!success) {
          setError('Neteisingas el. paštas arba slaptažodis');
          setLoading(false);
        }
      } catch (err) {
        console.error('Klaida prisijungiant:', err);
        setError(err.message || 'Įvyko nenumatyta klaida');
        setLoading(false);
      }
      setReadyToLogin(false);
    }
  }, [forms.login, readyToLogin]);
  
  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (!username.trim() || !password.trim()) {
      setError('Įveskite el. paštą ir slaptažodį');
      return;
    }
    
    setLoading(true);
    setError('');
    
    console.log('Ruošiamasi prisijungti:', { email: username, password });
    
    // Pirma atnaujininame formos laukus kontekste
    updateForm('login', 'email', username);
    updateForm('login', 'password', password);
    
    // Pažymime, kad esame pasiruošę prisijungti (useEffect sureaguos)
    setReadyToLogin(true);
  };
  
  // Handle demo login (for development/testing)
  const handleDemoLogin = (e) => {
    e.preventDefault();
    setUsername('admin@example.com');
    setPassword('password');
    
    // Submit the form with a slight delay to show the filled fields
    setTimeout(() => {
      const mockEvent = { preventDefault: () => {} };
      handleSubmit(mockEvent);
    }, 300);
  };
  
  return (
    <div className="min-h-screen flex items-center justify-center bg-gray-50 py-12 px-4 sm:px-6 lg:px-8">
      <div className="max-w-md w-full space-y-8">
        <div>
          <div className="flex justify-center">
            <div className="w-20 h-20 bg-amber-100 rounded-full flex items-center justify-center">
              <svg viewBox="0 0 24 24" width="36" height="36" stroke="currentColor" strokeWidth="2" fill="none" strokeLinecap="round" strokeLinejoin="round" className="text-amber-600">
                <path d="M6 2L3 6v14a2 2 0 0 0 2 2h14a2 2 0 0 0 2-2V6l-3-4z"></path>
                <line x1="3" y1="6" x2="21" y2="6"></line>
                <path d="M16 10a4 4 0 0 1-8 0"></path>
              </svg>
            </div>
          </div>
          <h2 className="mt-6 text-center text-3xl font-extrabold text-gray-900">
            Prisijungimas prie sistemos
          </h2>
          <p className="mt-2 text-center text-sm text-gray-600">
            Įveskite savo prisijungimo duomenis
          </p>
        </div>
        
        <form className="mt-8 space-y-6" onSubmit={handleSubmit}>
          {error && (
            <div className="bg-red-50 border-l-4 border-red-500 p-4 rounded-md">
              <div className="flex items-center">
                <AlertCircle size={20} className="text-red-500 mr-2" />
                <p className="text-sm text-red-700">{error}</p>
              </div>
            </div>
          )}
          
          <div className="rounded-md shadow-sm -space-y-px">
            <div className="relative">
              <label htmlFor="username" className="sr-only">
                El. paštas
              </label>
              <User size={20} className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400" />
              <input
                id="username"
                name="username"
                type="email"
                autoComplete="email"
                required
                value={username}
                onChange={(e) => setUsername(e.target.value)}
                className="appearance-none rounded-none rounded-t-md relative block w-full px-10 py-3 border border-gray-300 placeholder-gray-500 text-gray-900 focus:outline-none focus:ring-amber-500 focus:border-amber-500 focus:z-10 sm:text-sm"
                placeholder="El. paštas"
              />
            </div>
            <div className="relative">
              <label htmlFor="password" className="sr-only">
                Slaptažodis
              </label>
              <Lock size={20} className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400" />
              <input
                id="password"
                name="password"
                type={showPassword ? "text" : "password"}
                autoComplete="current-password"
                required
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                className="appearance-none rounded-none rounded-b-md relative block w-full px-10 py-3 border border-gray-300 placeholder-gray-500 text-gray-900 focus:outline-none focus:ring-amber-500 focus:border-amber-500 focus:z-10 sm:text-sm"
                placeholder="Slaptažodis"
              />
              <button
                type="button"
                onClick={() => setShowPassword(!showPassword)}
                className="absolute right-3 top-1/2 transform -translate-y-1/2 text-gray-400 hover:text-gray-500"
              >
                {showPassword ? (
                  <EyeOff size={20} />
                ) : (
                  <Eye size={20} />
                )}
              </button>
            </div>
          </div>
          
          <div className="flex items-center justify-between">
            <div className="flex items-center">
              <input
                id="remember-me"
                name="remember-me"
                type="checkbox"
                className="h-4 w-4 text-amber-600 focus:ring-amber-500 border-gray-300 rounded"
              />
              <label htmlFor="remember-me" className="ml-2 block text-sm text-gray-900">
                Prisiminti
              </label>
            </div>
            
            <div className="text-sm">
              <a href="#" className="font-medium text-amber-600 hover:text-amber-500">
                Pamiršote slaptažodį?
              </a>
            </div>
          </div>
          
          <div>
            <button
              type="submit"
              disabled={loading}
              className={`group relative w-full flex justify-center py-3 px-4 border border-transparent text-sm font-medium rounded-md text-white ${
                loading ? 'bg-amber-400' : 'bg-amber-600 hover:bg-amber-700'
              } focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-amber-500`}
            >
              <span className="absolute left-0 inset-y-0 flex items-center pl-3">
                <LogIn size={20} className="text-amber-300 group-hover:text-amber-200" />
              </span>
              {loading ? 'Jungiamasi...' : 'Prisijungti'}
            </button>
          </div>
          
          <div className="mt-6">
            <div className="relative">
              <div className="absolute inset-0 flex items-center">
                <div className="w-full border-t border-gray-300"></div>
              </div>
              <div className="relative flex justify-center text-sm">
                <span className="px-2 bg-gray-50 text-gray-500">
                  Arba
                </span>
              </div>
            </div>
            
            <div className="mt-6">
              <button
                onClick={handleDemoLogin}
                className="w-full flex justify-center py-2 px-4 border border-gray-300 rounded-md shadow-sm text-sm font-medium text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-amber-500"
              >
                Prisijungti demonstraciniu režimu
              </button>
            </div>
          </div>
        </form>
        
        <div className="mt-6 text-center">
          <p className="text-sm text-gray-600">
            Demonstraciniai prisijungimai: <br />
            <span className="text-gray-700 font-medium">Administratorius:</span> admin@example.com / password <br />
            <span className="text-gray-700 font-medium">Vartotojas:</span> jonas@example.com / password
          </p>
        </div>
      </div>
    </div>
  );
};

export default Login; 