import React, { useState } from 'react';
import { 
  Home, 
  CheckSquare, 
  User, 
  Bell, 
  LogOut, 
  Menu, 
  X,
  Hexagon,
  Settings,
  Users,
  UserPlus,
  Layers,
  BookOpen,
  FileText,
  Box,
  MessageSquare,
  BarChart3,
  ListTodo,
  ClipboardCheck
} from 'lucide-react';
import { useApp } from '../../context/AppContext';

const Sidebar = () => {
  const { appState, updateAppState, logout } = useApp();
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);
  
  // Get unread notifications count
  const unreadNotifications = appState.unreadNotifications || 0;
  
  // Navigacijos elementai
  const navItems = [
    {
      title: 'Apžvalga',
      icon: <Home size={20} />,
      view: 'dashboard',
      visibleFor: ['admin', 'user']
    },
    {
      title: 'Aviliai',
      icon: <Box size={20} />,
      view: 'hives',
      visibleFor: ['user']
    },
    {
      title: 'Užduotys',
      icon: <CheckSquare size={20} />,
      view: 'tasks',
      visibleFor: ['user']
    },
    {
      title: 'Žinutės',
      icon: <MessageSquare size={20} />,
      view: 'messages',
      visibleFor: ['user', 'admin']
    },
    {
      title: 'Profilis',
      icon: <User size={20} />,
      view: 'profile',
      visibleFor: ['user', 'admin']
    },
    // Administraciniai puslapiai
    {
      title: 'Vartotojai',
      icon: <Users size={20} />,
      view: 'admin-users',
      visibleFor: ['admin']
    },
    {
      title: 'Grupės',
      icon: <UserPlus size={20} />,
      view: 'admin-groups',
      visibleFor: ['admin']
    },
    {
      title: 'Aviliai',
      icon: <Box size={20} />,
      view: 'admin-hives',
      visibleFor: ['admin']
    },
    {
      title: 'Žingsniai',
      icon: <ListTodo size={20} />,
      view: 'admin-steps',
      visibleFor: ['admin']
    },
    {
      title: 'Užduotys',
      icon: <CheckSquare size={20} />,
      view: 'admin-tasks',
      visibleFor: ['admin']
    },
    {
      title: 'Šablonai',
      icon: <ClipboardCheck size={20} />,
      view: 'admin-templates',
      visibleFor: ['admin']
    },
  ];

  // Toggle mobile menu
  const toggleMobileMenu = () => {
    setMobileMenuOpen(!mobileMenuOpen);
  };
  
  // Handle navigation click
  const handleNavClick = (view) => {
    updateAppState({ currentView: view });
    setMobileMenuOpen(false);
  };
  
  // Handle logout
  const handleLogout = () => {
    logout();
  };
  
  // Filtruojame navigacijos elementus pagal vartotojo rolę
  const filteredNavItems = navItems.filter(item => {
    if (!appState.currentUser) return false;
    return item.visibleFor.includes(appState.currentUser.role);
  });
  
  return (
    <>
      {/* Mobile menu button */}
      <div className="fixed top-0 left-0 z-20 m-4 md:hidden">
        <button
          onClick={toggleMobileMenu}
          className="p-2 text-gray-600 bg-white rounded-md shadow-md focus:outline-none"
        >
          <Menu size={24} />
        </button>
      </div>
      
      {/* Sidebar for mobile */}
      <div
        className={`fixed inset-0 z-30 bg-gray-900 bg-opacity-50 ${
          mobileMenuOpen ? 'block' : 'hidden'
        } md:hidden`}
        onClick={toggleMobileMenu}
      />
      
      <div
        className={`fixed top-0 left-0 z-40 h-full w-64 bg-white shadow-lg transform ${
          mobileMenuOpen ? 'translate-x-0' : '-translate-x-full'
        } transition-transform duration-300 md:translate-x-0 md:static md:z-0`}
      >
        {/* Close button for mobile */}
        <div className="flex justify-end p-4 md:hidden">
          <button
            onClick={toggleMobileMenu}
            className="p-2 text-gray-600 rounded-md focus:outline-none"
          >
            <X size={24} />
          </button>
        </div>
        
        {/* Logo and app name */}
        <div className="flex items-center justify-center p-4 border-b border-gray-200">
          <div className="w-12 h-12 bg-amber-100 rounded-full flex items-center justify-center">
            <Hexagon size={28} className="text-amber-600" />
          </div>
          <div className="ml-3">
            <h1 className="text-xl font-semibold text-gray-800">Bitininkas</h1>
            <p className="text-sm text-gray-500">Bitininkystės sistema</p>
          </div>
        </div>
        
        {/* Navigation items */}
        <nav className="mt-6 px-4">
          <ul className="space-y-2">
            {filteredNavItems.map((item, index) => (
              <li key={index}>
                <button
                  className={`flex items-center w-full p-3 rounded-md font-medium ${
                    appState.currentView === item.view
                      ? 'bg-amber-100 text-amber-800'
                      : 'text-gray-600 hover:bg-gray-100'
                  }`}
                  onClick={() => handleNavClick(item.view)}
                >
                  <span className="mr-3">{item.icon}</span>
                  {item.title}
                </button>
              </li>
            ))}
          </ul>
        </nav>
        
        {/* User profile */}
        <div className="absolute bottom-0 w-full border-t border-gray-200 p-4">
          <div className="flex items-center">
            <div className="w-10 h-10 bg-gray-200 rounded-full flex items-center justify-center">
              <User size={20} className="text-gray-600" />
            </div>
            <div className="ml-3 flex-1 truncate">
              <p className="text-sm font-medium text-gray-800 truncate">
                {appState.currentUser ? appState.currentUser.name : 'Vartotojas'}
              </p>
              <p className="text-xs text-gray-500 truncate">
                {appState.currentUser ? appState.currentUser.email : ''}
              </p>
            </div>
            <button
              onClick={handleLogout}
              className="p-2 text-gray-500 hover:text-red-500 rounded-md focus:outline-none"
            >
              <LogOut size={20} />
            </button>
          </div>
        </div>
      </div>
    </>
  );
};

export default Sidebar; 