import React, { useState, useEffect } from 'react';
import { Plus, Edit, Trash, Search, X, Save, Check, Users, User, Info } from 'lucide-react';
import { useApp } from '../../context/AppContext';

const AdminHives = () => {
  const { 
    appState, 
    updateAppState, 
    forms, 
    updateForm, 
    hives,
    addHive,
    updateHive,
    deleteHive,
    users,
    groups
  } = useApp();
  
  const [showForm, setShowForm] = useState(false);
  const [editingHiveId, setEditingHiveId] = useState(null);
  const [searchTerm, setSearchTerm] = useState('');
  const [confirmDelete, setConfirmDelete] = useState(null);
  const [showDetails, setShowDetails] = useState(null);
  
  // Form handlers
  const resetForm = () => {
    updateForm('hive', 'name', '');
    updateForm('hive', 'description', '');
    updateForm('hive', 'location', '');
    updateForm('hive', 'type', '');
    updateForm('hive', 'groupId', '');
    updateForm('hive', 'status', 'active');
  };
  
  const handleSubmit = (e) => {
    e.preventDefault();
    
    const formData = forms.hive;
    
    // Validacija
    if (!formData.name) {
      alert('Prašome įvesti avilio pavadinimą');
      return;
    }
    
    if (!formData.groupId) {
      alert('Prašome pasirinkti grupę');
      return;
    }
    
    if (editingHiveId) {
      // Redagavimas
      updateHive(editingHiveId);
      setEditingHiveId(null);
    } else {
      // Naujo avilio pridėjimas
      addHive();
    }
    
    setShowForm(false);
    resetForm();
  };
  
  const editHive = (hive) => {
    setEditingHiveId(hive.id);
    updateForm('hive', 'name', hive.name);
    updateForm('hive', 'description', hive.description || '');
    updateForm('hive', 'location', hive.location || '');
    updateForm('hive', 'type', hive.type || '');
    updateForm('hive', 'groupId', hive.groupId || '');
    updateForm('hive', 'status', hive.status || 'active');
    setShowForm(true);
  };
  
  const confirmDeleteHive = (hiveId) => {
    if (confirmDelete === hiveId) {
      // Avilio šalinimas
      deleteHive(hiveId);
      setConfirmDelete(null);
      
      // Jei rodome šio avilio detales, uždarome langą
      if (showDetails === hiveId) {
        setShowDetails(null);
      }
    } else {
      setConfirmDelete(hiveId);
    }
  };
  
  // Filtruojame pagal paiešką
  const filteredHives = hives.filter(hive => 
    hive.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
    (hive.description && hive.description.toLowerCase().includes(searchTerm.toLowerCase())) ||
    (hive.location && hive.location.toLowerCase().includes(searchTerm.toLowerCase())) ||
    (hive.type && hive.type.toLowerCase().includes(searchTerm.toLowerCase()))
  );
  
  // Gauname avilio grupės pavadinimą
  const getGroupName = (groupId) => {
    if (!groups || !groupId) return '-';
    const group = groups.find(g => g.id === groupId);
    return group ? group.name : '-';
  };
  
  // Gauname vartotojų, priskirtų aviliui, kiekį
  const getHiveUserCount = (hiveId) => {
    if (!users) return 0;
    return users.filter(user => 
      user.hiveId === hiveId || 
      (user.hives && user.hives.includes(hiveId))
    ).length;
  };
  
  // Gauname užduočių, priskirtų aviliui, kiekį
  const getHiveTaskCount = (hiveId) => {
    const tasksList = appState.tasks || [];
    return tasksList.filter(task => task.hiveId === hiveId).length;
  };
  
  // Gauname avilio vartotojus
  const getHiveUsers = (hiveId) => {
    if (!users) return [];
    return users.filter(user => 
      user.hiveId === hiveId || 
      (user.hives && user.hives.includes(hiveId))
    );
  };
  
  return (
    <div className="p-6">
      <div className="flex justify-between items-center mb-6">
        <h2 className="text-2xl font-bold">Avilių administravimas</h2>
        <button 
          onClick={() => {
            resetForm();
            setEditingHiveId(null);
            setShowForm(true);
          }}
          className="bg-amber-600 text-white px-4 py-2 rounded-md hover:bg-amber-700 flex items-center"
        >
          <Plus size={16} className="mr-2" />
          Pridėti avilį
        </button>
      </div>
      
      {/* Paieškos laukas */}
      <div className="mb-6 relative">
        <input 
          type="text"
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
          placeholder="Ieškoti avilių..."
          className="w-full p-3 pl-10 pr-10 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-amber-500"
        />
        <Search size={20} className="absolute left-3 top-3.5 text-gray-400" />
        {searchTerm && (
          <button 
            onClick={() => setSearchTerm('')}
            className="absolute right-3 top-3.5 text-gray-400 hover:text-gray-600"
          >
            <X size={20} />
          </button>
        )}
      </div>
      
      {/* Avilio kūrimo/redagavimo forma */}
      {showForm && (
        <div className="bg-white p-6 rounded-lg shadow-md mb-6">
          <h3 className="text-lg font-semibold mb-4">{editingHiveId ? 'Redaguoti avilį' : 'Pridėti naują avilį'}</h3>
          
          <form onSubmit={handleSubmit} className="space-y-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">Pavadinimas *</label>
              <input 
                type="text"
                value={forms.hive?.name || ''}
                onChange={(e) => updateForm('hive', 'name', e.target.value)}
                className="w-full p-2 border border-gray-300 rounded-md"
                placeholder="Avilio pavadinimas"
                required
              />
            </div>
            
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">Aprašymas</label>
              <textarea 
                value={forms.hive?.description || ''}
                onChange={(e) => updateForm('hive', 'description', e.target.value)}
                className="w-full p-2 border border-gray-300 rounded-md"
                rows={2}
                placeholder="Trumpas avilio aprašymas"
              ></textarea>
            </div>
            
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Vieta</label>
                <input 
                  type="text"
                  value={forms.hive?.location || ''}
                  onChange={(e) => updateForm('hive', 'location', e.target.value)}
                  className="w-full p-2 border border-gray-300 rounded-md"
                  placeholder="Avilio vieta ar adresas"
                />
              </div>
              
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Tipas</label>
                <input 
                  type="text"
                  value={forms.hive?.type || ''}
                  onChange={(e) => updateForm('hive', 'type', e.target.value)}
                  className="w-full p-2 border border-gray-300 rounded-md"
                  placeholder="Avilio tipas"
                />
              </div>
            </div>
            
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Grupė *</label>
                <select
                  value={forms.hive?.groupId || ''}
                  onChange={(e) => updateForm('hive', 'groupId', e.target.value)}
                  className="w-full p-2 border border-gray-300 rounded-md"
                  required
                >
                  <option value="">Pasirinkti grupę</option>
                  {groups.map(group => (
                    <option key={group.id} value={group.id}>
                      {group.name}
                    </option>
                  ))}
                </select>
              </div>
              
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Būsena</label>
                <select
                  value={forms.hive?.status || 'active'}
                  onChange={(e) => updateForm('hive', 'status', e.target.value)}
                  className="w-full p-2 border border-gray-300 rounded-md"
                >
                  <option value="active">Aktyvus</option>
                  <option value="inactive">Neaktyvus</option>
                  <option value="maintenance">Priežiūra</option>
                </select>
              </div>
            </div>
            
            <div className="flex justify-end space-x-3 mt-6">
              <button 
                type="button"
                onClick={() => {
                  setShowForm(false);
                  resetForm();
                }}
                className="px-4 py-2 border border-gray-300 rounded-md text-gray-700 hover:bg-gray-50"
              >
                Atšaukti
              </button>
              <button 
                type="submit"
                className="px-4 py-2 bg-amber-600 text-white rounded-md hover:bg-amber-700 flex items-center"
              >
                <Save size={16} className="mr-2" />
                {editingHiveId ? 'Atnaujinti' : 'Pridėti'}
              </button>
            </div>
          </form>
        </div>
      )}
      
      {/* Avilio detalės */}
      {showDetails && (
        <div className="bg-white p-6 rounded-lg shadow-md mb-6">
          {hives.filter(h => h.id === showDetails).map(hive => (
            <div key={hive.id}>
              <div className="flex justify-between items-start">
                <h3 className="text-xl font-semibold">{hive.name}</h3>
                <button 
                  onClick={() => setShowDetails(null)}
                  className="text-gray-500 hover:text-gray-700"
                >
                  <X size={20} />
                </button>
              </div>
              
              <div className="mt-4 grid grid-cols-1 md:grid-cols-2 gap-6">
                <div>
                  <h4 className="text-md font-medium mb-2 text-gray-700">Avilio informacija</h4>
                  <div className="bg-gray-50 p-4 rounded-md">
                    <div className="mb-3">
                      <span className="block text-sm text-gray-500">Aprašymas</span>
                      <span className="block text-gray-800">{hive.description || '-'}</span>
                    </div>
                    <div className="mb-3">
                      <span className="block text-sm text-gray-500">Vieta</span>
                      <span className="block text-gray-800">{hive.location || '-'}</span>
                    </div>
                    <div className="mb-3">
                      <span className="block text-sm text-gray-500">Tipas</span>
                      <span className="block text-gray-800">{hive.type || '-'}</span>
                    </div>
                    <div className="mb-3">
                      <span className="block text-sm text-gray-500">Grupė</span>
                      <span className="block text-gray-800">{getGroupName(hive.groupId)}</span>
                    </div>
                    <div>
                      <span className="block text-sm text-gray-500">Būsena</span>
                      <span className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${
                        hive.status === 'active' 
                          ? 'bg-green-100 text-green-800' 
                          : hive.status === 'inactive' 
                            ? 'bg-gray-100 text-gray-800' 
                            : 'bg-yellow-100 text-yellow-800'
                      }`}>
                        {hive.status === 'active' 
                          ? 'Aktyvus' 
                          : hive.status === 'inactive' 
                            ? 'Neaktyvus' 
                            : 'Priežiūra'}
                      </span>
                    </div>
                  </div>
                </div>
                
                <div>
                  <h4 className="text-md font-medium mb-2 text-gray-700">Vartotojai ({getHiveUsers(hive.id).length})</h4>
                  <div className="bg-gray-50 p-4 rounded-md h-64 overflow-y-auto">
                    {getHiveUsers(hive.id).length > 0 ? (
                      <ul className="divide-y divide-gray-200">
                        {getHiveUsers(hive.id).map(user => (
                          <li key={user.id} className="py-2">
                            <div className="flex items-center">
                              <div className="flex-shrink-0 h-8 w-8 rounded-full bg-amber-100 flex items-center justify-center">
                                <User size={16} />
                              </div>
                              <div className="ml-3">
                                <p className="text-sm font-medium text-gray-900">{user.name}</p>
                                <p className="text-xs text-gray-500">{user.email}</p>
                              </div>
                            </div>
                          </li>
                        ))}
                      </ul>
                    ) : (
                      <div className="text-center text-gray-500 py-4">
                        Prie šio avilio nėra priskirta vartotojų
                      </div>
                    )}
                  </div>
                  
                  <h4 className="text-md font-medium mb-2 mt-4 text-gray-700">Užduočių statistika</h4>
                  <div className="bg-gray-50 p-4 rounded-md">
                    <div className="grid grid-cols-2 gap-4">
                      <div className="bg-white p-3 rounded-md shadow-sm">
                        <p className="text-xs text-gray-500">Iš viso užduočių</p>
                        <p className="text-xl font-semibold text-amber-600">{getHiveTaskCount(hive.id)}</p>
                      </div>
                      
                      <div className="bg-white p-3 rounded-md shadow-sm">
                        <p className="text-xs text-gray-500">Progresas</p>
                        <p className="text-xl font-semibold text-green-600">
                          {getHiveTaskCount(hive.id) > 0 
                            ? Math.round(
                                (appState.tasks
                                  .filter(task => task.hiveId === hive.id && task.status === 'completed')
                                  .length / getHiveTaskCount(hive.id)) * 100
                              ) + '%'
                            : '0%'
                          }
                        </p>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              
              <div className="mt-6 flex justify-end space-x-3">
                <button 
                  onClick={() => editHive(hive)}
                  className="px-3 py-1.5 bg-amber-100 text-amber-800 rounded flex items-center"
                >
                  <Edit size={14} className="mr-1" />
                  Redaguoti
                </button>
                <button 
                  onClick={() => confirmDeleteHive(hive.id)}
                  className={`px-3 py-1.5 bg-red-100 text-red-800 rounded flex items-center ${
                    confirmDelete === hive.id ? 'animate-pulse' : ''
                  }`}
                >
                  <Trash size={14} className="mr-1" />
                  {confirmDelete === hive.id ? 'Patvirtinti' : 'Šalinti'}
                </button>
              </div>
            </div>
          ))}
        </div>
      )}
      
      {/* Avilių sąrašas */}
      <div className="bg-white rounded-lg shadow-md overflow-hidden">
        <table className="min-w-full divide-y divide-gray-200">
          <thead className="bg-gray-50">
            <tr>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Avilys</th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Vieta</th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Grupė</th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Būsena</th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Statistika</th>
              <th className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider">Veiksmai</th>
            </tr>
          </thead>
          <tbody className="bg-white divide-y divide-gray-200">
            {filteredHives.length > 0 ?
              filteredHives.map(hive => (
                <tr key={hive.id} className="hover:bg-gray-50">
                  <td className="px-6 py-4 whitespace-nowrap">
                    <div className="flex items-center">
                      <div className="text-sm font-medium text-gray-900">{hive.name}</div>
                    </div>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    <div className="text-sm text-gray-900">{hive.location || '-'}</div>
                    <div className="text-xs text-gray-500">{hive.type || '-'}</div>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    <div className="text-sm text-gray-900">{getGroupName(hive.groupId)}</div>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    <span className={`px-2 py-1 inline-flex text-xs leading-5 font-semibold rounded-full ${
                      hive.status === 'active' 
                        ? 'bg-green-100 text-green-800' 
                        : hive.status === 'inactive' 
                          ? 'bg-gray-100 text-gray-800' 
                          : 'bg-yellow-100 text-yellow-800'
                    }`}>
                      {hive.status === 'active' 
                        ? 'Aktyvus' 
                        : hive.status === 'inactive' 
                          ? 'Neaktyvus' 
                          : 'Priežiūra'}
                    </span>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    <div className="flex items-center">
                      <Users size={16} className="text-gray-500 mr-1" />
                      <span className="text-sm text-gray-900 mr-3">{getHiveUserCount(hive.id)}</span>
                      
                      <Check size={16} className="text-gray-500 mr-1" />
                      <span className="text-sm text-gray-900">{getHiveTaskCount(hive.id)}</span>
                    </div>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap text-right text-sm font-medium">
                    <button 
                      onClick={() => setShowDetails(hive.id)}
                      className="text-indigo-600 hover:text-indigo-900 mx-2"
                      title="Išsamesnė informacija"
                    >
                      <Info size={18} />
                    </button>
                    <button 
                      onClick={() => editHive(hive)}
                      className="text-amber-600 hover:text-amber-900 mx-2"
                      title="Redaguoti avilį"
                    >
                      <Edit size={18} />
                    </button>
                    <button 
                      onClick={() => confirmDeleteHive(hive.id)}
                      className={`text-red-600 hover:text-red-900 mx-2 ${
                        confirmDelete === hive.id ? 'animate-pulse' : ''
                      }`}
                      title={confirmDelete === hive.id ? 'Patvirtinti šalinimą' : 'Šalinti avilį'}
                    >
                      <Trash size={18} />
                    </button>
                  </td>
                </tr>
              )) : (
                <tr>
                  <td colSpan={6} className="px-6 py-4 text-center text-gray-500">
                    {searchTerm ? 'Nerasta avilių pagal paiešką' : 'Nėra sukurtų avilių'}
                  </td>
                </tr>
              )
            }
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default AdminHives; 