import React, { createContext, useContext, useState, useEffect } from 'react';
import { v4 as uuidv4 } from 'uuid';
import { sampleHives, sampleTasks, sampleHoneyRecords, sampleHiveEvents, sampleHivePhotos } from '../data/sampleData';
import { 
  users, 
  userGroups,
  userTaskSteps, 
  hives, 
  tasks, 
  taskTemplates,
  notifications, 
  honeyRecords, 
  hiveEvents, 
  hivePhotos, 
  defaultAppState, 
  defaultForms 
} from '../data/initialData';

// Create context
export const AppContext = createContext();

// Custom hook for using app context
export const useApp = () => useContext(AppContext);

// Provider component
export const AppProvider = ({ children }) => {
  // Data state
  const [usersList, setUsers] = useState(users);
  const [userGroupsList, setUserGroups] = useState(userGroups);
  const [hivesList, setHives] = useState(hives);
  const [tasksList, setTasks] = useState(tasks);
  const [taskTemplatesList, setTaskTemplates] = useState(taskTemplates);
  const [userTaskStepsList, setUserTaskSteps] = useState(userTaskSteps);
  const [notificationsList, setNotifications] = useState(notifications);
  
  // Nauji duomenų masyvai
  const [steps, setSteps] = useState([]);
  const [templates, setTemplates] = useState([]);
  
  // App state - Initialize with all required data arrays
  const [appState, setAppState] = useState(defaultAppState({ 
    users: usersList, 
    userGroups: userGroupsList, 
    hives: hivesList, 
    tasks: tasksList, 
    templates: templates,
    steps: steps,
    honeyRecords: honeyRecords, 
    hiveEvents: hiveEvents, 
    hivePhotos: hivePhotos, 
    userTaskSteps: userTaskStepsList,
    taskComments: [],
    messages: [],
    chats: []
  }));
  
  // Forms state
  const [forms, setForms] = useState(defaultForms);
  
  // Editing state for hives
  const [editing, setEditing] = useState({
    hiveId: null,
    hiveName: '',
    locationId: null,
    locationValue: '',
    noteId: null,
    noteText: ''
  });

  // Count unread notifications on state change
  useEffect(() => {
    if (appState.currentUser) {
      const unreadCount = notificationsList.filter(
        notification => notification.userId === appState.currentUser.id && !notification.read
      ).length;
      
      updateAppState({
        unreadNotifications: unreadCount
      });
    }
  }, [appState.currentUser, notificationsList]);
  
  // Initialize forms when user logs in
  useEffect(() => {
    if (appState.currentUser) {
      setForms(prevForms => ({
        ...prevForms,
        profile: {
          name: appState.currentUser.name || '',
          email: appState.currentUser.email || '',
          phone: appState.currentUser.phone || '',
          address: appState.currentUser.address || '',
          isEdited: false
        }
      }));
    }
  }, [appState.currentUser]);
  
  // Update app state
  const updateAppState = (newState) => {
    setAppState(prevState => ({
      ...prevState,
      ...newState
    }));
  };
  
  // Update form state
  const updateForm = (formName, field, value) => {
    setForms(prevForms => {
      // Make sure the form exists
      if (!prevForms[formName]) {
        prevForms[formName] = {};
      }
      
      // Update for profile to track changes
      if (formName === 'profile') {
        return {
          ...prevForms,
          profile: {
            ...prevForms.profile,
            [field]: value,
            isEdited: true
          }
        };
      }
      
      return {
        ...prevForms,
        [formName]: {
          ...prevForms[formName],
          [field]: value
        }
      };
    });
  };
  
  // Login function
  const login = () => {
    const email = forms.login.email;
    const password = forms.login.password;
    
    const user = usersList.find(u => u.email === email && u.password === password);
    
    if (user) {
      updateAppState({
        currentUser: user,
        currentView: 'dashboard'
      });
      
      // Set profile form based on current user
      setForms(prevForms => ({
        ...prevForms,
        profile: {
          name: user.name || '',
          email: user.email || '',
          phone: user.phone || '',
          address: user.address || '',
          isEdited: false
        }
      }));
      
      return true;
    }
    
    return false;
  };
  
  // Logout function
  const logout = () => {
    updateAppState({
      currentUser: null,
      currentView: 'login',
      showNotifications: false,
      selectedHive: null,
      selectedTask: null
    });
  };
  
  // Handle logout
  const handleLogout = () => {
    logout();
  };
  
  // Update profile
  const updateProfile = () => {
    const { name, email, phone, address } = forms.profile;
    
    if (!name || !email) {
      // Show error
      return false;
    }
    
    // Update the user in the fake backend
    const updatedUsers = usersList.map(user => {
      if (user.id === appState.currentUser.id) {
        return {
          ...user,
          name,
          email,
          phone,
          address
        };
      }
      return user;
    });
    
    // Update current user
    updateAppState({
      currentUser: {
        ...appState.currentUser,
        name,
        email,
        phone,
        address
      },
      successMessages: {
        ...appState.successMessages,
        profile: true
      }
    });
    
    // Reset form edited state
    setForms(prevForms => ({
      ...prevForms,
      profile: {
        ...prevForms.profile,
        isEdited: false
      }
    }));
    
    // Hide success message after some time
    setTimeout(() => {
      updateAppState({
        successMessages: {
          ...appState.successMessages,
          profile: false
        }
      });
    }, 3000);
    
    return true;
  };
  
  // Change password
  const changePassword = () => {
    const { currentPassword, newPassword, confirmPassword } = forms.password;
    
    if (!currentPassword || !newPassword || !confirmPassword) {
      // Show error
      return false;
    }
    
    if (newPassword !== confirmPassword) {
      // Show error
      return false;
    }
    
    if (currentPassword !== appState.currentUser.password) {
      // Show error
      return false;
    }
    
    // Update the user in the fake backend
    const updatedUsers = usersList.map(user => {
      if (user.id === appState.currentUser.id) {
        return {
          ...user,
          password: newPassword
        };
      }
      return user;
    });
    
    // Show success message
    updateAppState({
      successMessages: {
        ...appState.successMessages,
        password: true
      }
    });
    
    // Reset form
    setForms(prevForms => ({
      ...prevForms,
      password: {
        currentPassword: '',
        newPassword: '',
        confirmPassword: ''
      }
    }));
    
    // Hide success message after some time
    setTimeout(() => {
      updateAppState({
        successMessages: {
          ...appState.successMessages,
          password: false
        }
      });
    }, 3000);
    
    return true;
  };
  
  // Add a new hive
  const addHive = () => {
    const newHive = {
      id: Date.now(),
      userId: appState.currentUser.id,
      name: forms.hive.name,
      location: forms.hive.location,
      purchaseDate: forms.hive.purchaseDate,
      image: '/api/placeholder/300/200',
      notes: forms.hive.notes || '',
      status: 'active',
      lastTaskDate: null
    };
    
    // Add to hives
    hivesList.push(newHive);
    
    // Update state
    updateAppState({
      showHiveForm: false
    });
    
    // Reset form
    setForms(prevForms => ({
      ...prevForms,
      hive: {
        name: '',
        location: '',
        purchaseDate: new Date().toISOString().split('T')[0],
        notes: ''
      }
    }));
    
    return true;
  };
  
  // Save hive name
  const saveHiveName = () => {
    if (!editing.hiveName.trim()) return;
    
    // Update hive name
    const updatedHives = hivesList.map(hive => {
      if (hive.id === editing.hiveId) {
        return { ...hive, name: editing.hiveName };
      }
      return hive;
    });
    
    // Reset editing state
    setEditing(prevState => ({
      ...prevState,
      hiveId: null,
      hiveName: ''
    }));
  };
  
  // Save hive location
  const saveHiveLocation = () => {
    // Update hive location
    const updatedHives = hivesList.map(hive => {
      if (hive.id === editing.locationId) {
        return { ...hive, location: editing.locationValue };
      }
      return hive;
    });
    
    // Reset editing state
    setEditing(prevState => ({
      ...prevState,
      locationId: null,
      locationValue: ''
    }));
  };
  
  // Show image upload dialog
  const changeHiveImage = (hiveId) => {
    updateAppState({
      showImageDialog: true,
      selectedHiveForImage: hiveId
    });
  };
  
  // Handle image dialog close
  const handleCloseImageDialog = () => {
    updateAppState({
      showImageDialog: false,
      selectedHiveForImage: null,
      selectedFile: null,
      previewUrl: '',
      isUploading: false
    });
  };
  
  // Handle file selection for image upload
  const handleFileSelect = (e) => {
    if (!e.target.files || e.target.files.length === 0) return;
    
    const file = e.target.files[0];
    const reader = new FileReader();
    
    reader.onloadend = () => {
      updateAppState({
        selectedFile: file,
        previewUrl: reader.result
      });
    };
    
    reader.readAsDataURL(file);
  };
  
  // Handle image upload
  const handleImageUpload = () => {
    if (!appState.selectedFile) return;
    
    updateAppState({ isUploading: true });
    
    // Simulate upload delay
    setTimeout(() => {
      // Check if uploading for a hive or task step
      if (appState.selectedHiveForImage) {
        // Find the hive
        const hiveIndex = hivesList.findIndex(h => h.id === appState.selectedHiveForImage);
        
        if (hiveIndex !== -1) {
          // Update hive image
          hivesList[hiveIndex] = {
            ...hivesList[hiveIndex],
            image: appState.previewUrl
          };
          
          // Add to hive photos
          const newPhoto = {
            id: Date.now(),
            hiveId: appState.selectedHiveForImage,
            imageUrl: appState.previewUrl,
            dateAdded: new Date().toISOString(),
            notes: 'Naujai įkelta nuotrauka'
          };
          
          hivePhotos.push(newPhoto);
        }
      } else if (appState.selectedTaskStepForImage) {
        // Find the task step
        const taskStepIndex = userTaskStepsList.findIndex(
          step => step.id === appState.selectedTaskStepForImage
        );
        
        if (taskStepIndex !== -1) {
          // Update task step with image evidence
          userTaskStepsList[taskStepIndex] = {
            ...userTaskStepsList[taskStepIndex],
            evidenceImage: appState.previewUrl,
            evidenceDate: new Date().toISOString()
          };
        }
      }
      
      // Close dialog and reset state
      handleCloseImageDialog();
      
      // Show success message
      updateAppState({
        successMessages: {
          ...appState.successMessages,
          imageUpload: true
        }
      });
      
      // Hide success message after some time
      setTimeout(() => {
        updateAppState({
          successMessages: {
            ...appState.successMessages,
            imageUpload: false
          }
        });
      }, 3000);
    }, 1000);
  };
  
  // Mark notifications as read
  const markNotificationsAsRead = () => {
    // Update notifications for current user
    notificationsList.forEach(notification => {
      if (notification.userId === appState.currentUser.id && !notification.read) {
        notification.read = true;
      }
    });
    
    // Update unread count
    updateAppState({ unreadNotifications: 0 });
  };
  
  // Mark single notification as read
  const markNotificationAsRead = (notificationId) => {
    // Find notification
    const notificationIndex = notificationsList.findIndex(n => n.id === notificationId);
    
    if (notificationIndex !== -1) {
      // Update notification
      notificationsList[notificationIndex] = {
        ...notificationsList[notificationIndex],
        read: true
      };
      
      // Update unread count
      const unreadCount = notificationsList.filter(
        notification => notification.userId === appState.currentUser.id && !notification.read
      ).length;
      
      updateAppState({ unreadNotifications: unreadCount });
    }
  };
  
  // Handle notification click
  const handleNotificationClick = (notification) => {
    // Mark notification as read if it's not read yet
    if (!notification.read) {
      markNotificationAsRead(notification.id);
    }
    
    // Handle navigation based on notification type and related entity
    if (notification.type === 'task' && notification.relatedId) {
      updateAppState({
        currentView: 'taskDetails',
        selectedTask: notification.relatedId
      });
    } else if (notification.type === 'hive' && notification.relatedId) {
      updateAppState({
        currentView: 'hiveDetails',
        selectedHive: notification.relatedId
      });
    } else if (notification.type === 'message') {
      updateAppState({
        currentView: 'notifications'
      });
    }
  };
  
  // Add note to hive
  const addHiveNote = (hiveId, note) => {
    // Find hive
    const hiveIndex = hivesList.findIndex(h => h.id === hiveId);
    
    if (hiveIndex === -1) return false;
    
    // Update hive notes
    const currentNotes = hivesList[hiveIndex].notes || '';
    const formattedDate = new Date().toLocaleDateString('lt-LT');
    const newNote = `[${formattedDate}] ${note}\n\n`;
    
    hivesList[hiveIndex] = {
      ...hivesList[hiveIndex],
      notes: newNote + currentNotes
    };
    
    // Add to hive events
    const newEvent = {
      id: Date.now(),
      hiveId: hiveId,
      userId: appState.currentUser.id,
      type: 'note',
      date: new Date().toISOString(),
      details: note
    };
    
    hiveEvents.push(newEvent);
    
    // Reset note form
    setForms(prevForms => ({
      ...prevForms,
      hiveNote: {
        text: ''
      }
    }));
    
    return true;
  };
  
  // Get task steps for user
  const getTaskStepsForUser = (taskId) => {
    return userTaskStepsList.filter(step => step.taskId === taskId);
  };
  
  // Update task step status
  const updateTaskStepStatus = (stepId, status) => {
    // Find step
    const stepIndex = userTaskStepsList.findIndex(step => step.id === stepId);
    
    if (stepIndex === -1) return false;
    
    // Update step status
    userTaskStepsList[stepIndex] = {
      ...userTaskStepsList[stepIndex],
      status: status,
      completedDate: status === 'completed' ? new Date().toISOString() : null
    };
    
    // Check if all steps are completed
    const taskId = userTaskStepsList[stepIndex].taskId;
    const stepsForTask = userTaskStepsList.filter(step => step.taskId === taskId);
    const allCompleted = stepsForTask.every(step => step.status === 'completed');
    
    if (allCompleted) {
      // Update task status
      const taskIndex = tasksList.findIndex(t => t.id === taskId);
      
      if (taskIndex !== -1) {
        tasksList[taskIndex] = {
          ...tasksList[taskIndex],
          status: 'completed',
          completedDate: new Date().toISOString()
        };
        
        // Create notification for task completion
        const newNotification = {
          id: Date.now(),
          userId: tasksList[taskIndex].assignedBy,
          type: 'task_completed',
          title: 'Užduotis užbaigta',
          message: `Užduotis "${tasksList[taskIndex].title}" buvo užbaigta`,
          read: false,
          date: new Date().toISOString(),
          relatedId: taskId
        };
        
        notificationsList.push(newNotification);
      }
    }
    
    return true;
  };
  
  // Upload evidence for task step
  const uploadTaskStepEvidence = (stepId) => {
    updateAppState({
      showImageDialog: true,
      selectedTaskStepForImage: stepId
    });
  };
  
  // Add user (admin function)
  const addUser = () => {
    const { name, email, password, phone, address, role, groupId } = forms.userAdd;
    
    if (!name || !email || !password || !role) {
      return false;
    }
    
    // Create new user
    const newUser = {
      id: Date.now(),
      name,
      email,
      password,
      phone: phone || '',
      address: address || '',
      role,
      groupId: groupId || null,
      dateCreated: new Date().toISOString()
    };
    
    // Add to users
    usersList.push(newUser);
    
    // Reset form
    setForms(prevForms => ({
      ...prevForms,
      userAdd: {
        name: '',
        email: '',
        password: '',
        phone: '',
        address: '',
        role: 'user',
        groupId: ''
      }
    }));
    
    // Update state
    updateAppState({
      showUserForm: false,
      successMessages: {
        ...appState.successMessages,
        userAdd: true
      }
    });
    
    // Hide success message after some time
    setTimeout(() => {
      updateAppState({
        successMessages: {
          ...appState.successMessages,
          userAdd: false
        }
      });
    }, 3000);
    
    return true;
  };
  
  // Update user (admin function)
  const updateUser = (updatedUser) => {
    const userIndex = usersList.findIndex(user => user.id === updatedUser.id);
    if (userIndex !== -1) {
      usersList[userIndex] = updatedUser;
      setForms(prevForms => ({
        ...prevForms,
        userEdit: {
          name: updatedUser.name || '',
          email: updatedUser.email || '',
          password: updatedUser.password || '',
          phone: updatedUser.phone || '',
          address: updatedUser.address || '',
          role: updatedUser.role || 'user',
          groupId: updatedUser.groupId || null
        }
      }));
    }
  };
  
  // Delete user (admin function)
  const deleteUser = (userId) => {
    // Find user index
    const userIndex = usersList.findIndex(u => u.id === userId);
    
    if (userIndex === -1) return false;
    
    // Remove user
    usersList.splice(userIndex, 1);
    
    // Update state
    updateAppState({
      showConfirmDelete: false,
      itemToDelete: null,
      deleteType: null,
      successMessages: {
        ...appState.successMessages,
        userDelete: true
      }
    });
    
    // Hide success message after some time
    setTimeout(() => {
      updateAppState({
        successMessages: {
          ...appState.successMessages,
          userDelete: false
        }
      });
    }, 3000);
    
    return true;
  };
  
  // Assign task (admin function)
  const assignTask = () => {
    const { groupId, templateId, hiveId, startDate, dueDate, notes } = forms.taskAssign;
    
    if (!groupId || !templateId || !startDate || !dueDate) {
      return false;
    }
    
    // Get template
    const template = taskTemplatesList.find(t => t.id === templateId);
    
    if (!template) return false;
    
    // Create new task
    const newTask = {
      id: Date.now(),
      templateId,
      title: template.title,
      description: template.description,
      status: 'pending',
      priority: template.priority || 'medium',
      createdDate: new Date().toISOString(),
      startDate,
      dueDate,
      completedDate: null,
      groupId: groupId,
      assignedBy: appState.currentUser.id,
      hiveId: hiveId || null,
      notes: notes || ''
    };
    
    // Add to tasks
    tasksList.push(newTask);
    
    // Create steps for the task
    template.steps.forEach((step, index) => {
      const newStep = {
        id: Date.now() + index + 1,
        taskId: newTask.id,
        templateStepId: step.id,
        title: step.title,
        description: step.description,
        order: index + 1,
        status: 'pending',
        completedDate: null,
        evidenceRequired: step.evidenceRequired || false,
        evidenceImage: null,
        evidenceDate: null
      };
      
      userTaskStepsList.push(newStep);
    });
    
    // Get users in the group
    const usersInGroup = usersList.filter(user => user.groupId === groupId);
    
    // Create notification for each user in the group
    usersInGroup.forEach(user => {
      const newNotification = {
        id: Date.now() + Math.floor(Math.random() * 1000),
        userId: user.id,
        type: 'task_assigned',
        title: 'Nauja užduotis',
        message: `Jūsų grupei priskirta nauja užduotis: "${template.title}"`,
        read: false,
        date: new Date().toISOString(),
        relatedId: newTask.id
      };
      
      notificationsList.push(newNotification);
    });
    
    // Update state
    updateAppState({
      showTaskAssignForm: false,
      successMessages: {
        ...appState.successMessages,
        taskAssign: true
      }
    });
    
    // Reset form
    setForms(prevForms => ({
      ...prevForms,
      taskAssign: {
        groupId: '',
        templateId: '',
        hiveId: '',
        startDate: new Date().toISOString().split('T')[0],
        dueDate: new Date(Date.now() + 14 * 24 * 60 * 60 * 1000).toISOString().split('T')[0],
        notes: ''
      }
    }));
    
    // Hide success message after some time
    setTimeout(() => {
      updateAppState({
        successMessages: {
          ...appState.successMessages,
          taskAssign: false
        }
      });
    }, 3000);
    
    return true;
  };
  
  // Add task template (admin function)
  const addTaskTemplate = () => {
    const { title, description, priority, steps } = forms.taskTemplate;
    
    if (!title || !description || !steps || steps.length === 0) {
      return false;
    }
    
    // Create new template
    const newTemplate = {
      id: Date.now(),
      title,
      description,
      priority: priority || 'medium',
      createdDate: new Date().toISOString(),
      createdBy: appState.currentUser.id,
      steps: steps.map((step, index) => ({
        id: Date.now() + index + 1,
        title: step.title,
        description: step.description,
        order: index + 1,
        evidenceRequired: step.evidenceRequired || false
      }))
    };
    
    // Add to task templates
    taskTemplatesList.push(newTemplate);
    
    // Reset form
    setForms(prevForms => ({
      ...prevForms,
      taskTemplate: {
        title: '',
        description: '',
        priority: 'medium',
        steps: []
      }
    }));
    
    // Update state
    updateAppState({
      showTaskTemplateForm: false,
      successMessages: {
        ...appState.successMessages,
        taskTemplate: true
      }
    });
    
    // Hide success message after some time
    setTimeout(() => {
      updateAppState({
        successMessages: {
          ...appState.successMessages,
          taskTemplate: false
        }
      });
    }, 3000);
    
    return true;
  };
  
  // Add user group (admin function)
  const addUserGroup = () => {
    const { name, description } = forms.userGroup;
    
    if (!name) {
      return false;
    }
    
    // Create new group
    const newGroup = {
      id: Date.now(),
      name,
      description: description || '',
      createdDate: new Date().toISOString(),
      createdBy: appState.currentUser.id
    };
    
    // Add to user groups
    userGroupsList.push(newGroup);
    
    // Reset form
    setForms(prevForms => ({
      ...prevForms,
      userGroup: {
        name: '',
        description: ''
      }
    }));
    
    // Update state
    updateAppState({
      showUserGroupForm: false,
      successMessages: {
        ...appState.successMessages,
        userGroup: true
      }
    });
    
    // Hide success message after some time
    setTimeout(() => {
      updateAppState({
        successMessages: {
          ...appState.successMessages,
          userGroup: false
        }
      });
    }, 3000);
    
    return true;
  };
  
  // Update hive details
  const updateHive = (hiveId) => {
    const hiveIndex = hivesList.findIndex(h => h.id === hiveId);
    
    if (hiveIndex === -1) return false;

    // Update hive with form data
    hivesList[hiveIndex] = {
      ...hivesList[hiveIndex],
      name: forms.hiveEdit.name,
      location: forms.hiveEdit.location,
      notes: forms.hiveEdit.notes
    };

    // Update state
    updateAppState({
      showHiveEdit: false,
      successMessages: {
        ...appState.successMessages,
        hiveUpdate: true
      }
    });

    // Reset form
    setForms(prevForms => ({
      ...prevForms,
      hiveEdit: {
        name: '',
        location: '',
        notes: ''
      }
    }));

    // Hide success message after some time
    setTimeout(() => {
      updateAppState({
        successMessages: {
          ...appState.successMessages,
          hiveUpdate: false
        }
      });
    }, 3000);

    return true;
  };
  
  // Initialize hive edit form
  const initHiveEditForm = (hiveId) => {
    const hive = hivesList.find(h => h.id === hiveId);
    
    if (!hive) return;

    setForms(prevForms => ({
      ...prevForms,
      hiveEdit: {
        name: hive.name || '',
        location: hive.location || '',
        notes: hive.notes || ''
      }
    }));

    updateAppState({
      showHiveEdit: true,
      editingHiveId: hiveId
    });
  };
  
  // Save profile
  const saveProfile = () => {
    updateProfile();
  };
  
  // Update user group
  const updateUserGroup = (groupId) => {
    const formData = forms.userGroup;
    const groupIndex = userGroupsList.findIndex(g => g.id === groupId);
    
    if (groupIndex === -1) {
      console.error('Grupė nerasta');
      return;
    }
    
    // Update group
    userGroupsList[groupIndex] = {
      ...userGroupsList[groupIndex],
      name: formData.name,
      description: formData.description || '',
      lastModified: new Date().toISOString(),
      modifiedBy: appState.currentUser.id
    };
    
    // Show success notification
    const newNotification = {
      id: uuidv4(),
      userId: appState.currentUser.id,
      type: 'activity',
      title: 'Grupė atnaujinta',
      message: `Grupė "${formData.name}" buvo sėkmingai atnaujinta.`,
      read: false,
      date: new Date().toISOString(),
      relatedId: groupId
    };
    
    notificationsList.push(newNotification);
    
    updateAppState({
      successMessage: 'Grupė sėkmingai atnaujinta!'
    });
    
    // Hide success message after some time
    setTimeout(() => {
      updateAppState({
        successMessage: null
      });
    }, 3000);
  };
  
  // Delete user group
  const deleteUserGroup = (groupId) => {
    // Check if users are assigned to this group
    const usersInGroup = usersList.some(user => user.groupId === groupId);
    
    if (usersInGroup) {
      updateAppState({
        errorMessage: 'Negalima ištrinti grupės, nes jai priskirti vartotojai.'
      });
      
      setTimeout(() => {
        updateAppState({
          errorMessage: null
        });
      }, 3000);
      
      return;
    }
    
    // Find group
    const groupIndex = userGroupsList.findIndex(g => g.id === groupId);
    const groupName = userGroupsList[groupIndex]?.name || 'Grupė';
    
    if (groupIndex !== -1) {
      // Remove group
      userGroupsList.splice(groupIndex, 1);
      
      // Add notification
      const newNotification = {
        id: uuidv4(),
        userId: appState.currentUser.id,
        type: 'activity',
        title: 'Grupė ištrinta',
        message: `Grupė "${groupName}" buvo sėkmingai ištrinta.`,
        read: false,
        date: new Date().toISOString()
      };
      
      notificationsList.push(newNotification);
      
      updateAppState({
        successMessage: 'Grupė sėkmingai ištrinta!'
      });
      
      // Hide success message after some time
      setTimeout(() => {
        updateAppState({
          successMessage: null
        });
      }, 3000);
    }
  };
  
  // Žingsnių funkcijos
  const addStep = () => {
    const newStep = {
      id: Date.now().toString(),
      title: forms.step.title,
      description: forms.step.description,
      evidenceRequired: forms.step.evidenceRequired,
      hasMedia: forms.step.hasMedia,
      mediaType: forms.step.mediaType,
      mediaUrl: forms.step.mediaUrl,
      createdDate: new Date().toISOString(),
      createdBy: appState.currentUser.id
    };
    
    setSteps([...steps, newStep]);
    
    // Rodyti sėkmės pranešimą
    updateAppState({
      successMessages: {
        ...appState.successMessages,
        stepAdded: true
      }
    });
    
    // Paslėpti sėkmės pranešimą po 3 sekundžių
    setTimeout(() => {
      updateAppState({
        successMessages: {
          ...appState.successMessages,
          stepAdded: false
        }
      });
    }, 3000);
  };

  const updateStep = (stepId) => {
    const stepIndex = steps.findIndex(step => step.id === stepId);
    if (stepIndex !== -1) {
      const updatedStep = {
        ...steps[stepIndex],
        title: forms.step.title,
        description: forms.step.description,
        evidenceRequired: forms.step.evidenceRequired,
        hasMedia: forms.step.hasMedia,
        mediaType: forms.step.mediaType,
        mediaUrl: forms.step.mediaUrl,
        lastModified: new Date().toISOString()
      };
      
      steps[stepIndex] = updatedStep;
      setSteps([...steps]);
      
      // Rodyti sėkmės pranešimą
      updateAppState({
        successMessages: {
          ...appState.successMessages,
          stepUpdated: true
        }
      });
      
      // Paslėpti sėkmės pranešimą po 3 sekundžių
      setTimeout(() => {
        updateAppState({
          successMessages: {
            ...appState.successMessages,
            stepUpdated: false
          }
        });
      }, 3000);
    }
  };

  const deleteStep = (stepId) => {
    // Patikriname, ar žingsnis nėra naudojamas šablonuose
    const isUsedInTemplate = templates.some(template => 
      template.steps.some(step => step.id === stepId)
    );
    
    if (isUsedInTemplate) {
      alert('Negalima ištrinti žingsnio, nes jis naudojamas šablone.');
      return;
    }
    
    setSteps(steps.filter(step => step.id !== stepId));
    
    // Rodyti sėkmės pranešimą
    updateAppState({
      successMessages: {
        ...appState.successMessages,
        stepDeleted: true
      }
    });
    
    // Paslėpti sėkmės pranešimą po 3 sekundžių
    setTimeout(() => {
      updateAppState({
        successMessages: {
          ...appState.successMessages,
          stepDeleted: false
        }
      });
    }, 3000);
  };

  // Šablonų funkcijos
  const addTemplate = () => {
    console.log("Adding template with form data:", forms.template);
    
    const { title, description, category, steps } = forms.template;
    
    if (!title) {
      console.error("Template title is required");
      return false;
    }
    
    // Ensure steps is an array
    const templateSteps = Array.isArray(steps) ? steps : [];
    
    console.log("Template steps being added:", templateSteps);
    
    // Create new template
    const newTemplate = {
      id: Date.now().toString(),
      title,
      description: description || '',
      category: category || 'general',
      steps: templateSteps.map((step, index) => ({
        ...step,
        order: index
      })),
      createdDate: new Date().toISOString(),
      createdBy: appState.currentUser.id
    };
    
    console.log("New template object:", newTemplate);
    
    // Atnaujinti templates masyvą
    const updatedTemplates = [...templates, newTemplate];
    setTemplates(updatedTemplates);
    
    // Taip pat atnaujinti appState, kad šablonas būtų pasiekiamas visoje aplikacijoje
    updateAppState({
      templates: updatedTemplates
    });
    
    // Pridėti šabloną į taskTemplates masyvą, jei jis egzistuoja
    if (taskTemplatesList) {
      taskTemplatesList.push(newTemplate);
      updateAppState({
        taskTemplates: [...taskTemplatesList]
      });
    }
    
    // Show success message
    updateAppState({
      successMessages: {
        ...appState.successMessages,
        templateAdded: true
      }
    });
    
    // Hide success message after 3 seconds
    setTimeout(() => {
      updateAppState({
        successMessages: {
          ...appState.successMessages,
          templateAdded: false
        }
      });
    }, 3000);
    
    return true;
  };

  const updateTemplate = (templateId) => {
    const templateIndex = templates.findIndex(template => template.id === templateId);
    if (templateIndex !== -1) {
      const updatedTemplate = {
        ...templates[templateIndex],
        title: forms.template.title,
        description: forms.template.description,
        category: forms.template.category,
        steps: forms.template.steps,
        lastModified: new Date().toISOString()
      };
      
      templates[templateIndex] = updatedTemplate;
      setTemplates([...templates]);
      
      // Rodyti sėkmės pranešimą
      updateAppState({
        successMessages: {
          ...appState.successMessages,
          templateUpdated: true
        }
      });
      
      // Paslėpti sėkmės pranešimą po 3 sekundžių
      setTimeout(() => {
        updateAppState({
          successMessages: {
            ...appState.successMessages,
            templateUpdated: false
          }
        });
      }, 3000);
    }
  };

  const deleteTemplate = (templateId) => {
    // Patikriname, ar šablonas nėra naudojamas užduotyse
    const isUsedInTask = tasksList.some(task => task.templateId === templateId);
    
    if (isUsedInTask) {
      alert('Negalima ištrinti šablono, nes jis naudojamas užduotyje.');
      return;
    }
    
    setTemplates(templates.filter(template => template.id !== templateId));
    
    // Rodyti sėkmės pranešimą
    updateAppState({
      successMessages: {
        ...appState.successMessages,
        templateDeleted: true
      }
    });
    
    // Paslėpti sėkmės pranešimą po 3 sekundžių
    setTimeout(() => {
      updateAppState({
        successMessages: {
          ...appState.successMessages,
          templateDeleted: false
        }
      });
    }, 3000);
  };
  
  // Žingsnių valdymas šablone
  const addStepToTemplate = (templateId, stepId) => {
    const templateIndex = templates.findIndex(template => template.id === templateId);
    const step = steps.find(step => step.id === stepId);
    
    if (templateIndex !== -1 && step) {
      const updatedTemplate = {
        ...templates[templateIndex],
        steps: [...templates[templateIndex].steps, {
          ...step,
          order: templates[templateIndex].steps.length
        }]
      };
      
      templates[templateIndex] = updatedTemplate;
      setTemplates([...templates]);
    }
  };
  
  const removeStepFromTemplate = (templateId, stepId) => {
    const templateIndex = templates.findIndex(template => template.id === templateId);
    
    if (templateIndex !== -1) {
      const updatedTemplate = {
        ...templates[templateIndex],
        steps: templates[templateIndex].steps.filter(step => step.id !== stepId)
      };
      
      templates[templateIndex] = updatedTemplate;
      setTemplates([...templates]);
    }
  };
  
  const moveStepUp = (templateId, stepId) => {
    const templateIndex = templates.findIndex(template => template.id === templateId);
    
    if (templateIndex !== -1) {
      const stepIndex = templates[templateIndex].steps.findIndex(step => step.id === stepId);
      
      if (stepIndex > 0) {
        const updatedSteps = [...templates[templateIndex].steps];
        [updatedSteps[stepIndex], updatedSteps[stepIndex - 1]] = [updatedSteps[stepIndex - 1], updatedSteps[stepIndex]];
        
        const updatedTemplate = {
          ...templates[templateIndex],
          steps: updatedSteps.map((step, index) => ({
            ...step,
            order: index
          }))
        };
        
        templates[templateIndex] = updatedTemplate;
        setTemplates([...templates]);
      }
    }
  };
  
  const moveStepDown = (templateId, stepId) => {
    const templateIndex = templates.findIndex(template => template.id === templateId);
    
    if (templateIndex !== -1) {
      const stepIndex = templates[templateIndex].steps.findIndex(step => step.id === stepId);
      
      if (stepIndex < templates[templateIndex].steps.length - 1) {
        const updatedSteps = [...templates[templateIndex].steps];
        [updatedSteps[stepIndex], updatedSteps[stepIndex + 1]] = [updatedSteps[stepIndex + 1], updatedSteps[stepIndex]];
        
        const updatedTemplate = {
          ...templates[templateIndex],
          steps: updatedSteps.map((step, index) => ({
            ...step,
            order: index
          }))
        };
        
        templates[templateIndex] = updatedTemplate;
        setTemplates([...templates]);
      }
    }
  };
  
  // Get user's tasks 
  const getUserTasks = (userId) => {
    // Gauti užduotis, kurios yra priskirtos konkrečiam vartotojui arba jo grupei
    const user = usersList.find(u => u.id === userId);
    if (!user) return [];
    
    return tasksList.filter(task => 
      task.assignedUserId === userId || 
      (user.groupId && task.groupId === user.groupId)
    );
  };
  
  // Siunčiame naują žinutę
  const sendMessage = (chatId, text, attachment = null) => {
    const newId = Date.now();
    const newMessage = {
      id: newId,
      chatId: chatId,
      senderId: appState.currentUser.id,
      text: text,
      timestamp: new Date().toISOString(),
      read: false,
      attachment: attachment
    };

    const updatedMessages = [...appState.messages, newMessage];

    updateAppState({
      messages: updatedMessages
    });
    
    return newId;
  };
  
  // Sukuriame naują pokalbį
  const createChat = (type, name, participants) => {
    const newChatId = Date.now();
    const newChat = {
      id: newChatId,
      type: type,
      name: name,
      participants: participants,
      createdAt: new Date().toISOString()
    };

    const updatedChats = [...appState.chats, newChat];
    
    updateAppState({
      chats: updatedChats
    });
    
    return newChatId;
  };
  
  // Pažymime žinutę kaip perskaitytą
  const markMessageAsRead = (messageId) => {
    const updatedMessages = appState.messages.map(message => {
      if (message.id === messageId) {
        return { ...message, read: true };
      }
      return message;
    });
    
    updateAppState({
      messages: updatedMessages
    });
  };
  
  // Complete task step function (used in TaskDetails)
  const completeTaskStep = (stepId, completed = true) => {
    console.log(`Completing task step ${stepId}, completed: ${completed}`);
    
    // Find the step in userTaskSteps array
    const stepIndex = userTaskStepsList.findIndex(step => step.id === stepId);
    
    if (stepIndex === -1) {
      console.error(`Task step with ID ${stepId} not found`);
      return false;
    }
    
    // Update the step status
    const newStatus = completed ? 'completed' : 'pending';
    
    userTaskStepsList[stepIndex] = {
      ...userTaskStepsList[stepIndex],
      status: newStatus,
      completedDate: completed ? new Date().toISOString() : null
    };
    
    console.log(`Updated step: ${JSON.stringify(userTaskStepsList[stepIndex])}`);
    
    // Check if all steps for this task are completed
    const taskId = userTaskStepsList[stepIndex].taskId;
    const taskSteps = userTaskStepsList.filter(step => step.taskId === taskId);
    const allCompleted = taskSteps.every(step => step.status === 'completed');
    
    // If all steps are completed, mark the task as completed
    if (allCompleted) {
      const taskIndex = tasksList.findIndex(task => task.id === taskId);
      
      if (taskIndex !== -1) {
        tasksList[taskIndex] = {
          ...tasksList[taskIndex],
          status: 'completed',
          completedDate: new Date().toISOString()
        };
        
        console.log(`All steps completed. Task ${taskId} marked as completed.`);
        
        // Create notification for task completion
        const newNotification = {
          id: Date.now(),
          userId: tasksList[taskIndex].assignedBy,
          type: 'task_completed',
          title: 'Užduotis užbaigta',
          message: `Užduotis "${tasksList[taskIndex].title}" buvo užbaigta.`,
          read: false,
          date: new Date().toISOString(),
          relatedId: taskId
        };
        
        notificationsList.push(newNotification);
      }
    }
    
    // Force UI update
    updateAppState({
      userTaskSteps: [...userTaskStepsList]
    });
    
    return true;
  };

  // Add task comment function for TaskDetails
  const addTaskComment = (taskId, commentText) => {
    if (!commentText.trim()) {
      return false;
    }
    
    // Create a new comment
    const newComment = {
      id: Date.now(),
      taskId,
      userId: appState.currentUser.id,
      text: commentText,
      date: new Date().toISOString()
    };
    
    // Add the comment to task comments array (create if it doesn't exist)
    if (!appState.taskComments) {
      updateAppState({
        taskComments: [newComment]
      });
    } else {
      updateAppState({
        taskComments: [...appState.taskComments, newComment]
      });
    }
    
    return true;
  };
  
  // Update task status function for TaskDetails
  const updateTaskStatus = (taskId, newStatus) => {
    const taskIndex = tasksList.findIndex(task => task.id === taskId);
    
    if (taskIndex === -1) {
      return false;
    }
    
    tasksList[taskIndex] = {
      ...tasksList[taskIndex],
      status: newStatus,
      completedDate: newStatus === 'completed' ? new Date().toISOString() : null
    };
    
    // Force UI update
    updateAppState({
      tasks: [...tasksList]
    });
    
    return true;
  };
  
  // Delete task function for TaskDetails
  const deleteTask = (taskId) => {
    // Remove the task
    const updatedTasks = tasksList.filter(task => task.id !== taskId);
    
    // Remove associated task steps
    const updatedSteps = userTaskStepsList.filter(step => step.taskId !== taskId);
    
    // Remove associated comments if they exist
    const updatedComments = appState.taskComments 
      ? appState.taskComments.filter(comment => comment.taskId !== taskId)
      : [];
    
    // Update the state
    updateAppState({
      tasks: updatedTasks,
      userTaskSteps: updatedSteps,
      taskComments: updatedComments,
      currentView: 'tasks', // Navigate back to tasks list
      viewParams: {}
    });
    
    return true;
  };
  
  // Add task (admin function)
  const addTask = () => {
    console.log("Adding new task with form data:", forms.task);
    
    const { title, description, hiveId, groupId, templateId, priority, startDate, dueDate, status } = forms.task;
    
    if (!title || !groupId) {
      console.error("Task title and group are required");
      return false;
    }

    // Gauti šabloną, jei nurodyta
    let templateSteps = [];
    let templateTitle = title;
    let templateDescription = description || '';

    if (templateId) {
      const template = taskTemplatesList.find(t => t.id === templateId);
      if (template) {
        templateTitle = title || template.title;
        templateDescription = description || template.description;
        templateSteps = template.steps || [];
      }
    }
    
    // Sukurti naują užduotį
    const newTask = {
      id: Date.now(),
      templateId: templateId || null,
      title: templateTitle,
      description: templateDescription,
      status: status || 'pending',
      priority: priority || 'medium',
      createdDate: new Date().toISOString(),
      startDate: startDate || new Date().toISOString(),
      dueDate: dueDate || new Date(Date.now() + 14 * 24 * 60 * 60 * 1000).toISOString(),
      completedDate: null,
      groupId,
      assignedBy: appState.currentUser.id,
      hiveId: hiveId || null
    };
    
    console.log("Created new task:", newTask);
    
    // Pridėti į užduočių sąrašą
    const updatedTasks = [...tasksList, newTask];
    // Išsaugoti pakeitimus masyvuose
    tasksList.push(newTask);
    
    // Sukurti žingsnius, jei yra šablonas
    if (templateSteps.length > 0) {
      templateSteps.forEach((step, index) => {
        const newStep = {
          id: Date.now() + index + 1,
          taskId: newTask.id,
          templateStepId: step.id,
          title: step.title,
          description: step.description,
          order: index + 1,
          status: 'pending',
          completedDate: null,
          evidenceRequired: step.evidenceRequired || false,
          evidenceImage: null,
          evidenceDate: null
        };
        
        userTaskStepsList.push(newStep);
      });
    }
    
    // Gauti vartotojus grupėje
    const usersInGroup = usersList.filter(user => user.groupId === groupId);
    
    // Sukurti pranešimus kiekvienam grupės vartotojui
    usersInGroup.forEach(user => {
      const newNotification = {
        id: Date.now() + Math.floor(Math.random() * 1000),
        userId: user.id,
        type: 'task_assigned',
        title: 'Nauja užduotis',
        message: `Jūsų grupei priskirta nauja užduotis: "${newTask.title}"`,
        read: false,
        date: new Date().toISOString(),
        relatedId: newTask.id
      };
      
      notificationsList.push(newNotification);
    });
    
    // Atnaujinti būseną
    updateAppState({
      tasks: updatedTasks,
      successMessages: {
        ...appState.successMessages,
        taskAdd: true
      }
    });
    
    // Išvalyti formą
    setForms(prevForms => ({
      ...prevForms,
      task: {
        title: '',
        description: '',
        hiveId: '',
        groupId: '',
        templateId: '',
        priority: 'medium',
        startDate: new Date().toISOString().split('T')[0],
        dueDate: '',
        status: 'pending'
      }
    }));
    
    // Paslėpti sėkmės pranešimą po kelių sekundžių
    setTimeout(() => {
      updateAppState({
        successMessages: {
          ...appState.successMessages,
          taskAdd: false
        }
      });
    }, 3000);
    
    return true;
  };
  
  // Provide the context value
  const contextValue = {
    appState,
    updateAppState,
    forms,
    updateForm,
    login,
    logout,
    handleLogout,
    updateProfile,
    changePassword,
    addHive,
    updateHive,
    initHiveEditForm,
    saveHiveName,
    saveHiveLocation,
    changeHiveImage,
    handleCloseImageDialog,
    handleFileSelect,
    handleImageUpload,
    markNotificationsAsRead,
    markNotificationAsRead,
    handleNotificationClick,
    addHiveNote,
    getTaskStepsForUser,
    updateTaskStepStatus,
    uploadTaskStepEvidence,
    addUser,
    updateUser,
    deleteUser,
    assignTask,
    addTaskTemplate,
    addUserGroup,
    updateUserGroup,
    deleteUserGroup,
    saveProfile,
    editing,
    setEditing,
    // Naujos funkcijos
    addStep,
    updateStep,
    deleteStep,
    addTemplate,
    updateTemplate,
    deleteTemplate,
    addStepToTemplate,
    removeStepFromTemplate,
    moveStepUp,
    moveStepDown,
    addTask,
    // Expose data
    users: usersList,
    userGroups: userGroupsList,
    hives: hivesList,
    tasks: tasksList,
    taskTemplates: taskTemplatesList,
    userTaskSteps: userTaskStepsList,
    notifications: notificationsList,
    steps,
    templates,
    getUserTasks,
    sendMessage,
    createChat,
    markMessageAsRead,
    completeTaskStep,
    addTaskComment,
    updateTaskStatus,
    deleteTask
  };
  
  return (
    <AppContext.Provider value={contextValue}>
      {children}
    </AppContext.Provider>
  );
}; 