import React, { useState, useRef } from 'react';
import { Plus, Edit, Trash, Search, X, Save, Check, Camera, Video, FileText, Image } from 'lucide-react';
import { useApp } from '../../context/AppContext';

const AdminSteps = () => {
  const { 
    appState, 
    updateAppState, 
    forms,
    updateForm,
    addStep,
    updateStep,
    deleteStep,
    steps = [],
    handleFileSelect,
    handleImageUpload
  } = useApp();
  
  const [showForm, setShowForm] = useState(false);
  const [editingStepId, setEditingStepId] = useState(null);
  const [searchTerm, setSearchTerm] = useState('');
  const [confirmDelete, setConfirmDelete] = useState(null);
  const fileInputRef = useRef(null);
  
  // Form handlers
  const resetForm = () => {
    updateForm('step', 'title', '');
    updateForm('step', 'description', '');
    updateForm('step', 'evidenceRequired', false);
    updateForm('step', 'hasMedia', false);
    updateForm('step', 'mediaType', '');
    updateForm('step', 'mediaUrl', '');
  };
  
  const handleSubmit = (e) => {
    e.preventDefault();
    
    const formData = forms.step;
    
    // Validacija
    if (!formData.title) {
      alert('Prašome įvesti žingsnio pavadinimą');
      return;
    }
    
    if (editingStepId) {
      // Redagavimas
      updateStep(editingStepId);
      setEditingStepId(null);
    } else {
      // Naujo žingsnio pridėjimas
      addStep();
    }
    
    setShowForm(false);
    resetForm();
  };
  
  const editStep = (step) => {
    setEditingStepId(step.id);
    updateForm('step', 'title', step.title);
    updateForm('step', 'description', step.description || '');
    updateForm('step', 'evidenceRequired', step.evidenceRequired || false);
    updateForm('step', 'hasMedia', step.hasMedia || false);
    updateForm('step', 'mediaType', step.mediaType || '');
    updateForm('step', 'mediaUrl', step.mediaUrl || '');
    setShowForm(true);
  };
  
  const confirmDeleteStep = (stepId) => {
    if (confirmDelete === stepId) {
      // Žingsnio šalinimas
      deleteStep(stepId);
      setConfirmDelete(null);
    } else {
      setConfirmDelete(stepId);
    }
  };
  
  // Media upload handling
  const handleMediaSelect = (type) => {
    updateForm('step', 'mediaType', type);
    updateForm('step', 'hasMedia', true);
    if (type === 'image' || type === 'video') {
      fileInputRef.current.click();
    }
  };
  
  const handleMediaUpload = (e) => {
    handleFileSelect(e);
    // Po failo pasirinkimo, media atsirastų formData.mediaUrl lauke
  };
  
  // Filtruojame pagal paiešką
  const filteredSteps = steps.filter(step => 
    step.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
    (step.description && step.description.toLowerCase().includes(searchTerm.toLowerCase()))
  );
  
  // Gauname žingsnio tipo ikoną
  const getStepIcon = (step) => {
    if (step.evidenceRequired) {
      return <Camera size={18} className="text-blue-500" />;
    } else if (step.hasMedia) {
      if (step.mediaType === 'video') {
        return <Video size={18} className="text-purple-500" />;
      } else if (step.mediaType === 'image') {
        return <Image size={18} className="text-green-500" />;
      }
    }
    return <Check size={18} className="text-amber-600" />;
  };
  
  return (
    <div className="p-6">
      <div className="flex justify-between items-center mb-6">
        <h2 className="text-2xl font-bold">Žingsnių administravimas</h2>
        <button 
          onClick={() => {
            resetForm();
            setEditingStepId(null);
            setShowForm(true);
          }}
          className="bg-amber-600 text-white px-4 py-2 rounded-md hover:bg-amber-700 flex items-center"
        >
          <Plus size={16} className="mr-2" />
          Pridėti žingsnį
        </button>
      </div>
      
      {/* Paieškos laukas */}
      <div className="mb-6 relative">
        <input 
          type="text"
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
          placeholder="Ieškoti žingsnių..."
          className="w-full p-3 pl-10 pr-10 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-amber-500"
        />
        <Search size={20} className="absolute left-3 top-3.5 text-gray-400" />
        {searchTerm && (
          <button 
            onClick={() => setSearchTerm('')}
            className="absolute right-3 top-3.5 text-gray-400 hover:text-gray-600"
          >
            <X size={20} />
          </button>
        )}
      </div>
      
      {/* Žingsnių forma */}
      {showForm && (
        <div className="bg-white p-6 rounded-lg shadow-md mb-6">
          <h3 className="text-lg font-semibold mb-4">{editingStepId ? 'Redaguoti žingsnį' : 'Pridėti naują žingsnį'}</h3>
          
          <form onSubmit={handleSubmit} className="space-y-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">Pavadinimas *</label>
              <input 
                type="text"
                value={forms.step?.title || ''}
                onChange={(e) => updateForm('step', 'title', e.target.value)}
                className="w-full p-2 border border-gray-300 rounded-md"
                placeholder="Žingsnio pavadinimas"
                required
              />
            </div>
            
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">Aprašymas</label>
              <textarea 
                value={forms.step?.description || ''}
                onChange={(e) => updateForm('step', 'description', e.target.value)}
                className="w-full p-2 border border-gray-300 rounded-md"
                rows={3}
                placeholder="Išsamus žingsnio aprašymas"
              ></textarea>
            </div>
            
            <div>
              <div className="flex items-center mb-2">
                <input 
                  type="checkbox"
                  id="evidenceRequired"
                  checked={forms.step?.evidenceRequired || false}
                  onChange={(e) => updateForm('step', 'evidenceRequired', e.target.checked)}
                  className="h-4 w-4 text-amber-600 focus:ring-amber-500 border-gray-300 rounded"
                />
                <label htmlFor="evidenceRequired" className="ml-2 block text-sm text-gray-700">
                  Reikalingi įrodymai (nuotrauka)
                </label>
              </div>
              <p className="text-xs text-gray-500">
                Įgalinus šią funkciją, vartotojai turės įkelti nuotrauką kaip įrodymą, kad žingsnis atliktas
              </p>
            </div>
            
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">Medijos tipas</label>
              <div className="flex space-x-2">
                <button 
                  type="button"
                  onClick={() => updateForm('step', 'hasMedia', false)}
                  className={`px-3 py-2 rounded-md ${!forms.step?.hasMedia ? 'bg-amber-100 text-amber-800 font-medium' : 'bg-gray-100 text-gray-700'}`}
                >
                  Nėra
                </button>
                <button 
                  type="button"
                  onClick={() => handleMediaSelect('image')}
                  className={`px-3 py-2 rounded-md flex items-center ${forms.step?.hasMedia && forms.step?.mediaType === 'image' ? 'bg-amber-100 text-amber-800 font-medium' : 'bg-gray-100 text-gray-700'}`}
                >
                  <Image size={16} className="mr-1" />
                  Nuotrauka
                </button>
                <button 
                  type="button"
                  onClick={() => handleMediaSelect('video')}
                  className={`px-3 py-2 rounded-md flex items-center ${forms.step?.hasMedia && forms.step?.mediaType === 'video' ? 'bg-amber-100 text-amber-800 font-medium' : 'bg-gray-100 text-gray-700'}`}
                >
                  <Video size={16} className="mr-1" />
                  Vaizdo įrašas
                </button>
                <button 
                  type="button"
                  onClick={() => handleMediaSelect('document')}
                  className={`px-3 py-2 rounded-md flex items-center ${forms.step?.hasMedia && forms.step?.mediaType === 'document' ? 'bg-amber-100 text-amber-800 font-medium' : 'bg-gray-100 text-gray-700'}`}
                >
                  <FileText size={16} className="mr-1" />
                  Dokumentas
                </button>
              </div>
              
              {forms.step?.hasMedia && (
                <div className="mt-3">
                  {forms.step?.mediaType === 'image' || forms.step?.mediaType === 'video' ? (
                    <div>
                      <input 
                        type="file" 
                        ref={fileInputRef}
                        style={{ display: 'none' }}
                        onChange={handleMediaUpload}
                        accept={forms.step?.mediaType === 'image' ? 'image/*' : 'video/*'}
                      />
                      <button
                        type="button"
                        onClick={() => fileInputRef.current.click()}
                        className="mt-2 px-3 py-2 border border-gray-300 rounded-md text-gray-700 hover:bg-gray-50 flex items-center"
                      >
                        <Plus size={16} className="mr-1" />
                        Įkelti {forms.step?.mediaType === 'image' ? 'nuotrauką' : 'vaizdo įrašą'}
                      </button>
                    </div>
                  ) : forms.step?.mediaType === 'document' && (
                    <div className="mt-2">
                      <label className="block text-sm font-medium text-gray-700 mb-1">Dokumento nuoroda</label>
                      <input 
                        type="text"
                        value={forms.step?.mediaUrl || ''}
                        onChange={(e) => updateForm('step', 'mediaUrl', e.target.value)}
                        className="w-full p-2 border border-gray-300 rounded-md"
                        placeholder="https://example.com/document.pdf"
                      />
                    </div>
                  )}
                </div>
              )}
            </div>
            
            <div className="flex justify-end space-x-3 mt-6">
              <button 
                type="button"
                onClick={() => {
                  setShowForm(false);
                  resetForm();
                }}
                className="px-4 py-2 border border-gray-300 rounded-md text-gray-700 hover:bg-gray-50"
              >
                Atšaukti
              </button>
              <button 
                type="submit"
                className="px-4 py-2 bg-amber-600 text-white rounded-md hover:bg-amber-700 flex items-center"
              >
                <Save size={16} className="mr-2" />
                {editingStepId ? 'Atnaujinti' : 'Pridėti'}
              </button>
            </div>
          </form>
        </div>
      )}
      
      {/* Žingsnių sąrašas */}
      <div className="bg-white rounded-lg shadow-md overflow-hidden">
        <table className="min-w-full divide-y divide-gray-200">
          <thead className="bg-gray-50">
            <tr>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Žingsnis</th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Aprašymas</th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Tipas</th>
              <th className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider">Veiksmai</th>
            </tr>
          </thead>
          <tbody className="bg-white divide-y divide-gray-200">
            {filteredSteps.length > 0 ?
              filteredSteps.map(step => (
                <tr key={step.id} className="hover:bg-gray-50">
                  <td className="px-6 py-4 whitespace-nowrap">
                    <div className="flex items-center">
                      <div className="flex-shrink-0 h-10 w-10 bg-amber-100 rounded-full flex items-center justify-center">
                        {getStepIcon(step)}
                      </div>
                      <div className="ml-4">
                        <div className="text-sm font-medium text-gray-900">{step.title}</div>
                        <div className="text-sm text-gray-500">ID: {step.id}</div>
                      </div>
                    </div>
                  </td>
                  <td className="px-6 py-4">
                    <div className="text-sm text-gray-900 line-clamp-2">{step.description || '-'}</div>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    {step.evidenceRequired ? (
                      <span className="px-2 py-1 inline-flex text-xs leading-5 font-semibold rounded-full bg-blue-100 text-blue-800">
                        Reikalingi įrodymai
                      </span>
                    ) : step.hasMedia ? (
                      <span className="px-2 py-1 inline-flex text-xs leading-5 font-semibold rounded-full bg-green-100 text-green-800">
                        {step.mediaType === 'image' ? 'Nuotrauka' : 
                         step.mediaType === 'video' ? 'Vaizdo įrašas' : 'Dokumentas'}
                      </span>
                    ) : (
                      <span className="px-2 py-1 inline-flex text-xs leading-5 font-semibold rounded-full bg-gray-100 text-gray-800">
                        Standartinis
                      </span>
                    )}
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap text-right text-sm font-medium">
                    <button 
                      onClick={() => editStep(step)}
                      className="text-amber-600 hover:text-amber-900 mx-2"
                      title="Redaguoti žingsnį"
                    >
                      <Edit size={18} />
                    </button>
                    <button 
                      onClick={() => confirmDeleteStep(step.id)}
                      className={`text-red-600 hover:text-red-900 mx-2 ${
                        confirmDelete === step.id ? 'animate-pulse' : ''
                      }`}
                      title={confirmDelete === step.id ? 'Patvirtinti šalinimą' : 'Šalinti žingsnį'}
                    >
                      <Trash size={18} />
                    </button>
                  </td>
                </tr>
              )) : (
                <tr>
                  <td colSpan={4} className="px-6 py-4 text-center text-gray-500">
                    {searchTerm ? 'Nerasta žingsnių pagal paiešką' : 'Nėra sukurtų žingsnių'}
                  </td>
                </tr>
              )
            }
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default AdminSteps; 