import React, { useState, useEffect, useRef } from 'react';
import { 
  Search, 
  User, 
  Users, 
  Send, 
  Plus, 
  X, 
  Check,
  ChevronLeft,
  Info,
  MoreVertical,
  Paperclip,
  Image,
  FileText,
  Video
} from 'lucide-react';
import { useApp } from '../../context/AppContext';

const MessagesView = () => {
  const { appState, updateAppState, sendMessage, markMessageAsRead, createChat } = useApp();
  const [activeChat, setActiveChat] = useState(null);
  const [message, setMessage] = useState('');
  const [searchTerm, setSearchTerm] = useState('');
  const [showNewChat, setShowNewChat] = useState(false);
  const [selectedUsers, setSelectedUsers] = useState([]);
  const [attachment, setAttachment] = useState(null);
  const [showAttachmentOptions, setShowAttachmentOptions] = useState(false);
  
  const messagesEndRef = useRef(null);
  const chatListRef = useRef(null);
  
  const currentUser = appState.currentUser;
  
  // Pirma apibrėžiame funkcijas, kad išvengtume 'Cannot access before initialization' klaidos
  
  // Get chat name (other user's name or group name)
  const getChatName = (chat) => {
    if (chat.type === 'group') {
      return chat.name;
    } else {
      const otherParticipantId = chat.participants.find(id => id !== currentUser.id);
      const otherUser = appState.users && appState.users.find(user => user.id === otherParticipantId);
      return otherUser ? otherUser.name : 'Nežinomas vartotojas';
    }
  };
  
  // Get chat avatar (other user's avatar or group avatar)
  const getChatAvatar = (chat) => {
    if (chat.type === 'group') {
      return <Users size={20} className="text-amber-600" />;
    } else {
      const otherParticipantId = chat.participants.find(id => id !== currentUser.id);
      const otherUser = appState.users && appState.users.find(user => user.id === otherParticipantId);
      
      if (otherUser && otherUser.avatar) {
        return (
          <img 
            src={otherUser.avatar} 
            alt={otherUser.name} 
            className="w-full h-full object-cover"
          />
        );
      } else {
        return <User size={20} className="text-amber-600" />;
      }
    }
  };
  
  // Get all participants names
  const getParticipantsNames = (chat) => {
    if (!appState.users) return '';
    
    return chat.participants
      .map(id => {
        const user = appState.users.find(u => u.id === id);
        return user ? user.name : 'Nežinomas vartotojas';
      })
      .join(', ');
  };
  
  // Format timestamp
  const formatTimestamp = (timestamp) => {
    const date = new Date(timestamp);
    const now = new Date();
    const yesterday = new Date(now);
    yesterday.setDate(yesterday.getDate() - 1);
    
    // Same day, show only time
    if (date.toDateString() === now.toDateString()) {
      return date.toLocaleTimeString('lt-LT', { hour: '2-digit', minute: '2-digit' });
    }
    
    // Yesterday, show "Vakar" + time
    if (date.toDateString() === yesterday.toDateString()) {
      return `Vakar ${date.toLocaleTimeString('lt-LT', { hour: '2-digit', minute: '2-digit' })}`;
    }
    
    // Other days, show full date
    return date.toLocaleDateString('lt-LT', { year: 'numeric', month: '2-digit', day: '2-digit' });
  };
  
  // Handle send message
  const handleSendMessage = (e) => {
    e.preventDefault();
    
    if (!message.trim() && !attachment) return;
    
    if (appState.sendMessage) {
      appState.sendMessage(activeChat.id, message, attachment);
      setMessage('');
      setAttachment(null);
      setShowAttachmentOptions(false);
    }
  };
  
  // Handle attachment selection
  const handleAttachment = (type) => {
    // Simulate attachment selection
    setAttachment({
      type,
      url: `/api/placeholder/300/200?text=${type}`,
      name: `${type}_${new Date().getTime()}`
    });
    
    setShowAttachmentOptions(false);
  };
  
  // Handle user selection for new chat
  const handleUserSelection = (userId) => {
    setSelectedUsers(prev => {
      if (prev.includes(userId)) {
        return prev.filter(id => id !== userId);
      } else {
        return [...prev, userId];
      }
    });
  };
  
  // Handle create new chat
  const handleCreateChat = () => {
    if (selectedUsers.length === 0) {
      alert('Pasirinkite bent vieną vartotoją');
      return;
    }
    
    if (appState.createChat) {
      const type = selectedUsers.length > 1 ? 'group' : 'private';
      const name = type === 'group' ? 'Nauja grupė' : '';
      
      appState.createChat(
        type,
        name,
        [...selectedUsers, currentUser.id]
      );
    }
    
    setSelectedUsers([]);
    setShowNewChat(false);
  };
  
  // Get unread count for a chat
  const getUnreadCount = (chat) => {
    if (!appState.messages) return 0;
    
    return appState.messages.filter(
      msg => msg.chatId === chat.id && !msg.read && msg.senderId !== currentUser.id
    ).length;
  };
  
  // Get last message for a chat
  const getLastMessage = (chat) => {
    if (!appState.messages) return null;
    
    const messages = appState.messages
      .filter(msg => msg.chatId === chat.id)
      .sort((a, b) => new Date(b.timestamp) - new Date(a.timestamp));
    
    return messages.length > 0 ? messages[0] : null;
  };
  
  // Dabar, kai visos funkcijos apibrėžtos, galime jas naudoti
  
  // Tikriname, ar yra chats objektas, jei ne, sukuriame tuščią masyvą
  const userChats = appState.chats ? appState.chats.filter(chat => 
    chat.participants.includes(currentUser.id)
  ) : [];
  
  // Get filtered chats based on search term
  const filteredChats = userChats.filter(chat => {
    // Get chat name (other user's name or group name)
    const chatName = getChatName(chat);
    return chatName.toLowerCase().includes(searchTerm.toLowerCase());
  });
  
  // Get messages for active chat
  const chatMessages = activeChat && appState.messages ? appState.messages
    .filter(msg => msg.chatId === activeChat.id)
    .sort((a, b) => new Date(a.timestamp) - new Date(b.timestamp))
  : [];
  
  // Scroll to bottom when messages change
  useEffect(() => {
    if (messagesEndRef.current) {
      messagesEndRef.current.scrollIntoView({ behavior: 'smooth' });
    }
  }, [chatMessages]);
  
  // Mark messages as read when active chat changes
  useEffect(() => {
    if (activeChat && appState.markMessageAsRead) {
      const unreadMessages = chatMessages.filter(
        msg => !msg.read && msg.senderId !== currentUser.id
      );
      
      unreadMessages.forEach(msg => {
        appState.markMessageAsRead(msg.id);
      });
    }
  }, [activeChat, chatMessages, currentUser, appState]);
  
  return (
    <div className="flex h-full bg-white rounded-lg shadow-md overflow-hidden">
      {/* Chat list sidebar */}
      <div className={`w-full md:w-80 border-r border-gray-200 flex flex-col ${activeChat ? 'hidden md:flex' : 'flex'}`}>
        <div className="p-4 border-b border-gray-200">
          <div className="flex justify-between items-center mb-4">
            <h2 className="text-lg font-bold">Žinutės</h2>
            <button 
              onClick={() => setShowNewChat(true)}
              className="text-amber-600 hover:text-amber-800"
            >
              <Plus size={20} />
            </button>
          </div>
          
          <div className="relative">
            <input
              type="text"
              placeholder="Ieškoti pokalbių..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className="w-full bg-gray-100 rounded-full py-2 pl-10 pr-4 focus:outline-none focus:ring-2 focus:ring-amber-500"
            />
            <Search size={18} className="absolute left-3 top-2.5 text-gray-500" />
          </div>
        </div>
        
        {showNewChat ? (
          <div className="flex-1 overflow-auto p-4">
            <div className="flex items-center mb-4">
              <button 
                onClick={() => {
                  setShowNewChat(false);
                  setSelectedUsers([]);
                }}
                className="text-gray-500 hover:text-gray-700 mr-2"
              >
                <ChevronLeft size={20} />
              </button>
              <h3 className="text-lg font-medium">Naujas pokalbis</h3>
            </div>
            
            {selectedUsers.length > 0 && (
              <div className="mb-4">
                <div className="flex flex-wrap gap-2 mb-2">
                  {selectedUsers.map(userId => {
                    const user = appState.users.find(u => u.id === userId);
                    return (
                      <div 
                        key={userId}
                        className="bg-amber-100 text-amber-800 rounded-full px-3 py-1 text-sm flex items-center"
                      >
                        <span className="mr-1">{user?.name || 'Vartotojas'}</span>
                        <button 
                          onClick={() => handleUserSelection(userId)}
                          className="text-amber-600 hover:text-amber-800"
                        >
                          <X size={14} />
                        </button>
                      </div>
                    );
                  })}
                </div>
                
                <button
                  onClick={handleCreateChat}
                  className="w-full bg-amber-600 hover:bg-amber-700 text-white py-2 rounded-md font-medium"
                >
                  {selectedUsers.length > 1 ? 'Sukurti grupės pokalbį' : 'Pradėti pokalbį'}
                </button>
              </div>
            )}
            
            <h4 className="text-sm font-medium text-gray-500 mb-3">Vartotojai</h4>
            <div className="space-y-2">
              {appState.users
                .filter(user => user.id !== currentUser.id)
                .map(user => (
                  <div 
                    key={user.id}
                    onClick={() => handleUserSelection(user.id)}
                    className={`flex items-center p-2 rounded-lg cursor-pointer ${
                      selectedUsers.includes(user.id) ? 'bg-amber-50' : 'hover:bg-gray-50'
                    }`}
                  >
                    <div className="w-10 h-10 bg-amber-100 rounded-full flex items-center justify-center mr-3">
                      {user.avatar ? (
                        <img 
                          src={user.avatar} 
                          alt={user.name} 
                          className="w-full h-full object-cover rounded-full"
                        />
                      ) : (
                        <span className="text-amber-800 font-medium text-sm">
                          {user.name.split(' ').map(n => n[0]).join('')}
                        </span>
                      )}
                    </div>
                    <div className="flex-1">
                      <h3 className="font-medium">{user.name}</h3>
                      <p className="text-sm text-gray-500">{user.email}</p>
                    </div>
                    {selectedUsers.includes(user.id) && (
                      <div className="w-6 h-6 bg-amber-600 rounded-full flex items-center justify-center">
                        <Check size={14} className="text-white" />
                      </div>
                    )}
                  </div>
                ))}
            </div>
          </div>
        ) : (
          <div className="flex-1 overflow-auto" ref={chatListRef}>
            {filteredChats.length > 0 ? (
              <div className="divide-y divide-gray-100">
                {filteredChats.map(chat => {
                  const unreadCount = getUnreadCount(chat);
                  const lastMessage = getLastMessage(chat);
                  
                  return (
                    <div 
                      key={chat.id}
                      onClick={() => setActiveChat(chat)}
                      className={`flex items-center p-3 cursor-pointer hover:bg-gray-50 ${
                        activeChat?.id === chat.id ? 'bg-amber-50' : ''
                      }`}
                    >
                      <div className="w-12 h-12 bg-amber-100 rounded-full flex items-center justify-center mr-3 flex-shrink-0">
                        {getChatAvatar(chat)}
                      </div>
                      <div className="flex-1 min-w-0">
                        <div className="flex justify-between">
                          <h3 className="font-medium truncate">{getChatName(chat)}</h3>
                          {lastMessage && (
                            <span className="text-xs text-gray-500">
                              {formatTimestamp(lastMessage.timestamp)}
                            </span>
                          )}
                        </div>
                        {chat.type === 'group' && (
                          <p className="text-xs text-gray-500 truncate mb-1">
                            {getParticipantsNames(chat)}
                          </p>
                        )}
                        {lastMessage && (
                          <p className="text-sm text-gray-600 truncate">
                            {lastMessage.senderId === currentUser.id && <span>Jūs: </span>}
                            {lastMessage.attachment ? (
                              <span className="flex items-center">
                                {lastMessage.attachment.type === 'image' && <Image size={12} className="mr-1" />}
                                {lastMessage.attachment.type === 'video' && <Video size={12} className="mr-1" />}
                                {lastMessage.attachment.type === 'document' && <FileText size={12} className="mr-1" />}
                                {lastMessage.attachment.name || lastMessage.attachment.type}
                              </span>
                            ) : (
                              lastMessage.text
                            )}
                          </p>
                        )}
                      </div>
                      {unreadCount > 0 && (
                        <div className="ml-2 w-5 h-5 bg-amber-600 rounded-full flex items-center justify-center flex-shrink-0">
                          <span className="text-white text-xs">{unreadCount}</span>
                        </div>
                      )}
                    </div>
                  );
                })}
              </div>
            ) : (
              <div className="p-4 text-center">
                <p className="text-gray-500">Nerasta pokalbių</p>
                {searchTerm && (
                  <button 
                    onClick={() => setSearchTerm('')}
                    className="text-amber-600 hover:text-amber-800 text-sm mt-2"
                  >
                    Išvalyti paiešką
                  </button>
                )}
              </div>
            )}
          </div>
        )}
      </div>
      
      {/* Chat area */}
      {activeChat ? (
        <div className="flex-1 flex flex-col">
          {/* Chat header */}
          <div className="p-3 border-b border-gray-200 flex items-center">
            <button 
              onClick={() => setActiveChat(null)}
              className="md:hidden text-gray-500 hover:text-gray-700 mr-2"
            >
              <ChevronLeft size={20} />
            </button>
            <div className="w-10 h-10 bg-amber-100 rounded-full flex items-center justify-center mr-3">
              {getChatAvatar(activeChat)}
            </div>
            <div className="flex-1">
              <h2 className="font-medium">{getChatName(activeChat)}</h2>
              {activeChat.type === 'group' && (
                <p className="text-xs text-gray-500">
                  {activeChat.participants.length} dalyviai
                </p>
              )}
            </div>
            <button className="text-gray-500 hover:text-gray-700">
              <Info size={20} />
            </button>
          </div>
          
          {/* Messages area */}
          <div className="flex-1 overflow-auto p-4 bg-gray-50">
            {chatMessages.length > 0 ? (
              <div className="space-y-3">
                {chatMessages.map(msg => {
                  const isCurrentUser = msg.senderId === currentUser.id;
                  const sender = appState.users.find(user => user.id === msg.senderId);
                  
                  return (
                    <div 
                      key={msg.id}
                      className={`flex ${isCurrentUser ? 'justify-end' : 'justify-start'}`}
                    >
                      {!isCurrentUser && activeChat.type === 'group' && (
                        <div className="w-8 h-8 bg-amber-100 rounded-full flex items-center justify-center mr-2 flex-shrink-0 self-end">
                          {sender?.avatar ? (
                            <img 
                              src={sender.avatar} 
                              alt={sender.name} 
                              className="w-full h-full object-cover rounded-full"
                            />
                          ) : (
                            <span className="text-amber-800 font-medium text-xs">
                              {sender?.name.split(' ').map(n => n[0]).join('') || '?'}
                            </span>
                          )}
                        </div>
                      )}
                      
                      <div className={`max-w-[70%]`}>
                        {!isCurrentUser && activeChat.type === 'group' && (
                          <p className="text-xs text-gray-500 mb-1 ml-1">{sender?.name || 'Nežinomas vartotojas'}</p>
                        )}
                        
                        <div 
                          className={`rounded-lg p-3 inline-block ${
                            isCurrentUser 
                              ? 'bg-amber-600 text-white' 
                              : 'bg-white border border-gray-200 text-gray-800'
                          }`}
                        >
                          {msg.attachment && (
                            <div className="mb-2">
                              {msg.attachment.type === 'image' && (
                                <img 
                                  src={msg.attachment.url} 
                                  alt="Attached image" 
                                  className="rounded-md max-h-60 max-w-full"
                                />
                              )}
                              {msg.attachment.type === 'video' && (
                                <video 
                                  src={msg.attachment.url} 
                                  controls
                                  className="rounded-md max-h-60 max-w-full"
                                ></video>
                              )}
                              {msg.attachment.type === 'document' && (
                                <div className="bg-gray-100 rounded-md p-2 flex items-center">
                                  <FileText size={16} className="text-amber-600 mr-2" />
                                  <span className="text-sm">{msg.attachment.name}</span>
                                </div>
                              )}
                            </div>
                          )}
                          
                          {msg.text && <p>{msg.text}</p>}
                          <div className={`text-xs mt-1 text-right ${isCurrentUser ? 'text-amber-200' : 'text-gray-500'}`}>
                            {formatTimestamp(msg.timestamp)}
                          </div>
                        </div>
                      </div>
                    </div>
                  );
                })}
                <div ref={messagesEndRef} />
              </div>
            ) : (
              <div className="h-full flex items-center justify-center">
                <div className="text-center">
                  <div className="w-16 h-16 bg-amber-100 rounded-full flex items-center justify-center mx-auto mb-4">
                    <MessageIcon className="text-amber-600" />
                  </div>
                  <h3 className="text-lg font-medium text-gray-700 mb-1">
                    Pradėkite pokalbį
                  </h3>
                  <p className="text-gray-500">
                    Parašykite savo pirąją žinutę žemiau
                  </p>
                </div>
              </div>
            )}
          </div>
          
          {/* Message input */}
          <div className="p-3 border-t border-gray-200">
            {attachment && (
              <div className="mb-3 bg-gray-100 rounded-md p-2 flex items-center justify-between">
                <div className="flex items-center">
                  {attachment.type === 'image' && <Image size={16} className="text-blue-600 mr-2" />}
                  {attachment.type === 'video' && <Video size={16} className="text-purple-600 mr-2" />}
                  {attachment.type === 'document' && <FileText size={16} className="text-orange-600 mr-2" />}
                  <span className="text-sm">{attachment.name}</span>
                </div>
                <button 
                  onClick={() => setAttachment(null)}
                  className="text-gray-500 hover:text-gray-700"
                >
                  <X size={16} />
                </button>
              </div>
            )}
            
            <form onSubmit={handleSendMessage} className="flex items-center">
              <div className="relative mr-2">
                <button 
                  type="button"
                  onClick={() => setShowAttachmentOptions(!showAttachmentOptions)}
                  className="text-gray-500 hover:text-gray-700 p-2"
                >
                  <Paperclip size={20} />
                </button>
                
                {showAttachmentOptions && (
                  <div className="absolute bottom-full left-0 mb-2 bg-white rounded-md shadow-lg p-2 border border-gray-200">
                    <div className="flex flex-col space-y-2">
                      <button 
                        type="button"
                        onClick={() => handleAttachment('image')}
                        className="flex items-center text-sm text-gray-700 hover:bg-gray-100 p-2 rounded"
                      >
                        <Image size={16} className="text-blue-600 mr-2" />
                        <span>Nuotrauka</span>
                      </button>
                      <button 
                        type="button"
                        onClick={() => handleAttachment('video')}
                        className="flex items-center text-sm text-gray-700 hover:bg-gray-100 p-2 rounded"
                      >
                        <Video size={16} className="text-purple-600 mr-2" />
                        <span>Video</span>
                      </button>
                      <button 
                        type="button"
                        onClick={() => handleAttachment('document')}
                        className="flex items-center text-sm text-gray-700 hover:bg-gray-100 p-2 rounded"
                      >
                        <FileText size={16} className="text-orange-600 mr-2" />
                        <span>Dokumentas</span>
                      </button>
                    </div>
                  </div>
                )}
              </div>
              
              <input
                type="text"
                placeholder="Rašyti žinutę..."
                value={message}
                onChange={(e) => setMessage(e.target.value)}
                className="flex-1 bg-gray-100 rounded-full py-2 px-4 focus:outline-none focus:ring-2 focus:ring-amber-500"
              />
              
              <button 
                type="submit"
                className="ml-2 bg-amber-600 text-white p-2 rounded-full hover:bg-amber-700 disabled:opacity-50 disabled:cursor-not-allowed"
                disabled={!message.trim() && !attachment}
              >
                <Send size={20} />
              </button>
            </form>
          </div>
        </div>
      ) : (
        <div className="flex-1 hidden md:flex items-center justify-center bg-gray-50">
          <div className="text-center">
            <div className="w-20 h-20 bg-amber-100 rounded-full flex items-center justify-center mx-auto mb-4">
              <MessageIcon className="text-amber-600 w-10 h-10" />
            </div>
            <h2 className="text-xl font-medium text-gray-700 mb-2">Jūsų žinutės</h2>
            <p className="text-gray-500 max-w-md">
              Pasirinkite pokalbį iš sąrašo kairėje arba sukurkite naują, paspaudę "+" mygtuką
            </p>
          </div>
        </div>
      )}
    </div>
  );
};

// Message icon component
const MessageIcon = ({ className }) => (
  <svg 
    xmlns="http://www.w3.org/2000/svg" 
    viewBox="0 0 24 24" 
    fill="none" 
    stroke="currentColor" 
    strokeWidth="2" 
    strokeLinecap="round" 
    strokeLinejoin="round" 
    className={className}
  >
    <path d="M21 15a2 2 0 0 1-2 2H7l-4 4V5a2 2 0 0 1 2-2h14a2 2 0 0 1 2 2z"></path>
  </svg>
);

export default MessagesView; 