import React, { useState } from 'react';
import { 
  Search, 
  PlusCircle, 
  Hexagon, 
  AlertCircle, 
  BarChart2,
  CheckCircle,
  Clock
} from 'lucide-react';
import { useApp } from '../../context/AppContext';

const HivesView = () => {
  const { 
    appState, 
    updateAppState, 
    hives, 
    tasks,
    userTaskSteps
  } = useApp();
  
  const [searchTerm, setSearchTerm] = useState('');
  
  // Filter hives based on search term and current user
  const filteredHives = hives
    .filter(hive => 
      (hive.userId === appState.currentUser?.id || appState.currentUser?.role === 'admin') &&
      (hive.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
       hive.location.toLowerCase().includes(searchTerm.toLowerCase()))
    );
  
  // Get the most urgent task for a hive
  const getMostUrgentTask = (hiveId) => {
    const hiveTasks = tasks.filter(task => task.hiveId === hiveId && task.status !== 'completed');
    
    if (hiveTasks.length === 0) return null;
    
    // Sort by due date (closest first)
    hiveTasks.sort((a, b) => new Date(a.dueDate) - new Date(b.dueDate));
    
    return hiveTasks[0];
  };
  
  // Calculate task progress
  const getTaskProgress = (taskId) => {
    const steps = userTaskSteps.filter(step => step.taskId === taskId);
    if (!steps.length) return 0;
    
    const completedSteps = steps.filter(step => step.status === 'completed').length;
    return Math.round((completedSteps / steps.length) * 100);
  };
  
  // Format date
  const formatDate = (dateString) => {
    if (!dateString) return '';
    return new Date(dateString).toLocaleDateString('lt-LT');
  };
  
  // Get status badge
  const getStatusBadge = (status) => {
    switch (status) {
      case 'active':
        return (
          <span className="flex items-center text-green-700 bg-green-100 px-2 py-1 rounded text-xs">
            <CheckCircle size={12} className="mr-1" />
            Aktyvus
          </span>
        );
      case 'inactive':
        return (
          <span className="flex items-center text-gray-700 bg-gray-100 px-2 py-1 rounded text-xs">
            <AlertCircle size={12} className="mr-1" />
            Neaktyvus
          </span>
        );
      default:
        return null;
    }
  };
  
  // Redirect to hive details
  const viewHiveDetails = (hiveId) => {
    updateAppState({ 
      currentView: 'hiveDetails', 
      selectedHive: hiveId 
    });
  };
  
  return (
    <div className="p-6">
      <div className="flex flex-col md:flex-row md:items-center justify-between mb-6">
        <h1 className="text-2xl font-bold mb-4 md:mb-0">Mano aviliai</h1>
        
        <div className="flex space-x-2">
          <div className="relative">
            <input
              type="text"
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              placeholder="Ieškoti avilių..."
              className="pl-10 pr-4 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-amber-500"
            />
            <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400" size={18} />
          </div>
          
          {appState.currentUser?.role === 'admin' && (
            <button
              onClick={() => updateAppState({ showAddHiveForm: true })}
              className="bg-amber-600 text-white px-4 py-2 rounded-lg hover:bg-amber-700 flex items-center"
              type="button"
            >
              <PlusCircle size={18} className="mr-1" />
              Pridėti avilį
            </button>
          )}
        </div>
      </div>
      
      {filteredHives.length > 0 ? (
        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6">
          {filteredHives.map(hive => {
            const urgentTask = getMostUrgentTask(hive.id);
            const isOverdue = urgentTask && new Date(urgentTask.dueDate) < new Date();
            
            return (
              <div 
                key={hive.id} 
                className="bg-white rounded-lg shadow-sm overflow-hidden cursor-pointer hover:shadow-md transition-shadow"
                onClick={() => viewHiveDetails(hive.id)}
              >
                <div 
                  className="h-40 bg-cover bg-center relative"
                  style={{ backgroundImage: `url(${hive.image})` }}
                >
                  <div className="absolute top-2 right-2">
                    {getStatusBadge(hive.status)}
                  </div>
                </div>
                
                <div className="p-4">
                  <div className="flex items-start justify-between mb-2">
                    <div>
                      <h2 className="text-lg font-semibold">{hive.name}</h2>
                      <p className="text-gray-600 text-sm">{hive.location}</p>
                    </div>
                    <div className="text-amber-600">
                      <Hexagon size={24} />
                    </div>
                  </div>
                  
                  <div className="text-sm text-gray-600 mb-3">
                    <div className="flex justify-between">
                      <span>Įsigijimo data:</span>
                      <span>{formatDate(hive.purchaseDate)}</span>
                    </div>
                  </div>
                  
                  {urgentTask ? (
                    <div className={`
                      p-3 rounded-lg mt-3
                      ${isOverdue ? 'bg-red-50 border border-red-100' : 'bg-amber-50 border border-amber-100'}
                    `}>
                      <div className="flex items-start mb-1">
                        {isOverdue ? (
                          <AlertCircle size={16} className="text-red-500 mt-0.5 mr-2 flex-shrink-0" />
                        ) : (
                          <Clock size={16} className="text-amber-500 mt-0.5 mr-2 flex-shrink-0" />
                        )}
                        <div>
                          <div className={`font-medium text-sm ${isOverdue ? 'text-red-700' : 'text-amber-700'}`}>
                            {urgentTask.title}
                          </div>
                          <div className="text-xs text-gray-600">
                            {isOverdue ? 'Vėluojama' : 'Iki'}: {formatDate(urgentTask.dueDate)}
                          </div>
                        </div>
                      </div>
                      <div className="w-full bg-gray-200 rounded-full h-1.5 mt-1">
                        <div 
                          className="bg-amber-600 h-1.5 rounded-full" 
                          style={{ width: `${getTaskProgress(urgentTask.id)}%` }}
                        ></div>
                      </div>
                    </div>
                  ) : (
                    <div className="p-3 bg-green-50 border border-green-100 rounded-lg mt-3 flex items-center text-green-700 text-sm">
                      <CheckCircle size={16} className="mr-2" />
                      Nėra laukiančių užduočių
                    </div>
                  )}
                </div>
              </div>
            );
          })}
        </div>
      ) : (
        <div className="text-center py-16">
          <BarChart2 size={48} className="mx-auto text-gray-300 mb-4" />
          <h2 className="text-xl font-semibold text-gray-600 mb-2">Neturite avilių</h2>
          <p className="text-gray-500 mb-4">
            {searchTerm ? 'Nerasta avilių pagal paieškos kriterijus' : 'Pridėkite pirmą avilį, kad galėtumėte pradėti darbą'}
          </p>
          
          {appState.currentUser?.role === 'admin' && !searchTerm && (
            <button
              onClick={() => updateAppState({ showAddHiveForm: true })}
              className="bg-amber-600 text-white px-4 py-2 rounded-lg hover:bg-amber-700 inline-flex items-center"
              type="button"
            >
              <PlusCircle size={18} className="mr-1" />
              Pridėti avilį
            </button>
          )}
        </div>
      )}
      
      {/* Add Hive Modal - will be shown when appState.showAddHiveForm is true */}
      {appState.showAddHiveForm && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
          {/* Modal content will be implemented separately */}
        </div>
      )}
    </div>
  );
};

export default HivesView; 