import React, { useState, useEffect } from 'react';
import { 
  ArrowLeft, 
  MapPin, 
  Calendar, 
  Image, 
  Edit, 
  Check, 
  X, 
  PlusCircle, 
  Hexagon,
  MessageSquare,
  BarChart,
  AlertCircle,
  ClipboardList,
  Clock,
  CheckCircle
} from 'lucide-react';
import { useApp } from '../../context/AppContext';

const HiveDetails = ({ hiveId }) => {
  const { 
    appState, 
    updateAppState, 
    addHiveNote, 
    saveHiveName, 
    saveHiveLocation,
    changeHiveImage
  } = useApp();
  
  const [note, setNote] = useState('');
  const [editingName, setEditingName] = useState(false);
  const [editingLocation, setEditingLocation] = useState(false);
  const [activeTab, setActiveTab] = useState('overview');
  
  // Find the hive from state
  const hive = appState.hives.find(h => h.id === hiveId);
  
  // If no hive found, redirect to hives list
  useEffect(() => {
    if (!hive) {
      updateAppState({ currentView: 'hives', viewParams: {} });
    }
  }, [hive, updateAppState]);
  
  if (!hive) return null;
  
  // Get hive tasks
  const hiveTasks = appState.tasks 
    ? appState.tasks.filter(task => task.hiveId === hiveId)
    : [];
  
  // Get hive notes and events
  const hiveNotes = appState.hiveEvents 
    ? appState.hiveEvents
        .filter(event => event.hiveId === hiveId && event.type === 'note')
        .sort((a, b) => new Date(b.date) - new Date(a.date))
    : [];
  
  const hiveEvents = appState.hiveEvents
    ? appState.hiveEvents
        .filter(event => event.hiveId === hiveId && event.type !== 'note')
        .sort((a, b) => new Date(b.date) - new Date(a.date))
    : [];
  
  // Get hive honey records
  const honeyRecords = appState.honeyRecords 
    ? appState.honeyRecords
        .filter(record => record.hiveId === hiveId)
        .sort((a, b) => new Date(b.date) - new Date(a.date))
    : [];
  
  // Calculate total honey collected
  const totalHoney = honeyRecords.reduce((total, record) => total + record.amount, 0);
  
  // Get hive photos
  const hivePhotos = appState.hivePhotos
    ? appState.hivePhotos
        .filter(photo => photo.hiveId === hiveId)
        .sort((a, b) => new Date(b.dateAdded) - new Date(a.dateAdded))
    : [];
  
  // Format date
  const formatDate = (dateString) => {
    if (!dateString) return '-';
    const date = new Date(dateString);
    return date.toLocaleDateString('lt-LT');
  };
  
  // Handle adding a note
  const handleNoteSubmit = (e) => {
    e.preventDefault();
    if (!note.trim()) return;
    
    addHiveNote(hiveId, note);
    setNote('');
  };
  
  // Handle name edit
  const handleNameEdit = () => {
    setEditingName(true);
    updateAppState({
      forms: {
        ...appState.forms,
        hiveName: { value: hive.name }
      }
    });
  };
  
  // Handle name save
  const handleNameSave = () => {
    saveHiveName(hiveId);
    setEditingName(false);
  };
  
  // Handle location edit
  const handleLocationEdit = () => {
    setEditingLocation(true);
    updateAppState({
      forms: {
        ...appState.forms,
        hiveLocation: { value: hive.location }
      }
    });
  };
  
  // Handle location save
  const handleLocationSave = () => {
    saveHiveLocation(hiveId);
    setEditingLocation(false);
  };
  
  // Get event type label
  const getEventTypeLabel = (type) => {
    switch (type) {
      case 'inspection':
        return 'Patikrinimas';
      case 'treatment':
        return 'Gydymas';
      case 'feeding':
        return 'Maitinimas';
      case 'maintenance':
        return 'Priežiūra';
      case 'harvest':
        return 'Derliaus nuėmimas';
      default:
        return type;
    }
  };
  
  // Get task status and icon
  const getTaskStatusInfo = (task) => {
    const isOverdue = new Date(task.dueDate) < new Date() && task.status !== 'completed';
    
    if (task.status === 'completed') {
      return {
        label: 'Užbaigta',
        color: 'bg-green-100 text-green-800',
        icon: <CheckCircle size={16} className="text-green-600 mr-1" />
      };
    } else if (isOverdue) {
      return {
        label: 'Vėluojama',
        color: 'bg-red-100 text-red-800',
        icon: <AlertCircle size={16} className="text-red-600 mr-1" />
      };
    } else if (task.status === 'in-progress') {
      return {
        label: 'Vykdoma',
        color: 'bg-blue-100 text-blue-800',
        icon: <Clock size={16} className="text-blue-600 mr-1" />
      };
    } else {
      return {
        label: 'Suplanuota',
        color: 'bg-amber-100 text-amber-800',
        icon: <Clock size={16} className="text-amber-600 mr-1" />
      };
    }
  };
  
  // Calculate task progress
  const getTaskProgress = (taskId) => {
    const taskSteps = appState.userTaskSteps.filter(step => step.taskId === taskId);
    if (taskSteps.length === 0) return 0;
    
    const completedSteps = taskSteps.filter(step => step.status === 'completed').length;
    return Math.round((completedSteps / taskSteps.length) * 100);
  };
  
  // Handle back button click
  const handleBack = () => {
    updateAppState({ currentView: 'hives', viewParams: {} });
  };
  
  // Handle image change
  const handleImageChange = () => {
    changeHiveImage(hiveId);
  };
  
  return (
    <div className="bg-white rounded-lg shadow-md overflow-hidden">
      {/* Header */}
      <div className="relative h-48 bg-amber-600">
        <img 
          src={hive.image || '/placeholder-hive.jpg'} 
          alt={hive.name} 
          className="w-full h-full object-cover"
        />
        <button 
          onClick={handleImageChange}
          className="absolute bottom-4 right-4 bg-white bg-opacity-80 p-2 rounded-full hover:bg-opacity-100"
        >
          <Image size={20} className="text-amber-600" />
        </button>
        <button 
          onClick={handleBack}
          className="absolute top-4 left-4 bg-white p-2 rounded-full hover:bg-gray-100"
        >
          <ArrowLeft size={20} className="text-gray-700" />
        </button>
      </div>
      
      {/* Hive info */}
      <div className="p-6">
        <div className="flex flex-wrap items-start justify-between">
          <div className="flex-1 min-w-0 pr-4">
            {editingName ? (
              <div className="flex items-center mb-2">
                <input 
                  type="text"
                  value={appState.forms.hiveName?.value || ''}
                  onChange={(e) => updateAppState({
                    forms: {
                      ...appState.forms,
                      hiveName: { value: e.target.value }
                    }
                  })}
                  className="flex-1 border-b-2 border-amber-500 focus:outline-none text-2xl font-bold pb-1"
                  autoFocus
                />
                <button 
                  onClick={handleNameSave}
                  className="ml-2 text-green-600 hover:text-green-800"
                >
                  <Check size={20} />
                </button>
                <button 
                  onClick={() => setEditingName(false)}
                  className="ml-1 text-red-600 hover:text-red-800"
                >
                  <X size={20} />
                </button>
              </div>
            ) : (
              <div className="flex items-center mb-2">
                <h1 className="text-2xl font-bold text-gray-900">{hive.name}</h1>
                <button 
                  onClick={handleNameEdit}
                  className="ml-2 text-gray-400 hover:text-gray-600"
                >
                  <Edit size={16} />
                </button>
              </div>
            )}
            
            {editingLocation ? (
              <div className="flex items-center text-gray-600">
                <MapPin size={16} className="mr-1 flex-shrink-0" />
                <input 
                  type="text"
                  value={appState.forms.hiveLocation?.value || ''}
                  onChange={(e) => updateAppState({
                    forms: {
                      ...appState.forms,
                      hiveLocation: { value: e.target.value }
                    }
                  })}
                  className="flex-1 border-b border-amber-500 focus:outline-none"
                  autoFocus
                />
                <button 
                  onClick={handleLocationSave}
                  className="ml-2 text-green-600 hover:text-green-800"
                >
                  <Check size={16} />
                </button>
                <button 
                  onClick={() => setEditingLocation(false)}
                  className="ml-1 text-red-600 hover:text-red-800"
                >
                  <X size={16} />
                </button>
              </div>
            ) : (
              <div className="flex items-center text-gray-600">
                <MapPin size={16} className="mr-1" />
                <span>{hive.location || 'Vieta nenurodyta'}</span>
                <button 
                  onClick={handleLocationEdit}
                  className="ml-2 text-gray-400 hover:text-gray-600"
                >
                  <Edit size={14} />
                </button>
              </div>
            )}
          </div>
          
          <div className="flex flex-col items-end">
            <div className="bg-amber-100 rounded-md px-3 py-1 text-sm text-amber-800 mb-2 flex items-center">
              <Hexagon size={14} className="mr-1" />
              ID: {hive.id}
            </div>
            <div className="text-gray-600 text-sm flex items-center">
              <Calendar size={14} className="mr-1" />
              Pridėta: {formatDate(hive.dateCreated)}
            </div>
          </div>
        </div>
        
        {/* Tabs */}
        <div className="border-b border-gray-200 mt-6">
          <nav className="-mb-px flex space-x-6" aria-label="Tabs">
            <button
              onClick={() => setActiveTab('overview')}
              className={`py-3 px-1 border-b-2 font-medium text-sm ${
                activeTab === 'overview'
                  ? 'border-amber-500 text-amber-600'
                  : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
              }`}
            >
              Apžvalga
            </button>
            <button
              onClick={() => setActiveTab('tasks')}
              className={`py-3 px-1 border-b-2 font-medium text-sm ${
                activeTab === 'tasks'
                  ? 'border-amber-500 text-amber-600'
                  : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
              }`}
            >
              Užduotys ({hiveTasks.length})
            </button>
            <button
              onClick={() => setActiveTab('notes')}
              className={`py-3 px-1 border-b-2 font-medium text-sm ${
                activeTab === 'notes'
                  ? 'border-amber-500 text-amber-600'
                  : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
              }`}
            >
              Pastabos ({hiveNotes.length})
            </button>
            <button
              onClick={() => setActiveTab('events')}
              className={`py-3 px-1 border-b-2 font-medium text-sm ${
                activeTab === 'events'
                  ? 'border-amber-500 text-amber-600'
                  : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
              }`}
            >
              Įvykiai ({hiveEvents.length})
            </button>
            <button
              onClick={() => setActiveTab('honey')}
              className={`py-3 px-1 border-b-2 font-medium text-sm ${
                activeTab === 'honey'
                  ? 'border-amber-500 text-amber-600'
                  : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
              }`}
            >
              Medus ({honeyRecords.length})
            </button>
            <button
              onClick={() => setActiveTab('gallery')}
              className={`py-3 px-1 border-b-2 font-medium text-sm ${
                activeTab === 'gallery'
                  ? 'border-amber-500 text-amber-600'
                  : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
              }`}
            >
              Galerija ({hivePhotos.length})
            </button>
          </nav>
        </div>
        
        {/* Content */}
        <div className="mt-6">
          {activeTab === 'overview' && (
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <div>
                <h3 className="text-lg font-medium mb-3">Bendra informacija</h3>
                <div className="bg-gray-50 rounded-lg p-4">
                  <div className="grid grid-cols-2 gap-4">
                    <div>
                      <p className="text-xs text-gray-500">Statusas</p>
                      <p className="font-medium">{hive.status === 'active' ? 'Aktyvus' : 'Neaktyvus'}</p>
                    </div>
                    <div>
                      <p className="text-xs text-gray-500">Tipas</p>
                      <p className="font-medium">{hive.type || '-'}</p>
                    </div>
                    <div>
                      <p className="text-xs text-gray-500">Paskutinis patikrinimas</p>
                      <p className="font-medium">
                        {hiveEvents.find(e => e.type === 'inspection')
                          ? formatDate(hiveEvents.find(e => e.type === 'inspection').date)
                          : '-'}
                      </p>
                    </div>
                    <div>
                      <p className="text-xs text-gray-500">Paskutinis maitinimas</p>
                      <p className="font-medium">
                        {hiveEvents.find(e => e.type === 'feeding')
                          ? formatDate(hiveEvents.find(e => e.type === 'feeding').date)
                          : '-'}
                      </p>
                    </div>
                  </div>
                </div>
                
                <h3 className="text-lg font-medium mt-6 mb-3">Medaus statistika</h3>
                <div className="bg-gray-50 rounded-lg p-4">
                  <div className="grid grid-cols-2 gap-4">
                    <div>
                      <p className="text-xs text-gray-500">Iš viso surinkta</p>
                      <p className="text-2xl font-bold text-amber-600">{totalHoney} kg</p>
                    </div>
                    <div>
                      <p className="text-xs text-gray-500">Paskutinis nuėmimas</p>
                      <p className="font-medium">
                        {honeyRecords.length > 0 ? formatDate(honeyRecords[0].date) : '-'}
                      </p>
                    </div>
                  </div>
                  
                  {honeyRecords.length > 0 && (
                    <div className="mt-4">
                      <div className="text-xs text-gray-500 mb-1">Medaus nuėmimo istorija</div>
                      <div className="space-y-2">
                        {honeyRecords.slice(0, 3).map(record => (
                          <div key={record.id} className="flex justify-between items-center text-sm">
                            <span>{formatDate(record.date)}</span>
                            <span className="font-medium">{record.amount} kg</span>
                          </div>
                        ))}
                        {honeyRecords.length > 3 && (
                          <button
                            onClick={() => setActiveTab('honey')}
                            className="text-amber-600 hover:text-amber-800 text-sm flex items-center"
                          >
                            Žiūrėti visą istoriją
                          </button>
                        )}
                      </div>
                    </div>
                  )}
                </div>
              </div>
              
              <div>
                <h3 className="text-lg font-medium mb-3">Aktyvios užduotys</h3>
                {hiveTasks.filter(task => task.status !== 'completed').length > 0 ? (
                  <div className="space-y-3">
                    {hiveTasks.filter(task => task.status !== 'completed')
                      .slice(0, 3)
                      .map(task => {
                        const statusInfo = getTaskStatusInfo(task);
                        return (
                          <div key={task.id} className="bg-gray-50 rounded-lg p-4">
                            <div className="flex justify-between items-start">
                              <div>
                                <h4 className="font-medium">{task.title}</h4>
                                <div className="flex items-center text-sm text-gray-500 mt-1">
                                  <Clock size={14} className="mr-1" />
                                  {formatDate(task.dueDate)}
                                </div>
                              </div>
                              <span className={`inline-flex items-center px-2 py-1 rounded-full text-xs font-medium ${statusInfo.color}`}>
                                {statusInfo.icon}
                                {statusInfo.label}
                              </span>
                            </div>
                            
                            <div className="mt-3">
                              <div className="flex justify-between text-xs text-gray-500 mb-1">
                                <span>Progresas</span>
                                <span>{getTaskProgress(task.id)}%</span>
                              </div>
                              <div className="w-full bg-gray-200 rounded-full h-2">
                                <div 
                                  className="bg-amber-600 h-2 rounded-full" 
                                  style={{ width: `${getTaskProgress(task.id)}%` }}
                                ></div>
                              </div>
                            </div>
                          </div>
                        );
                      })}
                    
                    {hiveTasks.filter(task => task.status !== 'completed').length > 3 && (
                      <button
                        onClick={() => setActiveTab('tasks')}
                        className="text-amber-600 hover:text-amber-800 text-sm flex items-center"
                      >
                        Žiūrėti visas užduotis
                      </button>
                    )}
                  </div>
                ) : (
                  <div className="bg-gray-50 rounded-lg p-6 text-center">
                    <ClipboardList size={36} className="mx-auto text-gray-400 mb-2" />
                    <p className="text-gray-500">Nėra aktyvių užduočių šiam aviliui</p>
                  </div>
                )}
                
                <h3 className="text-lg font-medium mb-3 mt-6">Paskutinės pastabos</h3>
                {hiveNotes.length > 0 ? (
                  <div className="space-y-3">
                    {hiveNotes.slice(0, 2).map(note => (
                      <div key={note.id} className="bg-gray-50 rounded-lg p-4">
                        <p className="text-gray-700">{note.content}</p>
                        <div className="flex items-center text-sm text-gray-500 mt-2">
                          <Calendar size={14} className="mr-1" />
                          {formatDate(note.date)}
                        </div>
                      </div>
                    ))}
                    
                    {hiveNotes.length > 2 && (
                      <button
                        onClick={() => setActiveTab('notes')}
                        className="text-amber-600 hover:text-amber-800 text-sm flex items-center"
                      >
                        Žiūrėti visas pastabas
                      </button>
                    )}
                  </div>
                ) : (
                  <div className="bg-gray-50 rounded-lg p-6 text-center">
                    <MessageSquare size={36} className="mx-auto text-gray-400 mb-2" />
                    <p className="text-gray-500">Nėra pastabų šiam aviliui</p>
                  </div>
                )}
              </div>
            </div>
          )}
          
          {activeTab === 'tasks' && (
            <div>
              <div className="flex justify-between items-center mb-4">
                <h3 className="text-lg font-medium">Avilio užduotys</h3>
              </div>
              
              {hiveTasks.length > 0 ? (
                <div className="space-y-4">
                  {hiveTasks.map(task => {
                    const statusInfo = getTaskStatusInfo(task);
                    return (
                      <div key={task.id} className="bg-gray-50 rounded-lg p-4">
                        <div className="flex justify-between items-start">
                          <div>
                            <h4 className="font-medium">{task.title}</h4>
                            <p className="text-sm text-gray-600 mt-1">{task.description || 'Aprašymas nepateiktas'}</p>
                            <div className="flex items-center text-sm text-gray-500 mt-2">
                              <div className="flex items-center mr-4">
                                <Calendar size={14} className="mr-1" />
                                <span>Pradžia: {formatDate(task.startDate)}</span>
                              </div>
                              <div className="flex items-center">
                                <Clock size={14} className="mr-1" />
                                <span>Pabaiga: {formatDate(task.dueDate)}</span>
                              </div>
                            </div>
                          </div>
                          <span className={`inline-flex items-center px-2 py-1 rounded-full text-xs font-medium ${statusInfo.color}`}>
                            {statusInfo.icon}
                            {statusInfo.label}
                          </span>
                        </div>
                        
                        <div className="mt-4">
                          <div className="flex justify-between text-xs text-gray-500 mb-1">
                            <span>Progresas</span>
                            <span>{getTaskProgress(task.id)}%</span>
                          </div>
                          <div className="w-full bg-gray-200 rounded-full h-2">
                            <div 
                              className="bg-amber-600 h-2 rounded-full" 
                              style={{ width: `${getTaskProgress(task.id)}%` }}
                            ></div>
                          </div>
                        </div>
                      </div>
                    );
                  })}
                </div>
              ) : (
                <div className="bg-gray-50 rounded-lg p-8 text-center">
                  <ClipboardList size={48} className="mx-auto text-gray-400 mb-3" />
                  <p className="text-gray-600 mb-3">Nėra užduočių priskirtų šiam aviliui</p>
                </div>
              )}
            </div>
          )}
          
          {activeTab === 'notes' && (
            <div>
              <form onSubmit={handleNoteSubmit} className="mb-6">
                <div className="flex">
                  <textarea
                    value={note}
                    onChange={(e) => setNote(e.target.value)}
                    placeholder="Pridėti naują pastabą..."
                    className="flex-1 border border-gray-300 rounded-l-md p-3 focus:outline-none focus:ring-2 focus:ring-amber-500 focus:border-transparent"
                    rows={2}
                  ></textarea>
                  <button
                    type="submit"
                    className="bg-amber-600 text-white px-4 rounded-r-md hover:bg-amber-700 flex items-center"
                  >
                    <PlusCircle size={20} className="mr-2" />
                    Pridėti
                  </button>
                </div>
              </form>
              
              {hiveNotes.length > 0 ? (
                <div className="space-y-4">
                  {hiveNotes.map(note => (
                    <div key={note.id} className="bg-gray-50 rounded-lg p-4">
                      <p className="text-gray-700">{note.content}</p>
                      <div className="flex items-center text-sm text-gray-500 mt-3">
                        <Calendar size={14} className="mr-1" />
                        {formatDate(note.date)}
                        
                        {note.userId && (
                          <>
                            <span className="mx-2">•</span>
                            <span>{appState.users.find(u => u.id === note.userId)?.name || 'Vartotojas'}</span>
                          </>
                        )}
                      </div>
                    </div>
                  ))}
                </div>
              ) : (
                <div className="bg-gray-50 rounded-lg p-8 text-center">
                  <MessageSquare size={48} className="mx-auto text-gray-400 mb-3" />
                  <p className="text-gray-600">Nėra pastabų šiam aviliui</p>
                  <p className="text-gray-500 text-sm mt-1">Būk pirmas, kuris pridės pastabą!</p>
                </div>
              )}
            </div>
          )}
          
          {activeTab === 'events' && (
            <div>
              {hiveEvents.length > 0 ? (
                <div className="space-y-4">
                  {hiveEvents.map(event => (
                    <div key={event.id} className="bg-gray-50 rounded-lg p-4">
                      <div className="flex items-center mb-2">
                        <span className="bg-amber-100 text-amber-800 px-2 py-1 rounded text-xs font-medium">
                          {getEventTypeLabel(event.type)}
                        </span>
                      </div>
                      <p className="text-gray-700">{event.content}</p>
                      <div className="flex items-center text-sm text-gray-500 mt-3">
                        <Calendar size={14} className="mr-1" />
                        {formatDate(event.date)}
                        
                        {event.userId && (
                          <>
                            <span className="mx-2">•</span>
                            <span>{appState.users.find(u => u.id === event.userId)?.name || 'Vartotojas'}</span>
                          </>
                        )}
                      </div>
                    </div>
                  ))}
                </div>
              ) : (
                <div className="bg-gray-50 rounded-lg p-8 text-center">
                  <Calendar size={48} className="mx-auto text-gray-400 mb-3" />
                  <p className="text-gray-600">Nėra įvykių šiam aviliui</p>
                </div>
              )}
            </div>
          )}
          
          {activeTab === 'honey' && (
            <div>
              <div className="bg-amber-50 rounded-lg p-4 mb-6 flex items-center">
                <BarChart size={24} className="text-amber-600 mr-3" />
                <div>
                  <h3 className="font-medium">Iš viso surinkta: <span className="text-amber-600 font-bold">{totalHoney} kg</span></h3>
                  <p className="text-sm text-gray-600">Per {honeyRecords.length} nuėmimus</p>
                </div>
              </div>
              
              {honeyRecords.length > 0 ? (
                <div className="bg-white rounded-lg shadow overflow-hidden">
                  <table className="min-w-full divide-y divide-gray-200">
                    <thead className="bg-gray-50">
                      <tr>
                        <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                          Data
                        </th>
                        <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                          Kiekis (kg)
                        </th>
                        <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                          Kokybė
                        </th>
                        <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                          Pastabos
                        </th>
                      </tr>
                    </thead>
                    <tbody className="bg-white divide-y divide-gray-200">
                      {honeyRecords.map(record => (
                        <tr key={record.id}>
                          <td className="px-6 py-4 whitespace-nowrap">
                            <div className="text-sm text-gray-900">{formatDate(record.date)}</div>
                          </td>
                          <td className="px-6 py-4 whitespace-nowrap">
                            <div className="text-sm font-medium text-gray-900">{record.amount} kg</div>
                          </td>
                          <td className="px-6 py-4 whitespace-nowrap">
                            <span className="px-2 py-1 inline-flex text-xs leading-5 font-medium rounded-full bg-green-100 text-green-800">
                              {record.quality || 'Gera'}
                            </span>
                          </td>
                          <td className="px-6 py-4">
                            <div className="text-sm text-gray-900">{record.notes || '-'}</div>
                          </td>
                        </tr>
                      ))}
                    </tbody>
                  </table>
                </div>
              ) : (
                <div className="bg-gray-50 rounded-lg p-8 text-center">
                  <BarChart size={48} className="mx-auto text-gray-400 mb-3" />
                  <p className="text-gray-600">Nėra medaus nuėmimo įrašų</p>
                </div>
              )}
            </div>
          )}
          
          {activeTab === 'gallery' && (
            <div>
              {hivePhotos.length > 0 ? (
                <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-4">
                  {hivePhotos.map(photo => (
                    <div key={photo.id} className="relative rounded-lg overflow-hidden bg-gray-100 aspect-square">
                      <img 
                        src={photo.url} 
                        alt={`Avilys ${formatDate(photo.date)}`} 
                        className="absolute inset-0 w-full h-full object-cover"
                      />
                      <div className="absolute inset-x-0 bottom-0 bg-gradient-to-t from-black/80 to-transparent p-3">
                        <p className="text-white text-sm">{formatDate(photo.date)}</p>
                        {photo.description && (
                          <p className="text-gray-200 text-xs mt-1">{photo.description}</p>
                        )}
                      </div>
                    </div>
                  ))}
                </div>
              ) : (
                <div className="bg-gray-50 rounded-lg p-8 text-center">
                  <Image size={48} className="mx-auto text-gray-400 mb-3" />
                  <p className="text-gray-600">Nėra nuotraukų šiam aviliui</p>
                </div>
              )}
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default HiveDetails; 