import React, { useState } from 'react';
import { 
  User, 
  Mail, 
  Phone, 
  MapPin, 
  Lock, 
  Save, 
  X, 
  Check,
  Edit2
} from 'lucide-react';
import { useApp } from '../../context/AppContext';

const ProfileView = () => {
  const { 
    appState, 
    updateAppState, 
    forms, 
    updateForm, 
    updateProfile, 
    updatePassword 
  } = useApp();
  
  const [activeTab, setActiveTab] = useState('profile');
  const [editMode, setEditMode] = useState(false);
  const [passwordMode, setPasswordMode] = useState(false);
  
  const user = appState.currentUser;
  
  if (!user) {
    return (
      <div className="p-6">
        <h1 className="text-2xl font-bold mb-4">Profilis</h1>
        <div className="bg-red-50 p-4 rounded-lg">
          <p className="text-red-600">Vartotojas neprisijungęs.</p>
        </div>
      </div>
    );
  }
  
  // Handle profile form submission
  const handleProfileSubmit = (e) => {
    e.preventDefault();
    updateProfile();
    setEditMode(false);
  };
  
  // Handle password form submission
  const handlePasswordSubmit = (e) => {
    e.preventDefault();
    updatePassword();
    setPasswordMode(false);
  };
  
  // Cancel profile editing
  const cancelProfileEdit = () => {
    // Reset form to current user data
    updateForm('profile', 'name', user.name);
    updateForm('profile', 'email', user.email);
    updateForm('profile', 'phone', user.phone || '');
    updateForm('profile', 'address', user.address || '');
    setEditMode(false);
  };
  
  // Cancel password change
  const cancelPasswordChange = () => {
    // Reset password form
    updateForm('password', 'current', '');
    updateForm('password', 'new', '');
    updateForm('password', 'confirm', '');
    setPasswordMode(false);
  };
  
  // Start profile editing
  const startProfileEdit = () => {
    // Initialize form with current user data
    updateForm('profile', 'name', user.name);
    updateForm('profile', 'email', user.email);
    updateForm('profile', 'phone', user.phone || '');
    updateForm('profile', 'address', user.address || '');
    setEditMode(true);
  };
  
  // Start password change
  const startPasswordChange = () => {
    // Reset password form
    updateForm('password', 'current', '');
    updateForm('password', 'new', '');
    updateForm('password', 'confirm', '');
    setPasswordMode(true);
  };
  
  return (
    <div className="p-6">
      <h1 className="text-2xl font-bold mb-6">Mano profilis</h1>
      
      <div className="mb-6">
        <div className="tabs flex space-x-1 border-b">
          <button 
            onClick={() => setActiveTab('profile')}
            className={`px-4 py-2 font-medium ${activeTab === 'profile' ? 'text-amber-600 border-b-2 border-amber-600' : 'text-gray-500 hover:text-gray-700'}`}
            type="button"
          >
            Profilio informacija
          </button>
          <button 
            onClick={() => setActiveTab('security')}
            className={`px-4 py-2 font-medium ${activeTab === 'security' ? 'text-amber-600 border-b-2 border-amber-600' : 'text-gray-500 hover:text-gray-700'}`}
            type="button"
          >
            Saugumas
          </button>
        </div>
      </div>
      
      {activeTab === 'profile' && (
        <div className="bg-white rounded-lg shadow-sm p-6">
          <div className="flex justify-between items-start mb-6">
            <h2 className="text-xl font-bold">Profilio informacija</h2>
            
            {!editMode && (
              <button
                onClick={startProfileEdit}
                className="text-amber-600 hover:text-amber-700 flex items-center"
                type="button"
              >
                <Edit2 size={16} className="mr-1" />
                Redaguoti
              </button>
            )}
          </div>
          
          {editMode ? (
            <form onSubmit={handleProfileSubmit}>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6 mb-6">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    Vardas ir pavardė
                  </label>
                  <div className="relative">
                    <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                      <User size={18} className="text-gray-400" />
                    </div>
                    <input
                      type="text"
                      value={forms.profile?.name || ''}
                      onChange={(e) => updateForm('profile', 'name', e.target.value)}
                      className="pl-10 w-full p-2 border rounded-lg"
                      required
                    />
                  </div>
                </div>
                
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    El. paštas
                  </label>
                  <div className="relative">
                    <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                      <Mail size={18} className="text-gray-400" />
                    </div>
                    <input
                      type="email"
                      value={forms.profile?.email || ''}
                      onChange={(e) => updateForm('profile', 'email', e.target.value)}
                      className="pl-10 w-full p-2 border rounded-lg"
                      required
                    />
                  </div>
                </div>
                
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    Telefono numeris
                  </label>
                  <div className="relative">
                    <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                      <Phone size={18} className="text-gray-400" />
                    </div>
                    <input
                      type="tel"
                      value={forms.profile?.phone || ''}
                      onChange={(e) => updateForm('profile', 'phone', e.target.value)}
                      className="pl-10 w-full p-2 border rounded-lg"
                    />
                  </div>
                </div>
                
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    Adresas
                  </label>
                  <div className="relative">
                    <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                      <MapPin size={18} className="text-gray-400" />
                    </div>
                    <input
                      type="text"
                      value={forms.profile?.address || ''}
                      onChange={(e) => updateForm('profile', 'address', e.target.value)}
                      className="pl-10 w-full p-2 border rounded-lg"
                    />
                  </div>
                </div>
              </div>
              
              <div className="flex justify-end space-x-2">
                <button
                  type="button"
                  onClick={cancelProfileEdit}
                  className="px-4 py-2 border border-gray-300 rounded-lg text-gray-700 hover:bg-gray-100 flex items-center"
                >
                  <X size={16} className="mr-1" />
                  Atšaukti
                </button>
                <button
                  type="submit"
                  className="px-4 py-2 bg-amber-600 text-white rounded-lg hover:bg-amber-700 flex items-center"
                >
                  <Save size={16} className="mr-1" />
                  Išsaugoti
                </button>
              </div>
            </form>
          ) : (
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <div>
                <h3 className="text-sm text-gray-500 mb-1">Vardas ir pavardė</h3>
                <div className="flex items-center">
                  <User size={18} className="text-amber-600 mr-2" />
                  <p>{user.name}</p>
                </div>
              </div>
              
              <div>
                <h3 className="text-sm text-gray-500 mb-1">El. paštas</h3>
                <div className="flex items-center">
                  <Mail size={18} className="text-amber-600 mr-2" />
                  <p>{user.email}</p>
                </div>
              </div>
              
              <div>
                <h3 className="text-sm text-gray-500 mb-1">Telefono numeris</h3>
                <div className="flex items-center">
                  <Phone size={18} className="text-amber-600 mr-2" />
                  <p>{user.phone || 'Nenurodytas'}</p>
                </div>
              </div>
              
              <div>
                <h3 className="text-sm text-gray-500 mb-1">Adresas</h3>
                <div className="flex items-center">
                  <MapPin size={18} className="text-amber-600 mr-2" />
                  <p>{user.address || 'Nenurodytas'}</p>
                </div>
              </div>
              
              <div>
                <h3 className="text-sm text-gray-500 mb-1">Rolė</h3>
                <div className="flex items-center">
                  <User size={18} className="text-amber-600 mr-2" />
                  <p>{user.role === 'admin' ? 'Administratorius' : 'Bitininkas'}</p>
                </div>
              </div>
              
              {user.groupId && (
                <div>
                  <h3 className="text-sm text-gray-500 mb-1">Grupė</h3>
                  <div className="flex items-center">
                    <User size={18} className="text-amber-600 mr-2" />
                    <p>{user.groupId}</p>
                  </div>
                </div>
              )}
            </div>
          )}
        </div>
      )}
      
      {activeTab === 'security' && (
        <div className="bg-white rounded-lg shadow-sm p-6">
          <div className="flex justify-between items-start mb-6">
            <h2 className="text-xl font-bold">Slaptažodžio keitimas</h2>
            
            {!passwordMode && (
              <button
                onClick={startPasswordChange}
                className="text-amber-600 hover:text-amber-700 flex items-center"
                type="button"
              >
                <Edit2 size={16} className="mr-1" />
                Keisti slaptažodį
              </button>
            )}
          </div>
          
          {passwordMode ? (
            <form onSubmit={handlePasswordSubmit}>
              <div className="space-y-4 mb-6">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    Dabartinis slaptažodis
                  </label>
                  <div className="relative">
                    <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                      <Lock size={18} className="text-gray-400" />
                    </div>
                    <input
                      type="password"
                      value={forms.password?.current || ''}
                      onChange={(e) => updateForm('password', 'current', e.target.value)}
                      className="pl-10 w-full p-2 border rounded-lg"
                      required
                    />
                  </div>
                </div>
                
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    Naujas slaptažodis
                  </label>
                  <div className="relative">
                    <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                      <Lock size={18} className="text-gray-400" />
                    </div>
                    <input
                      type="password"
                      value={forms.password?.new || ''}
                      onChange={(e) => updateForm('password', 'new', e.target.value)}
                      className="pl-10 w-full p-2 border rounded-lg"
                      required
                      minLength={6}
                    />
                  </div>
                  <p className="text-xs text-gray-500 mt-1">Slaptažodis turi būti bent 6 simbolių ilgio.</p>
                </div>
                
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    Pakartokite naują slaptažodį
                  </label>
                  <div className="relative">
                    <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                      <Lock size={18} className="text-gray-400" />
                    </div>
                    <input
                      type="password"
                      value={forms.password?.confirm || ''}
                      onChange={(e) => updateForm('password', 'confirm', e.target.value)}
                      className="pl-10 w-full p-2 border rounded-lg"
                      required
                    />
                  </div>
                </div>
              </div>
              
              <div className="flex justify-end space-x-2">
                <button
                  type="button"
                  onClick={cancelPasswordChange}
                  className="px-4 py-2 border border-gray-300 rounded-lg text-gray-700 hover:bg-gray-100 flex items-center"
                >
                  <X size={16} className="mr-1" />
                  Atšaukti
                </button>
                <button
                  type="submit"
                  className="px-4 py-2 bg-amber-600 text-white rounded-lg hover:bg-amber-700 flex items-center"
                  disabled={forms.password?.new !== forms.password?.confirm}
                >
                  <Save size={16} className="mr-1" />
                  Išsaugoti
                </button>
              </div>
              
              {forms.password?.new !== forms.password?.confirm && forms.password?.confirm && (
                <div className="mt-2 text-red-600 text-sm">
                  Slaptažodžiai nesutampa.
                </div>
              )}
            </form>
          ) : (
            <div className="bg-gray-50 p-4 rounded-lg">
              <div className="flex items-start">
                <div className="bg-amber-100 p-2 rounded-full mr-3">
                  <Lock size={20} className="text-amber-600" />
                </div>
                <div>
                  <h3 className="font-medium">Slaptažodžio saugumas</h3>
                  <p className="text-gray-600 text-sm mt-1">
                    Rekomenduojame reguliariai keisti slaptažodį, kad užtikrintumėte savo paskyros saugumą.
                    Slaptažodis turėtų būti unikalus ir sudėtingas.
                  </p>
                </div>
              </div>
            </div>
          )}
        </div>
      )}
    </div>
  );
};

export default ProfileView; 