import React from 'react';
import { Users, UserPlus, Grid, ListTodo, Group, Menu, X } from 'lucide-react';
import { useApp } from '../../context/AppContext';
import AdminUsers from './AdminUsers';
import AdminGroups from './AdminGroups';
import AdminHives from './AdminHives';
import AdminTasks from './AdminTasks';
import AdminTemplates from './AdminTemplates';

const AdminDashboard = () => {
  const { appState, updateAppState } = useApp();
  const [mobileMenuOpen, setMobileMenuOpen] = React.useState(false);
  
  const getActiveTab = () => {
    switch (appState.adminTab) {
      case 'users':
        return <AdminUsers />;
      case 'groups':
        return <AdminGroups />;
      case 'hives':
        return <AdminHives />;
      case 'tasks':
        return <AdminTasks />;
      case 'templates':
        return <AdminTemplates />;
      default:
        return <AdminUsers />;
    }
  };
  
  const tabs = [
    { id: 'users', name: 'Vartotojai', icon: <Users size={20} /> },
    { id: 'groups', name: 'Grupės', icon: <Group size={20} /> },
    { id: 'hives', name: 'Aviliai', icon: <Grid size={20} /> },
    { id: 'tasks', name: 'Užduotys', icon: <ListTodo size={20} /> },
    { id: 'templates', name: 'Šablonai', icon: <ListTodo size={20} /> }
  ];
  
  return (
    <div className="flex-1 flex flex-col md:flex-row">
      {/* Šoninė administratoriaus navigacija */}
      <div className="bg-gray-800 text-white md:w-64 p-4">
        <div className="flex items-center justify-between md:hidden mb-4">
          <h2 className="text-xl font-bold">Admin Panel</h2>
          <button 
            onClick={() => setMobileMenuOpen(!mobileMenuOpen)}
            className="p-1 rounded-md hover:bg-gray-700"
          >
            {mobileMenuOpen ? <X size={24} /> : <Menu size={24} />}
          </button>
        </div>
        
        <div className={`${mobileMenuOpen ? 'block' : 'hidden'} md:block`}>
          <h2 className="text-xl font-bold mb-6 hidden md:block">Admin Panel</h2>
          <nav className="space-y-2">
            {tabs.map(tab => (
              <button
                key={tab.id}
                onClick={() => {
                  updateAppState({ adminTab: tab.id });
                  setMobileMenuOpen(false);
                }}
                className={`flex items-center w-full p-3 rounded-md ${
                  appState.adminTab === tab.id 
                    ? 'bg-amber-600 text-white' 
                    : 'text-gray-300 hover:bg-gray-700'
                }`}
              >
                <span className="mr-3">{tab.icon}</span>
                {tab.name}
              </button>
            ))}
          </nav>
        </div>
      </div>
      
      {/* Pagrindinis turinys */}
      <div className="flex-1 overflow-hidden">
        {getActiveTab()}
      </div>
    </div>
  );
};

export default AdminDashboard; 