import React, { useState, useEffect } from 'react';
import { Plus, Edit, Trash, Search, X, Save, Check, ArrowUp, ArrowDown, Copy, List } from 'lucide-react';
import { useApp } from '../../context/AppContext';

const AdminTemplates = () => {
  const { 
    appState, 
    updateAppState, 
    forms,
    updateForm,
    taskTemplates,
    steps,
    addTemplate,
    updateTemplate,
    deleteTemplate,
    addStepToTemplate,
    removeStepFromTemplate,
    moveStepUp,
    moveStepDown
  } = useApp();
  
  const [showForm, setShowForm] = useState(false);
  const [editingTemplateId, setEditingTemplateId] = useState(null);
  const [searchTerm, setSearchTerm] = useState('');
  const [confirmDelete, setConfirmDelete] = useState(null);
  const [selectedSteps, setSelectedSteps] = useState([]);
  const [showStepSelector, setShowStepSelector] = useState(false);
  const [stepSearchTerm, setStepSearchTerm] = useState('');
  
  // Resetear el formulario
  const resetForm = () => {
    updateForm('template', 'title', '');
    updateForm('template', 'description', '');
    updateForm('template', 'category', 'general');
    updateForm('template', 'steps', []);
    setSelectedSteps([]);
  };
  
  // Inicializar el formulario para editar
  const initEditForm = (template) => {
    setEditingTemplateId(template.id);
    updateForm('template', 'title', template.title);
    updateForm('template', 'description', template.description || '');
    updateForm('template', 'category', template.category || 'general');
    updateForm('template', 'steps', template.steps || []);
    setSelectedSteps(template.steps || []);
    setShowForm(true);
  };
  
  // Manejar envío del formulario
  const handleSubmit = (e) => {
    e.preventDefault();
    
    // Validar formulario
    if (!forms.template.title) {
      alert('Būtina įvesti šablono pavadinimą');
      return;
    }
    
    // Actualizar pasos en el formulario antes de enviar
    updateForm('template', 'steps', selectedSteps);
    
    console.log("Form data before submitting:", {
      title: forms.template.title,
      description: forms.template.description,
      category: forms.template.category,
      steps: selectedSteps // Naudojame atnaujintus žingsnius
    });
    
    setTimeout(() => { // Įdedame nedidelį uždelsimą, kad updateForm užbaigtų darbą prieš išsaugant
      if (editingTemplateId) {
        // Actualizar plantilla existente
        updateTemplate(editingTemplateId);
      } else {
        // Añadir nueva plantilla
        addTemplate();
      }
      
      // Resetear estado
      setShowForm(false);
      setEditingTemplateId(null);
      resetForm();
    }, 100);
  };
  
  // Manejar eliminación de plantilla
  const handleDeleteTemplate = (templateId) => {
    if (confirmDelete === templateId) {
      // Eliminar plantilla
      deleteTemplate(templateId);
      setConfirmDelete(null);
    } else {
      setConfirmDelete(templateId);
    }
  };
  
  // Añadir paso a la selección
  const addStepToSelection = (step) => {
    if (!selectedSteps.some(s => s.id === step.id)) {
      const newStep = {
        ...step,
        order: selectedSteps.length
      };
      const updatedSteps = [...selectedSteps, newStep];
      setSelectedSteps(updatedSteps);
      updateForm('template', 'steps', updatedSteps);
      console.log("Step added to selection:", newStep);
      console.log("Updated selected steps:", updatedSteps);
    }
  };
  
  // Eliminar paso de la selección
  const removeStepFromSelection = (stepId) => {
    const updatedSteps = selectedSteps.filter(step => step.id !== stepId)
      .map((step, index) => ({
        ...step,
        order: index
      }));
    
    setSelectedSteps(updatedSteps);
    updateForm('template', 'steps', updatedSteps);
    console.log("Step removed from selection. Updated steps:", updatedSteps);
  };
  
  // Mover paso en la selección
  const moveStepInSelection = (stepId, direction) => {
    const index = selectedSteps.findIndex(step => step.id === stepId);
    if (index === -1) return;
    
    if (direction === 'up' && index > 0) {
      const updatedSteps = [...selectedSteps];
      [updatedSteps[index], updatedSteps[index - 1]] = [updatedSteps[index - 1], updatedSteps[index]];
      
      // Actualizar orden
      const reorderedSteps = updatedSteps.map((step, idx) => ({
        ...step,
        order: idx
      }));
      
      setSelectedSteps(reorderedSteps);
      updateForm('template', 'steps', reorderedSteps);
    } else if (direction === 'down' && index < selectedSteps.length - 1) {
      const updatedSteps = [...selectedSteps];
      [updatedSteps[index], updatedSteps[index + 1]] = [updatedSteps[index + 1], updatedSteps[index]];
      
      // Actualizar orden
      const reorderedSteps = updatedSteps.map((step, idx) => ({
        ...step,
        order: idx
      }));
      
      setSelectedSteps(reorderedSteps);
      updateForm('template', 'steps', reorderedSteps);
    }
  };
  
  // Filtrar plantillas por término de búsqueda
  const filteredTemplates = taskTemplates.filter(template => 
    template.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
    (template.description && template.description.toLowerCase().includes(searchTerm.toLowerCase())) ||
    (template.category && template.category.toLowerCase().includes(searchTerm.toLowerCase()))
  );
  
  // Filtrar pasos por término de búsqueda
  const filteredSteps = steps.filter(step => 
    !stepSearchTerm || 
    step.title.toLowerCase().includes(stepSearchTerm.toLowerCase()) ||
    (step.description && step.description.toLowerCase().includes(stepSearchTerm.toLowerCase()))
  );
  
  // Obtener el número de pasos por plantilla
  const getStepsCount = (templateId) => {
    const template = taskTemplates.find(t => t.id === templateId);
    return template && template.steps ? template.steps.length : 0;
  };
  
  // Obtener la categoría formateada
  const getCategoryLabel = (category) => {
    switch (category) {
      case 'general':
        return 'Bendra';
      case 'maintenance':
        return 'Priežiūra';
      case 'inspection':
        return 'Patikra';
      case 'harvest':
        return 'Derliaus nuėmimas';
      case 'treatment':
        return 'Gydymas';
      default:
        return category || 'Nenurodyta';
    }
  };
  
  return (
    <div className="p-6">
      <div className="flex justify-between items-center mb-6">
        <h2 className="text-2xl font-bold">Užduočių šablonai</h2>
        <button 
          onClick={() => {
            resetForm();
            setEditingTemplateId(null);
            setShowForm(true);
          }}
          className="bg-amber-600 text-white px-4 py-2 rounded-md hover:bg-amber-700 flex items-center"
        >
          <Plus size={16} className="mr-2" />
          Sukurti šabloną
        </button>
      </div>
      
      {/* Search field */}
      <div className="mb-6 relative">
        <input 
          type="text"
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
          placeholder="Ieškoti šablonų..."
          className="w-full p-3 pl-10 pr-10 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-amber-500"
        />
        <Search size={20} className="absolute left-3 top-3.5 text-gray-400" />
        {searchTerm && (
          <button 
            onClick={() => setSearchTerm('')}
            className="absolute right-3 top-3.5 text-gray-400 hover:text-gray-600"
          >
            <X size={20} />
          </button>
        )}
      </div>
      
      {/* Template form */}
      {showForm && (
        <div className="bg-white p-6 rounded-lg shadow-md mb-6">
          <h3 className="text-lg font-semibold mb-4">{editingTemplateId ? 'Redaguoti šabloną' : 'Sukurti naują šabloną'}</h3>
          
          <form onSubmit={handleSubmit} className="space-y-4">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Pavadinimas *</label>
                <input 
                  type="text"
                  value={forms.template?.title || ''}
                  onChange={(e) => updateForm('template', 'title', e.target.value)}
                  className="w-full p-2 border border-gray-300 rounded-md"
                  placeholder="Šablono pavadinimas"
                  required
                />
              </div>
              
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Kategorija</label>
                <select
                  value={forms.template?.category || 'general'}
                  onChange={(e) => updateForm('template', 'category', e.target.value)}
                  className="w-full p-2 border border-gray-300 rounded-md"
                >
                  <option value="general">Bendra</option>
                  <option value="maintenance">Priežiūra</option>
                  <option value="inspection">Patikra</option>
                  <option value="harvest">Derliaus nuėmimas</option>
                  <option value="treatment">Gydymas</option>
                </select>
              </div>
            </div>
            
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">Aprašymas</label>
              <textarea 
                value={forms.template?.description || ''}
                onChange={(e) => updateForm('template', 'description', e.target.value)}
                className="w-full p-2 border border-gray-300 rounded-md"
                rows={3}
                placeholder="Detalus šablono aprašymas"
              ></textarea>
            </div>
            
            <div>
              <div className="flex justify-between items-center mb-2">
                <label className="block text-sm font-medium text-gray-700">Žingsniai</label>
                <button 
                  type="button"
                  onClick={() => setShowStepSelector(true)}
                  className="text-sm text-amber-600 hover:text-amber-800 flex items-center"
                >
                  <Plus size={16} className="mr-1" />
                  Pridėti žingsnius
                </button>
              </div>
              
              {selectedSteps.length > 0 ? (
                <div className="border border-gray-200 rounded-md overflow-hidden">
                  <ul className="divide-y divide-gray-200 max-h-60 overflow-y-auto">
                    {selectedSteps.map((step, index) => (
                      <li key={step.id} className="p-3 hover:bg-gray-50 flex justify-between items-center">
                        <div className="flex items-center">
                          <span className="bg-amber-100 text-amber-800 w-6 h-6 rounded-full flex items-center justify-center mr-3">
                            {index + 1}
                          </span>
                          <div>
                            <p className="font-medium text-gray-800">{step.title}</p>
                            {step.description && (
                              <p className="text-sm text-gray-500">{step.description}</p>
                            )}
                          </div>
                        </div>
                        <div className="flex items-center space-x-2">
                          <button 
                            type="button"
                            onClick={() => moveStepInSelection(step.id, 'up')}
                            disabled={index === 0}
                            className={`p-1 rounded-full ${index === 0 ? 'text-gray-300 cursor-not-allowed' : 'text-gray-500 hover:bg-gray-100'}`}
                          >
                            <ArrowUp size={16} />
                          </button>
                          <button 
                            type="button"
                            onClick={() => moveStepInSelection(step.id, 'down')}
                            disabled={index === selectedSteps.length - 1}
                            className={`p-1 rounded-full ${index === selectedSteps.length - 1 ? 'text-gray-300 cursor-not-allowed' : 'text-gray-500 hover:bg-gray-100'}`}
                          >
                            <ArrowDown size={16} />
                          </button>
                          <button 
                            type="button"
                            onClick={() => removeStepFromSelection(step.id)}
                            className="p-1 rounded-full text-red-500 hover:bg-red-50"
                          >
                            <X size={16} />
                          </button>
                        </div>
                      </li>
                    ))}
                  </ul>
                </div>
              ) : (
                <div className="border border-dashed border-gray-300 rounded-md p-4 text-center text-gray-500">
                  Nėra pridėtų žingsnių. Spauskite "Pridėti žingsnius", kad pridėtumėte užduoties etapus.
                </div>
              )}
            </div>
            
            <div className="flex justify-end space-x-3 mt-6">
              <button 
                type="button"
                onClick={() => {
                  setShowForm(false);
                  resetForm();
                }}
                className="px-4 py-2 border border-gray-300 rounded-md text-gray-700 hover:bg-gray-50"
              >
                Atšaukti
              </button>
              <button 
                type="submit"
                className="px-4 py-2 bg-amber-600 text-white rounded-md hover:bg-amber-700 flex items-center"
                disabled={selectedSteps.length === 0}
              >
                <Save size={16} className="mr-2" />
                {editingTemplateId ? 'Atnaujinti' : 'Sukurti'}
              </button>
            </div>
          </form>
        </div>
      )}
      
      {/* Step selector modal */}
      {showStepSelector && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
          <div className="bg-white rounded-lg shadow-xl w-full max-w-2xl max-h-[80vh] overflow-hidden">
            <div className="p-4 border-b border-gray-200 flex justify-between items-center">
              <h3 className="text-lg font-semibold">Pasirinkite žingsnius</h3>
              <button 
                type="button"
                onClick={() => setShowStepSelector(false)}
                className="text-gray-500 hover:text-gray-700"
              >
                <X size={20} />
              </button>
            </div>
            
            <div className="p-4">
              <div className="mb-4 relative">
                <input 
                  type="text"
                  value={stepSearchTerm}
                  onChange={(e) => setStepSearchTerm(e.target.value)}
                  placeholder="Ieškoti žingsnių..."
                  className="w-full p-2 pl-8 border border-gray-300 rounded-md"
                />
                <Search size={16} className="absolute left-2.5 top-3 text-gray-400" />
              </div>
              
              <div className="max-h-[50vh] overflow-y-auto">
                {filteredSteps.length > 0 ? (
                  <ul className="divide-y divide-gray-200">
                    {filteredSteps.map(step => {
                      const isSelected = selectedSteps.some(s => s.id === step.id);
                      return (
                        <li 
                          key={step.id} 
                          className={`p-3 hover:bg-gray-50 cursor-pointer ${isSelected ? 'bg-amber-50' : ''}`}
                          onClick={() => {
                            if (isSelected) {
                              removeStepFromSelection(step.id);
                            } else {
                              addStepToSelection(step);
                            }
                          }}
                        >
                          <div className="flex items-center justify-between">
                            <div>
                              <p className="font-medium text-gray-800">{step.title}</p>
                              {step.description && (
                                <p className="text-sm text-gray-500">{step.description}</p>
                              )}
                            </div>
                            {isSelected ? (
                              <Check size={20} className="text-amber-600" />
                            ) : (
                              <Plus size={20} className="text-gray-400" />
                            )}
                          </div>
                        </li>
                      );
                    })}
                  </ul>
                ) : (
                  <div className="text-center py-8 text-gray-500">
                    {stepSearchTerm ? 'Nerasta žingsnių pagal paiešką' : 'Nėra sukurtų žingsnių'}
                    {!steps.length && (
                      <p className="mt-2">
                        Pirma sukurkite žingsnius "Žingsnių" skiltyje.
                      </p>
                    )}
                  </div>
                )}
              </div>
              
              <div className="border-t border-gray-200 mt-4 pt-4 flex justify-end">
                <button
                  type="button"
                  onClick={() => setShowStepSelector(false)}
                  className="px-4 py-2 bg-amber-600 text-white rounded-md hover:bg-amber-700"
                >
                  Baigti pasirinkimą
                </button>
              </div>
            </div>
          </div>
        </div>
      )}
      
      {/* Templates list */}
      <div className="bg-white rounded-lg shadow-md overflow-hidden">
        <table className="min-w-full divide-y divide-gray-200">
          <thead className="bg-gray-50">
            <tr>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Šablonas</th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Aprašymas</th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Kategorija</th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Žingsnių kiekis</th>
              <th className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider">Veiksmai</th>
            </tr>
          </thead>
          <tbody className="bg-white divide-y divide-gray-200">
            {filteredTemplates.length > 0 ? (
              filteredTemplates.map(template => (
                <tr key={template.id} className="hover:bg-gray-50">
                  <td className="px-6 py-4 whitespace-nowrap">
                    <div className="font-medium text-gray-900">{template.title}</div>
                  </td>
                  <td className="px-6 py-4">
                    <div className="text-sm text-gray-900 line-clamp-2">{template.description || '-'}</div>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    <span className="px-2 py-1 inline-flex text-xs leading-5 font-semibold rounded-full bg-gray-100 text-gray-800">
                      {getCategoryLabel(template.category)}
                    </span>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                    {getStepsCount(template.id)}
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap text-right text-sm font-medium">
                    <button 
                      onClick={() => initEditForm(template)}
                      className="text-amber-600 hover:text-amber-900 mx-2"
                    >
                      <Edit size={18} />
                    </button>
                    <button 
                      onClick={() => handleDeleteTemplate(template.id)}
                      className={`text-red-600 hover:text-red-900 mx-2 ${
                        confirmDelete === template.id ? 'animate-pulse' : ''
                      }`}
                    >
                      <Trash size={18} />
                    </button>
                  </td>
                </tr>
              ))
            ) : (
              <tr>
                <td colSpan={5} className="px-6 py-4 text-center text-gray-500">
                  {searchTerm ? 'Nerasta šablonų pagal paiešką' : 'Nėra sukurtų šablonų'}
                </td>
              </tr>
            )}
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default AdminTemplates; 