import React, { useState, useEffect } from 'react';
import { 
  ArrowLeft, 
  Calendar, 
  Clock, 
  CheckCircle, 
  ChevronDown, 
  ChevronUp,
  AlertCircle,
  BarChart,
  MessageSquare,
  Edit,
  Trash,
  Save,
  X,
  Check,
  Flag,
  Hexagon,
  Users
} from 'lucide-react';
import { useApp } from '../../context/AppContext';

const TaskDetails = ({ taskId }) => {
  const { 
    appState, 
    updateAppState, 
    completeTaskStep,
    addTaskComment,
    updateTaskStatus,
    deleteTask
  } = useApp();
  
  const [comment, setComment] = useState('');
  const [expandedSteps, setExpandedSteps] = useState([]);
  const [confirmDelete, setConfirmDelete] = useState(false);
  
  // Find the task from state
  const task = appState.tasks ? appState.tasks.find(t => t.id === taskId) : null;
  
  // If no task found, redirect to tasks list
  useEffect(() => {
    if (!task) {
      updateAppState({ currentView: 'tasks', viewParams: {} });
    }
  }, [task, updateAppState]);
  
  if (!task) return null;
  
  // Get task steps, comments, users with safety checks
  const taskSteps = appState.userTaskSteps ? appState.userTaskSteps.filter(step => step.taskId === taskId) : [];
  const taskComments = appState.taskComments ? appState.taskComments.filter(comment => comment.taskId === taskId) : [];
  const taskAssignedUser = appState.users ? appState.users.find(user => user.id === task.assignedTo) : null;
  const taskCreatedByUser = appState.users ? appState.users.find(user => user.id === task.assignedBy) : null;
  const hive = appState.hives ? appState.hives.find(h => h.id === task.hiveId) : null;
  
  // Sort steps by order
  taskSteps.sort((a, b) => a.order - b.order);
  
  // Format date
  const formatDate = (dateString) => {
    if (!dateString) return '-';
    const date = new Date(dateString);
    return date.toLocaleDateString('lt-LT');
  };
  
  // Toggle step expansion
  const toggleStepExpansion = (stepId) => {
    if (expandedSteps.includes(stepId)) {
      setExpandedSteps(expandedSteps.filter(id => id !== stepId));
    } else {
      setExpandedSteps([...expandedSteps, stepId]);
    }
  };
  
  // Handle comment submission
  const handleCommentSubmit = (e) => {
    e.preventDefault();
    if (!comment.trim()) return;
    
    addTaskComment(taskId, comment);
    setComment('');
  };
  
  // Handle step completion toggle
  const handleStepStatusChange = (stepId, completed) => {
    completeTaskStep(stepId, completed);
  };
  
  // Handle task status change
  const handleStatusChange = (newStatus) => {
    updateTaskStatus(taskId, newStatus);
  };
  
  // Handle delete confirmation
  const handleDeleteConfirm = () => {
    deleteTask(taskId);
    updateAppState({ currentView: 'tasks', viewParams: {} });
  };
  
  // Check if task is overdue
  const isOverdue = new Date(task.dueDate) < new Date() && task.status !== 'completed';
  
  // Calculate task progress
  const taskProgress = taskSteps.length > 0 
    ? Math.round((taskSteps.filter(step => step.status === 'completed').length / taskSteps.length) * 100) 
    : 0;
  
  // Get step type icon
  const getStepTypeIcon = (type) => {
    switch (type) {
      case 'image':
        return <div className="w-6 h-6 rounded-full bg-blue-100 flex items-center justify-center mr-3"><Check size={14} className="text-blue-600" /></div>;
      case 'video':
        return <div className="w-6 h-6 rounded-full bg-purple-100 flex items-center justify-center mr-3"><Check size={14} className="text-purple-600" /></div>;
      case 'document':
        return <div className="w-6 h-6 rounded-full bg-orange-100 flex items-center justify-center mr-3"><Check size={14} className="text-orange-600" /></div>;
      default:
        return <div className="w-6 h-6 rounded-full bg-green-100 flex items-center justify-center mr-3"><Check size={14} className="text-green-600" /></div>;
    }
  };
  
  // Get priority label and color
  const getPriorityInfo = (priority) => {
    switch (priority) {
      case 'high':
        return { 
          label: 'Aukštas', 
          color: 'bg-red-100 text-red-800',
          icon: <Flag size={14} className="text-red-600 mr-1" /> 
        };
      case 'medium':
        return { 
          label: 'Vidutinis', 
          color: 'bg-amber-100 text-amber-800',
          icon: <Flag size={14} className="text-amber-600 mr-1" /> 
        };
      case 'low':
        return { 
          label: 'Žemas', 
          color: 'bg-blue-100 text-blue-800',
          icon: <Flag size={14} className="text-blue-600 mr-1" /> 
        };
      default:
        return { 
          label: 'Nenurodyta', 
          color: 'bg-gray-100 text-gray-800',
          icon: <Flag size={14} className="text-gray-600 mr-1" /> 
        };
    }
  };
  
  // Get status label and color
  const getStatusInfo = (status, isOverdue) => {
    if (status === 'completed') {
      return {
        label: 'Užbaigta',
        color: 'bg-green-100 text-green-800',
        icon: <CheckCircle size={16} className="text-green-600 mr-1" />
      };
    } else if (isOverdue) {
      return {
        label: 'Vėluojama',
        color: 'bg-red-100 text-red-800',
        icon: <AlertCircle size={16} className="text-red-600 mr-1" />
      };
    } else if (status === 'in-progress') {
      return {
        label: 'Vykdoma',
        color: 'bg-blue-100 text-blue-800',
        icon: <Clock size={16} className="text-blue-600 mr-1" />
      };
    } else {
      return {
        label: 'Suplanuota',
        color: 'bg-amber-100 text-amber-800',
        icon: <Clock size={16} className="text-amber-600 mr-1" />
      };
    }
  };
  
  // Handle back button click
  const handleBack = () => {
    updateAppState({ currentView: 'tasks', viewParams: {} });
  };
  
  const priorityInfo = getPriorityInfo(task.priority);
  const statusInfo = getStatusInfo(task.status, isOverdue);
  
  return (
    <div className="bg-white rounded-lg shadow-md p-6">
      {/* Header with back button */}
      <div className="flex items-center mb-4">
        <button 
          onClick={handleBack}
          className="mr-3 text-gray-500 hover:text-gray-700"
        >
          <ArrowLeft size={20} />
        </button>
        <h1 className="text-2xl font-bold text-gray-900 flex-1">{task.title}</h1>
        
        {/* Task actions */}
        <div className="flex space-x-2">
          {confirmDelete ? (
            <>
              <button 
                onClick={handleDeleteConfirm}
                className="bg-red-600 hover:bg-red-700 text-white px-3 py-1 rounded-md text-sm flex items-center"
              >
                <Check size={16} className="mr-1" /> Patvirtinti
              </button>
              <button 
                onClick={() => setConfirmDelete(false)}
                className="bg-gray-300 hover:bg-gray-400 text-gray-800 px-3 py-1 rounded-md text-sm flex items-center"
              >
                <X size={16} className="mr-1" /> Atšaukti
              </button>
            </>
          ) : (
            <>
              <button 
                onClick={() => updateAppState({ 
                  currentView: 'editTask', 
                  viewParams: { taskId } 
                })}
                className="text-blue-600 hover:text-blue-800 px-2 py-1"
              >
                <Edit size={18} />
              </button>
              <button 
                onClick={() => setConfirmDelete(true)}
                className="text-red-600 hover:text-red-800 px-2 py-1"
              >
                <Trash size={18} />
              </button>
            </>
          )}
        </div>
      </div>
      
      {/* Task info section */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-6">
        <div className="col-span-2">
          <div className="bg-gray-50 rounded-lg p-4 mb-4">
            <p className="text-gray-700">{task.description || 'Aprašymas nepateiktas'}</p>
          </div>
          
          <div className="flex flex-wrap gap-4">
            <div className="flex items-center text-sm text-gray-600">
              <Calendar size={16} className="mr-1 text-gray-500" />
              <span className="mr-1">Pradžia:</span>
              <span className="font-medium">{formatDate(task.startDate)}</span>
            </div>
            
            <div className="flex items-center text-sm text-gray-600">
              <Clock size={16} className="mr-1 text-gray-500" />
              <span className="mr-1">Terminas:</span>
              <span className={`font-medium ${isOverdue ? 'text-red-600' : ''}`}>{formatDate(task.dueDate)}</span>
            </div>
            
            <div className="flex items-center">
              <span className={`inline-flex items-center px-2 py-1 rounded-full text-xs font-medium ${priorityInfo.color}`}>
                {priorityInfo.icon}
                {priorityInfo.label}
              </span>
            </div>
            
            <div className="flex items-center">
              <span className={`inline-flex items-center px-2 py-1 rounded-full text-xs font-medium ${statusInfo.color}`}>
                {statusInfo.icon}
                {statusInfo.label}
              </span>
            </div>
          </div>
        </div>
        
        <div>
          <div className="bg-gray-50 rounded-lg p-4">
            <h3 className="text-sm font-medium text-gray-700 mb-3">Užduoties informacija</h3>
            
            <div className="space-y-3">
              <div>
                <p className="text-xs text-gray-500">Avilys</p>
                <p className="font-medium">
                  {task.hiveId 
                    ? (hive ? hive.name : 'Nežinomas avilys')
                    : 'Nepriskirta'
                  }
                </p>
              </div>
              
              <div>
                <p className="text-xs text-gray-500">Grupė</p>
                <p className="font-medium">
                  {task.groupId && appState.userGroups
                    ? (appState.userGroups.find(g => g.id === task.groupId)?.name || 'Nežinoma grupė')
                    : 'Nepriskirta'
                  }
                </p>
              </div>
              
              <div>
                <p className="text-xs text-gray-500">Šablonas</p>
                <p className="font-medium">
                  {task.templateId && appState.taskTemplates
                    ? (appState.taskTemplates.find(t => t.id === task.templateId)?.title || 'Nežinomas šablonas')
                    : 'Nepriskirta'
                  }
                </p>
              </div>
              
              <div>
                <p className="text-xs text-gray-500">ID</p>
                <div className="flex items-center">
                  <Hexagon size={14} className="text-amber-600 mr-1" />
                  <span>{task.id}</span>
                </div>
              </div>
            </div>
          </div>
          
          {taskAssignedUser && (
            <div className="mt-4">
              <h3 className="text-sm font-medium text-gray-700 mb-2 flex items-center">
                <Users size={16} className="mr-1" />
                Priskirti vartotojai
              </h3>
              <div className="bg-gray-50 rounded-lg p-3">
                <div className="space-y-2">
                  <div className="flex items-center">
                    <div className="w-8 h-8 bg-amber-100 rounded-full flex items-center justify-center mr-2">
                      <span className="text-amber-800 font-medium text-xs">
                        {taskAssignedUser.name.split(' ').map(n => n[0]).join('')}
                      </span>
                    </div>
                    <div>
                      <p className="text-sm font-medium">{taskAssignedUser.name}</p>
                      <p className="text-xs text-gray-500">{taskAssignedUser.email}</p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          )}
        </div>
      </div>
      
      {/* Progress section */}
      <div className="bg-amber-50 rounded-lg p-4 mb-6">
        <div className="flex justify-between items-center mb-2">
          <h3 className="font-medium">Užduoties progresas</h3>
          <span className="text-amber-800 font-medium">{taskProgress}%</span>
        </div>
        <div className="w-full bg-gray-200 rounded-full h-2.5">
          <div 
            className="bg-amber-600 h-2.5 rounded-full" 
            style={{ width: `${taskProgress}%` }}
          ></div>
        </div>
        <div className="mt-4 flex flex-wrap gap-3">
          {task.status !== 'completed' && (
            <button
              onClick={() => handleStatusChange('completed')}
              className="bg-green-600 hover:bg-green-700 text-white px-3 py-1.5 rounded-md text-sm flex items-center"
            >
              <CheckCircle size={16} className="mr-1.5" />
              Pažymėti kaip užbaigtą
            </button>
          )}
          {task.status === 'pending' && (
            <button
              onClick={() => handleStatusChange('in-progress')}
              className="bg-blue-600 hover:bg-blue-700 text-white px-3 py-1.5 rounded-md text-sm flex items-center"
            >
              <Clock size={16} className="mr-1.5" />
              Pradėti vykdyti
            </button>
          )}
          {task.status === 'completed' && (
            <button
              onClick={() => handleStatusChange('in-progress')}
              className="bg-amber-600 hover:bg-amber-700 text-white px-3 py-1.5 rounded-md text-sm flex items-center"
            >
              <ArrowLeft size={16} className="mr-1.5" />
              Grąžinti į vykdymą
            </button>
          )}
        </div>
      </div>
      
      {/* Steps section */}
      <div className="mb-6">
        <h2 className="text-lg font-medium mb-4">Užduoties žingsniai ({taskSteps.length})</h2>
        
        {taskSteps.length > 0 ? (
          <div className="space-y-3">
            {taskSteps.map(step => {
              const isExpanded = expandedSteps.includes(step.id);
              return (
                <div key={step.id} className="border border-gray-200 rounded-lg overflow-hidden">
                  <div 
                    className={`flex items-center p-3 cursor-pointer ${
                      step.status === 'completed' ? 'bg-green-50' : 'bg-white'
                    }`}
                    onClick={() => toggleStepExpansion(step.id)}
                  >
                    <div className="flex items-center flex-1">
                      <div className="mr-3">
                        <input 
                          type="checkbox" 
                          checked={step.status === 'completed'}
                          onChange={(e) => handleStepStatusChange(step.id, e.target.checked)}
                          onClick={(e) => e.stopPropagation()}
                          className="w-5 h-5 rounded border-gray-300 text-amber-600 focus:ring-amber-500"
                        />
                      </div>
                      {getStepTypeIcon(step.type)}
                      <div>
                        <h3 className={`font-medium ${step.status === 'completed' ? 'line-through text-gray-500' : 'text-gray-900'}`}>
                          {step.title}
                        </h3>
                        {!isExpanded && step.description && (
                          <p className="text-sm text-gray-500 mt-0.5 line-clamp-1">{step.description}</p>
                        )}
                      </div>
                    </div>
                    <div>
                      {isExpanded ? (
                        <ChevronUp size={20} className="text-gray-400" />
                      ) : (
                        <ChevronDown size={20} className="text-gray-400" />
                      )}
                    </div>
                  </div>
                  
                  {isExpanded && (
                    <div className="p-4 bg-gray-50 border-t border-gray-200">
                      {step.description && (
                        <p className="text-gray-700 mb-3">{step.description}</p>
                      )}
                      
                      {step.media && (
                        <div className="mt-3">
                          {step.type === 'image' && (
                            <img 
                              src={step.media} 
                              alt={step.title} 
                              className="max-w-full h-auto rounded-md max-h-80 object-contain bg-white p-2 border border-gray-200"
                            />
                          )}
                          {step.type === 'video' && (
                            <video 
                              src={step.media} 
                              controls
                              className="max-w-full rounded-md"
                            ></video>
                          )}
                          {step.type === 'document' && (
                            <a 
                              href={step.media} 
                              target="_blank" 
                              rel="noopener noreferrer"
                              className="text-blue-600 hover:text-blue-800 underline flex items-center"
                            >
                              <span>Atidaryti dokumentą</span>
                            </a>
                          )}
                        </div>
                      )}
                    </div>
                  )}
                </div>
              );
            })}
          </div>
        ) : (
          <div className="bg-gray-50 rounded-lg p-6 text-center">
            <BarChart size={36} className="mx-auto text-gray-400 mb-2" />
            <p className="text-gray-500">Šiai užduočiai nėra priskirtų žingsnių</p>
          </div>
        )}
      </div>
      
      {/* Comments section */}
      <div>
        <h2 className="text-lg font-medium mb-4">Komentarai ({taskComments.length})</h2>
        
        <form onSubmit={handleCommentSubmit} className="mb-6">
          <div className="flex">
            <textarea
              value={comment}
              onChange={(e) => setComment(e.target.value)}
              placeholder="Pridėti naują komentarą..."
              className="flex-1 border border-gray-300 rounded-l-md p-3 focus:outline-none focus:ring-2 focus:ring-amber-500 focus:border-transparent"
              rows={2}
            ></textarea>
            <button
              type="submit"
              className="bg-amber-600 text-white px-4 rounded-r-md hover:bg-amber-700 flex items-center"
            >
              Pridėti
            </button>
          </div>
        </form>
        
        {taskComments.length > 0 ? (
          <div className="space-y-4">
            {taskComments.map(comment => {
              const commentUser = appState.users ? appState.users.find(u => u.id === comment.userId) : null;
              return (
                <div key={comment.id} className="bg-gray-50 rounded-lg p-4">
                  <div className="flex items-start">
                    <div className="w-8 h-8 bg-amber-100 rounded-full flex items-center justify-center mr-3 flex-shrink-0">
                      <span className="text-amber-800 font-medium text-xs">
                        {commentUser ? commentUser.name.split(' ').map(n => n[0]).join('') : 'U'}
                      </span>
                    </div>
                    <div className="flex-1">
                      <div className="flex justify-between items-center mb-1">
                        <h4 className="font-medium">
                          {commentUser ? commentUser.name : 'Nežinomas vartotojas'}
                        </h4>
                        <span className="text-sm text-gray-500">{formatDate(comment.date)}</span>
                      </div>
                      <p className="text-gray-700">{comment.content || comment.text}</p>
                    </div>
                  </div>
                </div>
              );
            })}
          </div>
        ) : (
          <div className="bg-gray-50 rounded-lg p-6 text-center">
            <MessageSquare size={36} className="mx-auto text-gray-400 mb-2" />
            <p className="text-gray-500">Nėra komentarų</p>
            <p className="text-gray-400 text-sm mt-1">Būk pirmas(-a), kuris(-i) pakomentuos šią užduotį!</p>
          </div>
        )}
      </div>
    </div>
  );
};

export default TaskDetails; 