import React, { useState } from 'react';
import { Plus, Edit, Trash, Search, X, Save, Users, UserMinus, UserPlus } from 'lucide-react';
import { useApp } from '../../context/AppContext';

const AdminGroups = () => {
  const { 
    appState, 
    updateAppState, 
    users = [], 
    userGroups = [],
    forms,
    updateForm,
    addUserGroup,
    updateUserGroup,
    deleteUserGroup,
    updateUser
  } = useApp();
  
  const [showForm, setShowForm] = useState(false);
  const [editingGroupId, setEditingGroupId] = useState(null);
  const [searchTerm, setSearchTerm] = useState('');
  const [confirmDelete, setConfirmDelete] = useState(null);
  
  // Pridėti vartotojų valdymo funkcionalumą grupėje
  const [selectedGroup, setSelectedGroup] = useState(null);
  const [showUserManagement, setShowUserManagement] = useState(false);
  const [availableUsers, setAvailableUsers] = useState([]);
  const [groupUsers, setGroupUsers] = useState([]);
  const [searchUserTerm, setSearchUserTerm] = useState('');
  
  // Form handlers
  const resetForm = () => {
    updateForm('userGroup', 'name', '');
    updateForm('userGroup', 'description', '');
  };
  
  const handleSubmit = (e) => {
    e.preventDefault();
    
    const formData = forms.userGroup;
    
    // Validacija
    if (!formData.name) {
      alert('Prašome įvesti grupės pavadinimą');
      return;
    }
    
    if (editingGroupId) {
      // Redagavimas
      updateUserGroup(editingGroupId);
      
      setEditingGroupId(null);
    } else {
      // Naujos grupės pridėjimas
      addUserGroup();
    }
    
    setShowForm(false);
    resetForm();
  };
  
  const editGroup = (group) => {
    setEditingGroupId(group.id);
    updateForm('userGroup', 'name', group.name);
    updateForm('userGroup', 'description', group.description || '');
    setShowForm(true);
  };
  
  const deleteGroup = (groupId) => {
    if (confirmDelete === groupId) {
      // Grupės šalinimas
      deleteUserGroup(groupId);
      
      setConfirmDelete(null);
    } else {
      setConfirmDelete(groupId);
    }
  };
  
  // Filtruojame pagal paiešką
  const filteredGroups = userGroups.filter(group => 
    group.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
    (group.description && group.description.toLowerCase().includes(searchTerm.toLowerCase()))
  );
  
  // Gauname vartotojų skaičių grupėje
  const getUserCount = (groupId) => {
    return users.filter(user => user.groupId === groupId).length;
  };
  
  // Atidaryti vartotojų valdymo langą
  const openUserManagement = (group) => {
    setSelectedGroup(group);
    
    // Gauti grupės vartotojus
    const usersInGroup = users.filter(user => user.groupId === group.id);
    setGroupUsers(usersInGroup);
    
    // Gauti vartotojus, kurie nepriklauso šiai grupei
    const usersNotInGroup = users.filter(user => user.groupId !== group.id);
    setAvailableUsers(usersNotInGroup);
    
    setShowUserManagement(true);
  };
  
  // Pridėti vartotoją į grupę
  const addUserToGroup = (userId) => {
    // Rasti vartotoją
    const userToAdd = availableUsers.find(user => user.id === userId);
    if (!userToAdd) return;
    
    // Atnaujinti vartotojo grupę
    const updatedUser = { ...userToAdd, groupId: selectedGroup.id };
    updateUser(updatedUser);
    
    // Atnaujinti sąrašus
    setGroupUsers([...groupUsers, updatedUser]);
    setAvailableUsers(availableUsers.filter(user => user.id !== userId));
    
    // Rodyti sėkmės pranešimą
    updateAppState({
      successMessages: {
        ...appState.successMessages,
        userAddedToGroup: true
      }
    });
    
    // Paslėpti sėkmės pranešimą po 3 sekundžių
    setTimeout(() => {
      updateAppState({
        successMessages: {
          ...appState.successMessages,
          userAddedToGroup: false
        }
      });
    }, 3000);
  };
  
  // Pašalinti vartotoją iš grupės
  const removeUserFromGroup = (userId) => {
    // Rasti vartotoją
    const userToRemove = groupUsers.find(user => user.id === userId);
    if (!userToRemove) return;
    
    // Atnaujinti vartotojo grupę (nustatyti į null)
    const updatedUser = { ...userToRemove, groupId: null };
    updateUser(updatedUser);
    
    // Atnaujinti sąrašus
    setAvailableUsers([...availableUsers, updatedUser]);
    setGroupUsers(groupUsers.filter(user => user.id !== userId));
    
    // Rodyti sėkmės pranešimą
    updateAppState({
      successMessages: {
        ...appState.successMessages,
        userRemovedFromGroup: true
      }
    });
    
    // Paslėpti sėkmės pranešimą po 3 sekundžių
    setTimeout(() => {
      updateAppState({
        successMessages: {
          ...appState.successMessages,
          userRemovedFromGroup: false
        }
      });
    }, 3000);
  };
  
  // Filtruoti vartotojus pagal paiešką
  const filteredAvailableUsers = searchUserTerm 
    ? availableUsers.filter(user => 
        user.name.toLowerCase().includes(searchUserTerm.toLowerCase()) ||
        user.email.toLowerCase().includes(searchUserTerm.toLowerCase())
      )
    : availableUsers;
  
  return (
    <div className="p-6">
      <div className="flex justify-between items-center mb-6">
        <h2 className="text-2xl font-bold">Grupių administravimas</h2>
        <button 
          onClick={() => {
            resetForm();
            setEditingGroupId(null);
            setShowForm(true);
          }}
          className="bg-amber-600 text-white px-4 py-2 rounded-md hover:bg-amber-700 flex items-center"
        >
          <Plus size={16} className="mr-2" />
          Pridėti grupę
        </button>
      </div>
      
      {/* Paieškos laukas */}
      <div className="mb-6 relative">
        <input 
          type="text"
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
          placeholder="Ieškoti grupių..."
          className="w-full p-3 pl-10 pr-10 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-amber-500"
        />
        <Search size={20} className="absolute left-3 top-3.5 text-gray-400" />
        {searchTerm && (
          <button 
            onClick={() => setSearchTerm('')}
            className="absolute right-3 top-3.5 text-gray-400 hover:text-gray-600"
          >
            <X size={20} />
          </button>
        )}
      </div>
      
      {/* Grupių forma */}
      {showForm && (
        <div className="bg-white p-6 rounded-lg shadow-md mb-6">
          <h3 className="text-lg font-semibold mb-4">{editingGroupId ? 'Redaguoti grupę' : 'Pridėti naują grupę'}</h3>
          
          <form onSubmit={handleSubmit} className="space-y-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">Pavadinimas *</label>
              <input 
                type="text"
                value={forms.userGroup?.name || ''}
                onChange={(e) => updateForm('userGroup', 'name', e.target.value)}
                className="w-full p-2 border border-gray-300 rounded-md"
                required
              />
            </div>
            
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">Aprašymas</label>
              <textarea 
                value={forms.userGroup?.description || ''}
                onChange={(e) => updateForm('userGroup', 'description', e.target.value)}
                className="w-full p-2 border border-gray-300 rounded-md"
                rows={3}
              ></textarea>
            </div>
            
            <div className="flex justify-end space-x-3 mt-6">
              <button 
                type="button"
                onClick={() => {
                  setShowForm(false);
                  resetForm();
                }}
                className="px-4 py-2 border border-gray-300 rounded-md text-gray-700 hover:bg-gray-50"
              >
                Atšaukti
              </button>
              <button 
                type="submit"
                className="px-4 py-2 bg-amber-600 text-white rounded-md hover:bg-amber-700 flex items-center"
              >
                <Save size={16} className="mr-2" />
                {editingGroupId ? 'Atnaujinti' : 'Pridėti'}
              </button>
            </div>
          </form>
        </div>
      )}
      
      {/* Grupių sąrašas */}
      <div className="bg-white rounded-lg shadow-md overflow-hidden">
        <table className="min-w-full divide-y divide-gray-200">
          <thead className="bg-gray-50">
            <tr>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Grupė</th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Aprašymas</th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Narių skaičius</th>
              <th className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider">Veiksmai</th>
            </tr>
          </thead>
          <tbody className="bg-white divide-y divide-gray-200">
            {filteredGroups.length > 0 ? (
              filteredGroups.map(group => {
                const userCount = getUserCount(group.id);
                return (
                  <tr key={group.id} className="hover:bg-gray-50">
                    <td className="px-6 py-4 whitespace-nowrap">
                      <div className="flex items-center">
                        <div className="flex-shrink-0 h-10 w-10 bg-amber-100 rounded-full flex items-center justify-center">
                          <Users size={18} className="text-amber-600" />
                        </div>
                        <div className="ml-4">
                          <div className="text-sm font-medium text-gray-900">{group.name}</div>
                          <div className="text-sm text-gray-500">ID: {group.id}</div>
                        </div>
                      </div>
                    </td>
                    <td className="px-6 py-4">
                      <div className="text-sm text-gray-900">{group.description || '-'}</div>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <div className="text-sm text-gray-900">{userCount} vartotojai</div>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-right text-sm font-medium">
                      <button 
                        onClick={() => editGroup(group)}
                        className="text-amber-600 hover:text-amber-900 mx-2"
                      >
                        <Edit size={18} />
                      </button>
                      <button 
                        onClick={() => deleteGroup(group.id)}
                        className={`text-red-600 hover:text-red-900 mx-2 ${
                          confirmDelete === group.id ? 'animate-pulse' : ''
                        }`}
                      >
                        <Trash size={18} />
                      </button>
                      <button
                        onClick={() => openUserManagement(group)}
                        className="text-blue-600 hover:text-blue-900 mx-2"
                      >
                        <Users size={18} />
                      </button>
                    </td>
                  </tr>
                );
              })
            ) : (
              <tr>
                <td colSpan={4} className="px-6 py-4 text-center text-gray-500">
                  {searchTerm ? 'Nerasta grupių pagal paiešką' : 'Nėra sukurtų grupių'}
                </td>
              </tr>
            )}
          </tbody>
        </table>
      </div>
      
      {/* Vartotojų valdymo modalas */}
      {showUserManagement && selectedGroup && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
          <div className="bg-white p-6 rounded-lg max-w-4xl w-full h-5/6 flex flex-col">
            <div className="flex justify-between items-center mb-4">
              <h3 className="text-lg font-bold">Vartotojų valdymas: {selectedGroup.name}</h3>
              <button 
                onClick={() => setShowUserManagement(false)} 
                className="text-gray-500 hover:text-gray-700"
                type="button"
              >
                <X size={20} />
              </button>
            </div>
            
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6 flex-1 overflow-hidden">
              {/* Grupės vartotojai */}
              <div className="flex flex-col h-full">
                <h4 className="font-medium mb-2">Grupės vartotojai ({groupUsers.length})</h4>
                
                <div className="flex-1 overflow-auto border rounded-lg">
                  {groupUsers.length > 0 ? (
                    <ul className="divide-y divide-gray-200">
                      {groupUsers.map(user => (
                        <li key={user.id} className="p-3 hover:bg-gray-50 flex justify-between items-center">
                          <div>
                            <div className="font-medium">{user.name}</div>
                            <div className="text-sm text-gray-500">{user.email}</div>
                          </div>
                          <button
                            onClick={() => removeUserFromGroup(user.id)}
                            className="text-red-600 hover:text-red-800"
                            type="button"
                          >
                            <UserMinus size={18} />
                          </button>
                        </li>
                      ))}
                    </ul>
                  ) : (
                    <div className="text-center py-8 text-gray-500">
                      Šioje grupėje nėra vartotojų
                    </div>
                  )}
                </div>
              </div>
              
              {/* Galimi vartotojai */}
              <div className="flex flex-col h-full">
                <h4 className="font-medium mb-2">Galimi vartotojai ({availableUsers.length})</h4>
                
                <div className="mb-2 relative">
                  <input 
                    type="text"
                    value={searchUserTerm}
                    onChange={(e) => setSearchUserTerm(e.target.value)}
                    placeholder="Ieškoti vartotojų..."
                    className="w-full pl-10 pr-4 py-2 border rounded-lg"
                  />
                  <Search className="absolute left-3 top-2.5 text-gray-400" size={18} />
                </div>
                
                <div className="flex-1 overflow-auto border rounded-lg">
                  {filteredAvailableUsers.length > 0 ? (
                    <ul className="divide-y divide-gray-200">
                      {filteredAvailableUsers.map(user => (
                        <li key={user.id} className="p-3 hover:bg-gray-50 flex justify-between items-center">
                          <div>
                            <div className="font-medium">{user.name}</div>
                            <div className="text-sm text-gray-500">{user.email}</div>
                          </div>
                          <button
                            onClick={() => addUserToGroup(user.id)}
                            className="text-green-600 hover:text-green-800"
                            type="button"
                          >
                            <UserPlus size={18} />
                          </button>
                        </li>
                      ))}
                    </ul>
                  ) : (
                    <div className="text-center py-8 text-gray-500">
                      {searchUserTerm ? 'Nerasta vartotojų pagal paiešką' : 'Nėra galimų vartotojų'}
                    </div>
                  )}
                </div>
              </div>
            </div>
            
            <div className="flex justify-end mt-4">
              <button 
                onClick={() => setShowUserManagement(false)}
                className="px-4 py-2 bg-amber-600 text-white rounded-md hover:bg-amber-700"
                type="button"
              >
                Uždaryti
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default AdminGroups; 