import React, { useState, useEffect } from 'react';
import { Search, Plus, Edit, Trash, X, Save, Calendar, Clock, Check, AlertCircle, Users, User, Filter } from 'lucide-react';
import { useApp } from '../../context/AppContext';

const AdminTasks = () => {
  const { 
    appState, 
    updateAppState, 
    forms, 
    updateForm,
    tasks,
    addTask,
    updateTask,
    deleteTask,
    users,
    templates
  } = useApp();
  
  // Naudoti userGroups iš appState vietoj neegzistuojančio 'groups'
  const userGroups = appState.userGroups || [];
  const hives = appState.hives || [];
  
  const [showForm, setShowForm] = useState(false);
  const [editingTaskId, setEditingTaskId] = useState(null);
  const [searchTerm, setSearchTerm] = useState('');
  const [confirmDelete, setConfirmDelete] = useState(null);
  const [showFilters, setShowFilters] = useState(false);
  const [filters, setFilters] = useState({
    status: '',
    priority: '',
    templateId: '',
    groupId: '',
    startDate: ''
  });
  
  // Form handlers
  const resetForm = () => {
    updateForm('task', 'title', '');
    updateForm('task', 'description', '');
    updateForm('task', 'hiveId', '');
    updateForm('task', 'groupId', '');
    updateForm('task', 'templateId', '');
    updateForm('task', 'priority', 'medium');
    updateForm('task', 'startDate', new Date().toISOString().split('T')[0]);
    updateForm('task', 'dueDate', '');
    updateForm('task', 'status', 'pending');
  };
  
  const handleSubmit = (e) => {
    e.preventDefault();
    
    const formData = forms.task;
    
    // Validacija
    if (!formData.title) {
      alert('Prašome įvesti užduoties pavadinimą');
      return;
    }
    
    if (!formData.groupId) {
      alert('Prašome pasirinkti grupę');
      return;
    }
    
    if (!formData.hiveId) {
      alert('Prašome pasirinkti avilį');
      return;
    }
    
    if (editingTaskId) {
      // Redagavimas
      updateTask(editingTaskId);
      setEditingTaskId(null);
    } else {
      // Naujos užduoties pridėjimas
      addTask();
    }
    
    setShowForm(false);
    resetForm();
  };
  
  const editTask = (task) => {
    setEditingTaskId(task.id);
    updateForm('task', 'title', task.title);
    updateForm('task', 'description', task.description || '');
    updateForm('task', 'hiveId', task.hiveId || '');
    updateForm('task', 'groupId', task.groupId || '');
    updateForm('task', 'templateId', task.templateId || '');
    updateForm('task', 'priority', task.priority || 'medium');
    updateForm('task', 'startDate', task.startDate || new Date().toISOString().split('T')[0]);
    updateForm('task', 'dueDate', task.dueDate || '');
    updateForm('task', 'status', task.status || 'pending');
    setShowForm(true);
  };
  
  const confirmDeleteTask = (taskId) => {
    if (confirmDelete === taskId) {
      // Užduoties šalinimas
      deleteTask(taskId);
      setConfirmDelete(null);
    } else {
      setConfirmDelete(taskId);
    }
  };
  
  // Update filters
  const updateFilters = (key, value) => {
    setFilters({ ...filters, [key]: value });
  };
  
  // Reset filters
  const resetFilters = () => {
    setFilters({
      status: '',
      priority: '',
      templateId: '',
      groupId: '',
      startDate: ''
    });
  };
  
  // Filtruojame užduotis
  const filteredTasks = tasks.filter(task => {
    // Teksto paieška
    const matchesSearch = 
      task.title.toLowerCase().includes(searchTerm.toLowerCase()) || 
      (task.description && task.description.toLowerCase().includes(searchTerm.toLowerCase()));
    
    // Filtrai
    const matchesStatus = !filters.status || task.status === filters.status;
    const matchesPriority = !filters.priority || task.priority === filters.priority;
    const matchesTemplate = !filters.templateId || task.templateId === filters.templateId;
    const matchesGroup = !filters.groupId || task.groupId === filters.groupId;
    
    // Datos filtras
    let matchesDate = true;
    if (filters.startDate) {
      const filterDate = new Date(filters.startDate);
      const taskDate = new Date(task.startDate);
      matchesDate = taskDate >= filterDate;
    }
    
    return matchesSearch && matchesStatus && matchesPriority && matchesTemplate && matchesGroup && matchesDate;
  });
  
  // Gauname grupės pavadinimą pagal ID
  const getGroupName = (groupId) => {
    if (!userGroups) return 'Nepriskirta';
    const group = userGroups.find(g => g.id === groupId);
    return group ? group.name : 'Nepriskirta';
  };
  
  // Gauname avilio pavadinimą pagal ID
  const getHiveName = (hiveId) => {
    if (!hives) return 'Nepriskirtas';
    const hive = hives.find(h => h.id === hiveId);
    return hive ? hive.name : 'Nepriskirtas';
  };
  
  // Gauname šablono pavadinimą pagal ID
  const getTemplateName = (templateId) => {
    if (!templates) return 'Be šablono';
    const template = templates.find(t => t.id === templateId);
    return template ? template.title : 'Be šablono';
  };
  
  // Gauname grupės vartotojų skaičių
  const getGroupUserCount = (groupId) => {
    if (!users) return 0;
    return users.filter(user => user.groupId === groupId).length;
  };
  
  // Formatas datai
  const formatDate = (dateString) => {
    if (!dateString) return '-';
    return new Date(dateString).toLocaleDateString('lt-LT');
  };
  
  // Prioriteto spalvos
  const priorityColors = {
    high: 'text-red-600 bg-red-100',
    medium: 'text-amber-600 bg-amber-100',
    low: 'text-green-600 bg-green-100'
  };
  
  // Statuso spalvos
  const statusColors = {
    pending: 'text-gray-600 bg-gray-100',
    'in-progress': 'text-blue-600 bg-blue-100',
    review: 'text-purple-600 bg-purple-100',
    completed: 'text-green-600 bg-green-100',
    cancelled: 'text-red-600 bg-red-100'
  };
  
  // Prioriteto tekstas
  const priorityText = {
    high: 'Aukštas',
    medium: 'Vidutinis',
    low: 'Žemas'
  };
  
  // Statuso tekstas
  const statusText = {
    pending: 'Laukiama',
    'in-progress': 'Vykdoma',
    review: 'Peržiūrima',
    completed: 'Užbaigta',
    cancelled: 'Atšaukta'
  };
  
  return (
    <div className="p-6">
      <div className="flex justify-between items-center mb-6">
        <h2 className="text-2xl font-bold">Užduočių administravimas</h2>
        <button 
          onClick={() => {
            resetForm();
            setEditingTaskId(null);
            setShowForm(true);
          }}
          className="bg-amber-600 text-white px-4 py-2 rounded-md hover:bg-amber-700 flex items-center"
        >
          <Plus size={16} className="mr-2" />
          Pridėti užduotį
        </button>
      </div>
      
      {/* Paieška ir filtrai */}
      <div className="mb-6">
        <div className="flex gap-2 mb-2">
          <div className="relative flex-1">
            <input 
              type="text"
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              placeholder="Ieškoti užduočių..."
              className="w-full p-3 pl-10 pr-10 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-amber-500"
            />
            <Search size={20} className="absolute left-3 top-3.5 text-gray-400" />
            {searchTerm && (
              <button 
                onClick={() => setSearchTerm('')}
                className="absolute right-3 top-3.5 text-gray-400 hover:text-gray-600"
              >
                <X size={20} />
              </button>
            )}
          </div>
          
          <button 
            onClick={() => setShowFilters(!showFilters)}
            className={`p-3 rounded-lg flex items-center ${showFilters ? 'bg-amber-600 text-white' : 'bg-gray-200 text-gray-700'}`}
          >
            <Filter size={20} className="mr-2" />
            Filtrai
          </button>
        </div>
        
        {showFilters && (
          <div className="bg-white p-4 rounded-lg shadow-md mb-4">
            <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Būsena</label>
                <select
                  value={filters.status}
                  onChange={(e) => updateFilters('status', e.target.value)}
                  className="w-full p-2 border border-gray-300 rounded-md"
                >
                  <option value="">Visos būsenos</option>
                  <option value="pending">Laukiama</option>
                  <option value="in-progress">Vykdoma</option>
                  <option value="review">Peržiūrima</option>
                  <option value="completed">Užbaigta</option>
                  <option value="cancelled">Atšaukta</option>
                </select>
              </div>
              
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Prioritetas</label>
                <select
                  value={filters.priority}
                  onChange={(e) => updateFilters('priority', e.target.value)}
                  className="w-full p-2 border border-gray-300 rounded-md"
                >
                  <option value="">Visi prioritetai</option>
                  <option value="high">Aukštas</option>
                  <option value="medium">Vidutinis</option>
                  <option value="low">Žemas</option>
                </select>
              </div>
              
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Grupė</label>
                <select
                  value={filters.groupId}
                  onChange={(e) => updateFilters('groupId', e.target.value)}
                  className="w-full p-2 border border-gray-300 rounded-md"
                >
                  <option value="">Visos grupės</option>
                  {userGroups.map(group => (
                    <option key={group.id} value={group.id}>{group.name}</option>
                  ))}
                </select>
              </div>
              
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Šablonas</label>
                <select
                  value={filters.templateId}
                  onChange={(e) => updateFilters('templateId', e.target.value)}
                  className="w-full p-2 border border-gray-300 rounded-md"
                >
                  <option value="">Visi šablonai</option>
                  <option value="none">Be šablono</option>
                  {templates.map(template => (
                    <option key={template.id} value={template.id}>{template.title}</option>
                  ))}
                </select>
              </div>
            </div>
            
            <div className="grid grid-cols-1 md:grid-cols-4 gap-4 mt-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Pradžios data nuo</label>
                <input 
                  type="date"
                  value={filters.startDate}
                  onChange={(e) => updateFilters('startDate', e.target.value)}
                  className="w-full p-2 border border-gray-300 rounded-md"
                />
              </div>
              
              <div className="flex items-end">
                <button 
                  onClick={resetFilters}
                  className="px-4 py-2 border border-gray-300 rounded-md text-gray-700 hover:bg-gray-50"
                >
                  Išvalyti filtrus
                </button>
              </div>
            </div>
          </div>
        )}
      </div>
      
      {/* Užduoties kūrimo/redagavimo forma */}
      {showForm && (
        <div className="bg-white p-6 rounded-lg shadow-md mb-6">
          <h3 className="text-lg font-semibold mb-4">{editingTaskId ? 'Redaguoti užduotį' : 'Pridėti naują užduotį'}</h3>
          
          <form onSubmit={handleSubmit} className="space-y-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">Pavadinimas *</label>
              <input 
                type="text"
                value={forms.task?.title || ''}
                onChange={(e) => updateForm('task', 'title', e.target.value)}
                className="w-full p-2 border border-gray-300 rounded-md"
                placeholder="Užduoties pavadinimas"
                required
              />
            </div>
            
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">Aprašymas</label>
              <textarea 
                value={forms.task?.description || ''}
                onChange={(e) => updateForm('task', 'description', e.target.value)}
                className="w-full p-2 border border-gray-300 rounded-md"
                rows={2}
                placeholder="Užduoties aprašymas"
              ></textarea>
            </div>
            
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Grupė *</label>
                <select
                  value={forms.task?.groupId || ''}
                  onChange={(e) => updateForm('task', 'groupId', e.target.value)}
                  className="w-full p-2 border border-gray-300 rounded-md"
                  required
                >
                  <option value="">Pasirinkti grupę</option>
                  {userGroups.map(group => (
                    <option key={group.id} value={group.id}>
                      {group.name} ({getGroupUserCount(group.id)} vartotojai)
                    </option>
                  ))}
                </select>
              </div>
              
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Avilys *</label>
                <select
                  value={forms.task?.hiveId || ''}
                  onChange={(e) => updateForm('task', 'hiveId', e.target.value)}
                  className="w-full p-2 border border-gray-300 rounded-md"
                  required
                >
                  <option value="">Pasirinkti avilį</option>
                  {hives.map(hive => (
                    <option key={hive.id} value={hive.id}>
                      {hive.name} {hive.location ? `(${hive.location})` : ''}
                    </option>
                  ))}
                </select>
              </div>
            </div>
            
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Šablonas</label>
                <select
                  value={forms.task?.templateId || ''}
                  onChange={(e) => updateForm('task', 'templateId', e.target.value)}
                  className="w-full p-2 border border-gray-300 rounded-md"
                >
                  <option value="">Be šablono</option>
                  {templates.map(template => (
                    <option key={template.id} value={template.id}>
                      {template.title}
                    </option>
                  ))}
                </select>
              </div>
              
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Prioritetas</label>
                <select
                  value={forms.task?.priority || 'medium'}
                  onChange={(e) => updateForm('task', 'priority', e.target.value)}
                  className="w-full p-2 border border-gray-300 rounded-md"
                >
                  <option value="high">Aukštas</option>
                  <option value="medium">Vidutinis</option>
                  <option value="low">Žemas</option>
                </select>
              </div>
            </div>
            
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Pradžios data</label>
                <input 
                  type="date"
                  value={forms.task?.startDate || ''}
                  onChange={(e) => updateForm('task', 'startDate', e.target.value)}
                  className="w-full p-2 border border-gray-300 rounded-md"
                />
              </div>
              
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Pabaigos data</label>
                <input 
                  type="date"
                  value={forms.task?.dueDate || ''}
                  onChange={(e) => updateForm('task', 'dueDate', e.target.value)}
                  className="w-full p-2 border border-gray-300 rounded-md"
                />
              </div>
            </div>
            
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">Būsena</label>
              <select
                value={forms.task?.status || 'pending'}
                onChange={(e) => updateForm('task', 'status', e.target.value)}
                className="w-full p-2 border border-gray-300 rounded-md"
              >
                <option value="pending">Laukiama</option>
                <option value="in-progress">Vykdoma</option>
                <option value="review">Peržiūrima</option>
                <option value="completed">Užbaigta</option>
                <option value="cancelled">Atšaukta</option>
              </select>
            </div>
            
            <div className="flex justify-end space-x-3 mt-6">
              <button 
                type="button"
                onClick={() => {
                  setShowForm(false);
                  resetForm();
                }}
                className="px-4 py-2 border border-gray-300 rounded-md text-gray-700 hover:bg-gray-50"
              >
                Atšaukti
              </button>
              <button 
                type="submit"
                className="px-4 py-2 bg-amber-600 text-white rounded-md hover:bg-amber-700 flex items-center"
              >
                <Save size={16} className="mr-2" />
                {editingTaskId ? 'Atnaujinti' : 'Pridėti'}
              </button>
            </div>
          </form>
        </div>
      )}
      
      {/* Užduočių sąrašas */}
      <div className="bg-white rounded-lg shadow-md overflow-hidden">
        <table className="min-w-full divide-y divide-gray-200">
          <thead className="bg-gray-50">
            <tr>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Užduotis</th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Grupė / Avilys</th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Datos</th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Prioritetas</th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Būsena</th>
              <th className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider">Veiksmai</th>
            </tr>
          </thead>
          <tbody className="bg-white divide-y divide-gray-200">
            {filteredTasks.length > 0 ?
              filteredTasks.map(task => (
                <tr key={task.id} className="hover:bg-gray-50">
                  <td className="px-6 py-4">
                    <div className="text-sm font-medium text-gray-900">{task.title}</div>
                    <div className="text-xs text-gray-500 truncate max-w-xs">{task.description || '-'}</div>
                    {task.templateId && (
                      <div className="text-xs text-gray-500 mt-1">
                        <span className="inline-flex items-center px-2 py-0.5 rounded text-xs font-medium bg-gray-100 text-gray-800">
                          {getTemplateName(task.templateId)}
                        </span>
                      </div>
                    )}
                  </td>
                  <td className="px-6 py-4">
                    <div className="flex items-center text-sm text-gray-900 mb-1">
                      <Users size={16} className="text-gray-500 mr-1" />
                      {getGroupName(task.groupId)}
                    </div>
                    <div className="flex items-center text-sm text-gray-900">
                      <AlertCircle size={16} className="text-gray-500 mr-1" />
                      {getHiveName(task.hiveId)}
                    </div>
                  </td>
                  <td className="px-6 py-4">
                    <div className="flex items-center text-sm text-gray-900 mb-1">
                      <Calendar size={16} className="text-gray-500 mr-1" />
                      {formatDate(task.startDate)}
                    </div>
                    {task.dueDate && (
                      <div className="flex items-center text-sm text-gray-900">
                        <Clock size={16} className="text-gray-500 mr-1" />
                        {formatDate(task.dueDate)}
                      </div>
                    )}
                  </td>
                  <td className="px-6 py-4">
                    <span className={`px-2 py-1 inline-flex text-xs leading-5 font-semibold rounded-full ${priorityColors[task.priority]}`}>
                      {priorityText[task.priority]}
                    </span>
                  </td>
                  <td className="px-6 py-4">
                    <span className={`px-2 py-1 inline-flex text-xs leading-5 font-semibold rounded-full ${statusColors[task.status]}`}>
                      {statusText[task.status]}
                    </span>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap text-right text-sm font-medium">
                    <button 
                      onClick={() => editTask(task)}
                      className="text-amber-600 hover:text-amber-900 mx-2"
                      title="Redaguoti užduotį"
                    >
                      <Edit size={18} />
                    </button>
                    <button 
                      onClick={() => confirmDeleteTask(task.id)}
                      className={`text-red-600 hover:text-red-900 mx-2 ${
                        confirmDelete === task.id ? 'animate-pulse' : ''
                      }`}
                      title={confirmDelete === task.id ? 'Patvirtinti šalinimą' : 'Šalinti užduotį'}
                    >
                      <Trash size={18} />
                    </button>
                  </td>
                </tr>
              )) : (
                <tr>
                  <td colSpan={6} className="px-6 py-4 text-center text-gray-500">
                    {searchTerm || Object.values(filters).some(v => v) 
                      ? 'Nerasta užduočių pagal paieškos ar filtravimo kriterijus' 
                      : 'Nėra sukurtų užduočių'}
                  </td>
                </tr>
              )
            }
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default AdminTasks; 